import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface IAiima_Transporter_Driver_Bthb_Booking {
ID:number,
BookingCode:number,
JourneyCode?:string,
Transporter_id?:number,
Driver_id?:number,
bt_hb_customer_id?:number,
Truck_id?:number,
Container_MTU_No?:string,
Container_Size?:string,
Location_From?:string,
Journey_Destination_Port?:string,
Booking_Journey_Type?:string,
Booking_Category?:string,
JourneyStartedDate?:Date,
ExpectedDeliveryDate?:Date,
LockBooking?:string,
TruckStayDuration?:number,
BookingStatus?:string,
CallCardAvatar?:string,
CallCardUploadedAt?:Date,
LoggedIP:string,
RecordCreatedAt:Date,
RecordUpdatedAt?:Date
}

interface IAiima_Transporter_Driver_Bthb_BookingData {
    list?: Array<IAiima_Transporter_Driver_Bthb_Booking>;
    pageNo: number;
    pageSize: number;
    searchKey?: string;
    totalCount?: number;
    message?: string;
}

const initialState: IAiima_Transporter_Driver_Bthb_BookingData = {
    pageNo: 1,
    pageSize: 20,
    searchKey: '',
    list: [],
    totalCount: 0,
    message: '',
};

const aiima_transporter_driver_bthb_bookingSlice = createSlice({
    name: "aiima_transporter_driver_bthb_booking",
    initialState,
    reducers: {
        setAiima_Transporter_Driver_Bthb_BookingList: (state, _action: PayloadAction<IAiima_Transporter_Driver_Bthb_BookingData>) => {
            state.list = _action.payload.list;
            state.pageNo = _action.payload.pageNo;
            state.pageSize = _action.payload.pageSize;
            state.totalCount = _action.payload.totalCount;
        },
        resetAiima_Transporter_Driver_Bthb_BookingToInit: (state) => {
            state = initialState;
        },
        setAiima_Transporter_Driver_Bthb_BookingMessage: (state, _action: PayloadAction<string>) => {
            state.message = _action.payload;
        },
    },
});

export const { setAiima_Transporter_Driver_Bthb_BookingList, resetAiima_Transporter_Driver_Bthb_BookingToInit, setAiima_Transporter_Driver_Bthb_BookingMessage } = aiima_transporter_driver_bthb_bookingSlice.actions;

export default aiima_transporter_driver_bthb_bookingSlice.reducer;

