import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface IAiimma_Transporters {
ID:number,
UserID:string,
FullName:string,
Gender?:string,
Phone?:string,
Whatsapp?:string,
Email?:string,
Website?:string,
Address?:string,
StateOfOrigin?:string,
Nationality?:string,
Transporter_Avatar_Url?:string,
Transporter_Type?:string,
TransporterAvatar?:string,
LoggedIP:string,
RecordCreatedAt:Date,
RecordUpdatedAt?:Date
}

interface IAiimma_TransportersData {
    list?: Array<IAiimma_Transporters>;
    pageNo: number;
    pageSize: number;
    searchKey?: string;
    totalCount?: number;
    message?: string;
}

const initialState: IAiimma_TransportersData = {
    pageNo: 1,
    pageSize: 20,
    searchKey: '',
    list: [],
    totalCount: 0,
    message: '',
};

const aiimma_transportersSlice = createSlice({
    name: "aiimma_transporters",
    initialState,
    reducers: {
        setAiimma_TransportersList: (state, _action: PayloadAction<IAiimma_TransportersData>) => {
            state.list = _action.payload.list;
            state.pageNo = _action.payload.pageNo;
            state.pageSize = _action.payload.pageSize;
            state.totalCount = _action.payload.totalCount;
        },
        resetAiimma_TransportersToInit: (state) => {
            state = initialState;
        },
        setAiimma_TransportersMessage: (state, _action: PayloadAction<string>) => {
            state.message = _action.payload;
        },
    },
});

export const { setAiimma_TransportersList, resetAiimma_TransportersToInit, setAiimma_TransportersMessage } = aiimma_transportersSlice.actions;

export default aiimma_transportersSlice.reducer;

