import React, { useState, useEffect } from 'react';
import { useNavigate } from "react-router";
//import 'template/Auth2';
import '../styles/Auth2.css';
import { Stack, FormControlLabel, TextField, InputAdornment, Checkbox, Typography, IconButton, Button, MenuItem, Box, Grid } from '@mui/material';
import SendIcon from '@mui/icons-material/Send';
import PersonOutlineIcon from '@mui/icons-material/PersonOutline'; 
import EmailIcon from '@mui/icons-material/Email';
import LockIcon from '@mui/icons-material/Lock';
import PhoneIphoneIcon from '@mui/icons-material/PhoneIphone';
import HowToRegIcon from '@mui/icons-material/HowToReg';
import { useAppDispatch } from "redux/store";
import { onboardingAPICALL } from "services/authService";
import { setJWTToken } from "redux/actions";
import { setError } from "redux/slices/auth";
import { useSelector } from "react-redux";
import { RootState } from "redux/reducers";
import { useFormik } from "formik";
import * as yup from "yup";

const width = 666;
const height = 468;

const logoWidth = 180;
const logoHeight = 90;

const validationSchema = yup.object({
  fullname: yup
    .string()   
    .required('Login ID is required'),
  email: yup
    .string()  
    .email('Enter a valid email')
    .required('Email is required'),
    password: yup
    .string()  
    .min(8, 'Password should be of minimum 8 characters length')
    .required('Email is required'),
    phone: yup
    .string()  
    .min(11, 'Minimum phone number must be 11 digits')
    .max(11, 'Maximum phone number must be 11 digits')
    .required('Phone number is required'), 
    registeras: yup
    .string()     
    .required('Select how you want to sign up'),   
})

const Registerr: React.FC = () => {
    const navigate = useNavigate();
    const dispatch = useAppDispatch();
    const rData = useSelector((state: RootState) => state.authToken);
    const [registerAs, setRegisterAs] = useState('');

  const handleRegisterAsChange = (event: React.ChangeEvent<HTMLInputElement>) => {
       setRegisterAs(event.target.value as string);
  }

  const formik = useFormik({
    initialValues: {
      fullname: '',
      email: '',
      password: '',
      phone: '',
      registeras: '',
    },
    validationSchema: validationSchema,
    onSubmit: async (values) => {
      /* const response = await onboardingAPICALL(values.username, values.password);
      if (response) {
        dispatch(setJWTToken(response));
        navigate("/dashboardd", { replace: true });
      } else {
        dispatch(setError("Invalid Credentials"));
      } */
    },
  });

  return <>
  <div className="container-fluid px-1 px-md-5 px-lg-1 px-xl-5 py-5 mx-auto">
  <div className="card card0 border-0">
    <div className="row d-flex">
      <div className="col-lg-6">
        <div className="card1 pb-5">
        <br/>
            <br/>
            <br/>
            <br/>
            <br/>
            <br/>
          <div className="row">
            {" "}
            {/* <img src="assets/images/ai_imma_logo_only.png" width={logoWidth} height={logoHeight} className="logo" /> */}{" "}
          </div>
          <div className="row px-3 justify-content-center mt-4 mb-5 border-line">
            {" "}
           
            <img src="assets/images/ai-immaLogo.png" width={width} height={height} className="image" />{" "}
          </div>
        </div>
      </div>
      <div className="col-lg-6">
        <div className="card2 card border-0 px-4 py-5">
          <div className="row mb-4 px-3">
            <h3 style={{color: '#32a852'}}>Join the trend</h3>
            <HowToRegIcon color="primary" fontSize='large' />
            
          </div>
          <div className="row px-3 mb-4">
            <div className="line" />{" "}
            <small style={{color: '#32a852'}}>Register</small>
            <div className="line" />
          </div>
          <form style={{width: '100%}'}} onSubmit={formik.handleSubmit}>
          <div className="row px-3">
          <Grid container direction={"column"} spacing={2}>
          <Grid item>
  <TextField 
              style ={{width: '100%'}} 
              label="Full Name" 
              name="fullname"
              placeholder="Full Name" 
              variant="standard"
              type="text" 
              value={formik.values.fullname}
              onChange={formik.handleChange}
              error={formik.touched.fullname && Boolean(formik.errors.fullname)}
              helperText={formik.touched.fullname && formik.errors.fullname}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <PersonOutlineIcon color="primary" />
                  </InputAdornment>
                ),
              }} />
                          
  </Grid>
  <Grid item>
  <TextField 
              style ={{width: '100%'}} 
              label="Email Address" 
              name="email"
              placeholder="Email Address / Phone Number" 
              variant="standard"
              type="text" 
              value={formik.values.email}
              onChange={formik.handleChange}
              error={formik.touched.email && Boolean(formik.errors.email)}
              helperText={formik.touched.email && formik.errors.email}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <EmailIcon color="primary" />
                  </InputAdornment>
                ),
              }} />        
  </Grid>
  <Grid item>
  <TextField 
              style ={{width: '100%'}} 
              type="password" 
              label="Password" 
              name="password"
              placeholder="Password" 
              variant="standard"
              value={formik.values.password}
              onChange={formik.handleChange}
              error={formik.touched.password && Boolean(formik.errors.password)}
              helperText={formik.touched.password && formik.errors.password}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <LockIcon color="primary" />
                  </InputAdornment>
                ),
              }} />         
  </Grid>
  <Grid item>
  <TextField 
              style ={{width: '100%'}}  
              type="number" 
              label="Phone Number" 
              name="phone"
              placeholder="Phone Number" 
              variant="standard"
              value={formik.values.phone}
              onChange={formik.handleChange}
              error={formik.touched.phone && Boolean(formik.errors.phone)}
              helperText={formik.touched.phone && formik.errors.phone}
              color="primary"
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <PhoneIphoneIcon color="primary" />
                  </InputAdornment>
                ),
              }}
              //inputProps={{ maxLength: 11 }}
              />            
  </Grid>
  <Grid item>
  <TextField 
              style ={{width: '70%'}}                           
              label="Register As" 
              name="registeras"
              select
              value={formik.values.registeras}
             onChange={handleRegisterAsChange}
             error={formik.touched.registeras && Boolean(formik.errors.registeras)}
             helperText={formik.touched.registeras && formik.errors.registeras}
             >
                
                <MenuItem value="AG">Agent</MenuItem>
                <MenuItem value="DRV">Driver</MenuItem>
                <MenuItem value="TRN">Transporter</MenuItem>

                 </TextField>
  </Grid>
  <Grid item>
  <FormControlLabel 
label='I accept to the Terms and Conditions' 
control={
<Checkbox
color='primary'
/* size='small'

checked={acceptTnC}
onChange={handleChange} */
 />
} 
/>       
  </Grid>
  <Grid item>
  <Button variant='contained' 
startIcon={<SendIcon />} 
disableRipple 
>Register</Button>
  </Grid>
  <Grid item>
  Already have an account? <a style={{color: 'green'}} onClick={() => {
                      navigate("/login")}}>Login</a>
  </Grid>
</Grid>
                   
   </div> 
   </form>                           
          <div className="row mb-4 px-3">
            {" "}
            <small className="font-weight-bold">
             
            </small>{" "}
          </div>
        </div>
      </div>
    </div>
    <div style={{backgroundColor: '#32a852'}} className="bg-blue py-4">
      <div className="row px-3">
        {" "}
        <small className="ml-4 ml-sm-5 mb-2">
        Copyright  2022, Aiimma Global Logistics. All rights reserved.
        </small>
        <div className="social-contact ml-4 ml-sm-auto">
          {" "}
          <span style={{color: 'white'}}><a style={{color: 'white'}} onClick={() => {
                      navigate("/termsandcondition")}}>Terms and Condition | </a></span>

          <span style={{color: 'white'}}><a style={{color: 'white'}} onClick={() => {
                      navigate("/privacypolicy")}}>Privacy Policy </a></span>
        </div>
      </div>
    </div>
  </div>
</div>

  </>
}

export default Registerr;