import { APIService } from "services";

export const getAiimma_Customers_Deposit_Balance = async (pageNo: any,pageSize: any,search: string | any[]) => {
    let res;
    if(search.length===0) {
        res = await getAllAiimma_Customers_Deposit_Balance(pageNo,pageSize);
    }
    else{
        try {
            res = await searchAiimma_Customers_Deposit_Balance(search,pageNo,pageSize);
        } catch(err) {
             return { records:[], totalCount:0 }
        }
    }
    if(res && res.data){
    return res.data;
    }else{
    return { records:[], totalCount:0 }
    }
    
}


export const addAiimma_Customers_Deposit_Balance = (data: any) => {
return APIService.api().post(`/aiimma_customers_deposit_balance`,data)
}
export const updateAiimma_Customers_Deposit_Balance = (ID: any,data: any) => {
return APIService.api().patch(`/aiimma_customers_deposit_balance/${ID}`,data)
}
export const getAllAiimma_Customers_Deposit_Balance = (pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_customers_deposit_balance/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const getOneAiimma_Customers_Deposit_Balance = (ID: any) => {
return APIService.api().get(`/aiimma_customers_deposit_balance/${ID}`)
}
export const searchAiimma_Customers_Deposit_Balance = (searchKey: any,pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_customers_deposit_balance/search/${searchKey}/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const deleteAiimma_Customers_Deposit_Balance = (ID: any) => {
return APIService.api().delete(`/aiimma_customers_deposit_balance/${ID}`)
}
