import { APIService } from "services";

export const getAiimma_Bookingvw = async (pageNo: any,pageSize: any,search: string | any[]) => {
    let res;
    if(search.length===0) {
        res = await getAllAiimma_Bookingvw(pageNo,pageSize);
    }
    else{
        try {
            res = await searchAiimma_Bookingvw(search,pageNo,pageSize);
        } catch(err) {
             return { records:[], totalCount:0 }
        }
    }
    if(res && res.data){
    return res.data;
    }else{
    return { records:[], totalCount:0 }
    }
    
}


export const addAiimma_Bookingvw = (data: any) => {
return APIService.api().post(`/booking`,data)
}
export const updateAiimma_Bookingvw = (ID: any,data: any) => {
return APIService.api().patch(`/booking/${ID}`,data)
}
export const getAllAiimma_Bookingvw = (pageNo: any,pageSize: any) => {
return APIService.api().get(`/booking/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const getOneAiimma_Bookingvw = (ID: any) => {
return APIService.api().get(`/booking/${ID}`)
}
export const searchAiimma_Bookingvw = (searchKey: any,pageNo: any,pageSize: any) => {
return APIService.api().get(`/booking/search/${searchKey}/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const deleteAiimma_Bookingvw = (ID: any) => {
return APIService.api().delete(`/booking/${ID}`)
}
