import { useFormik } from "formik";
import React, { useEffect } from "react";
import { useSelector } from "react-redux";
import { RootState } from "redux/reducers";

import { Button, Card, Form } from "react-bootstrap";
import { setAiimma_Transporter_Driver_JunctionMessage } from "redux/actions";
import { resetAiimma_TransportersToInit, setAiimma_TransportersList } from "redux/actions";

import { getAiimma_Transporters } from "services/aiimma_transportersService";

import { useAppDispatch } from "redux/store";
import { addAiimma_Transporter_Driver_Junction, updateAiimma_Transporter_Driver_Junction } from "services/aiimma_transporter_driver_junctionService";
import { Constant } from "template/Constant";
import * as yup from 'yup';
type Props = {
    row?: any,
    hideShowForm: (actionName: any) => void;
    getData: (page: any, pageSize: any, searchKey: any) => void;
    action?: string
};
export const Aiimma_Transporter_Driver_JunctionForm: React.FC<Props> = ({ row, hideShowForm, getData, action }) => {
    const dispatch = useAppDispatch();
    const iValue={ID:'',transporter_id:'',driver_id:'',driver_still_active:'',connection_started:'',connection_ended:'',LoggedIP:'',RecordCreationAt:''};
    const initialValue = action === 'edit' ? row : iValue;
    const aiimma_transportersData = useSelector((state: RootState) => state.aiimma_transporters);

    useEffect(() => {
if (aiimma_transportersData && aiimma_transportersData.list && aiimma_transportersData.list.length === 0) {
            dispatch(resetAiimma_TransportersToInit());
            getAiimma_Transporters(Constant.defaultPageNumber, Constant.defaultDropdownPageSize, '').then((response) => {
                if (response && response.records) {
                  dispatch(setAiimma_TransportersList({ pageNo: Constant.defaultPageNumber, pageSize: Constant.defaultDropdownPageSize, list: response.records, totalCount: response.total_count, searchKey: '' }));
                } else {
                  dispatch(setAiimma_Transporter_Driver_JunctionMessage("No Record Found For Aiimma_Transporters"));
                }
              })
        }
})
    const formik = useFormik({
        initialValues: initialValue,
        onSubmit: async (values) => {
            if (action === 'edit') {
                const response = await updateAiimma_Transporter_Driver_Junction(values.ID,values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_Transporter_Driver_JunctionMessage("Updated Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_Transporter_Driver_JunctionMessage("Some error occured!"));
                }
            } else if (action === 'add') {
                const response = await addAiimma_Transporter_Driver_Junction(values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_Transporter_Driver_JunctionMessage("Added Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_Transporter_Driver_JunctionMessage("Some error occured!"));
                }
            }
        },
        validationSchema: yup.object({
           driver_still_active: yup.string(),
connection_started: yup.date(),
connection_ended: yup.date(),
LoggedIP: yup.string().required('LoggedIP is required'),
RecordCreationAt: yup.date().required('RecordCreationAt is required'),
transporter_id: yup.string().required('transporter_id is required'),

        }),
    });
    return (
        <Card className="shadow mb-4">
            <Card.Header className="py-3">
                <h6 className="m-0 font-weight-bold text-primary text-capitalize">{action} Aiimma_Transporter_Driver_Junction
                    <Button className="btn-icon-split float-right" onClick={() => hideShowForm(false)}>
                        <span className="icon text-white-50">
                            <i className="fas fa-list"></i>
                        </span>
                        <span className="text">View Aiimma_Transporter_Driver_Junction</span>
                    </Button>
                </h6>

            </Card.Header>
            <Card.Body>
                <Form onSubmit={formik.handleSubmit}>
                  <Form.Group>
<label className="form -control-label">driver_still_active</label>
<Form.Control type="text" name="driver_still_active" className="form-control" value={formik.values.driver_still_active}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.driver_still_active && !!formik.errors.driver_still_active}
isValid ={!!formik.touched.driver_still_active && !formik.errors.driver_still_active}
></Form.Control>
{
    formik.errors.driver_still_active && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.driver_still_active}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">connection_started</label>
<Form.Control type="date" name="connection_started" className="form-control" value={formik.values.connection_started}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.connection_started && !!formik.errors.connection_started}
isValid ={!!formik.touched.connection_started && !formik.errors.connection_started}
></Form.Control>
{
    formik.errors.connection_started && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.connection_started}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">connection_ended</label>
<Form.Control type="date" name="connection_ended" className="form-control" value={formik.values.connection_ended}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.connection_ended && !!formik.errors.connection_ended}
isValid ={!!formik.touched.connection_ended && !formik.errors.connection_ended}
></Form.Control>
{
    formik.errors.connection_ended && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.connection_ended}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">LoggedIP</label>
<Form.Control type="text" name="LoggedIP" className="form-control" value={formik.values.LoggedIP}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.LoggedIP && !!formik.errors.LoggedIP}
isValid ={!!formik.touched.LoggedIP && !formik.errors.LoggedIP}
></Form.Control>
{
    formik.errors.LoggedIP && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.LoggedIP}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">RecordCreationAt</label>
<Form.Control type="date" name="RecordCreationAt" className="form-control" value={formik.values.RecordCreationAt}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.RecordCreationAt && !!formik.errors.RecordCreationAt}
isValid ={!!formik.touched.RecordCreationAt && !formik.errors.RecordCreationAt}
></Form.Control>
{
    formik.errors.RecordCreationAt && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.RecordCreationAt}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">transporter_id</label>
<Form.Control as="select"  name="transporter_id" className="form-control" value={formik.values.transporter_id}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.transporter_id && !!formik.errors.transporter_id}
isValid ={!!formik.touched.transporter_id && !formik.errors.transporter_id}
>
<option value={0}>Select Aiimma_Transporters </option> 
{
aiimma_transportersData.list.map((item, i) => {
return <option value={item.ID} key={`aiimma_transporters-${i}`}>{item.UserID}</option>
})}
</Form.Control>
{
    formik.errors.transporter_id && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.transporter_id}
    </Form.Control.Feedback>
)}
</Form.Group>

                    <Form.Group>
                        <Button type="submit" className="float-right" variant="primary">Save</Button>
                    </Form.Group>
                </Form>
            </Card.Body>
        </Card>
    );
}

