import React from 'react';
import { Button, Card, Form } from "react-bootstrap";

const JourneyDetails: React.FC = () => {
  return <>
  <Card className="shadow mb-4">
          <Card.Header className="py-3">
              <h3 className="m-0 font-weight-bold text-primary text-capitalize">Define Your Journey                 
              </h3>

          </Card.Header>
          <Card.Body>
<Form.Group>
<label className="form -control-label">Location From</label>
<Form.Control 
type="text" 
name="Location_From" 
className="form-control" 
>    
</Form.Control>
</Form.Group>

<Form.Group>
<label className="form -control-label">Destination Port</label>
<Form.Control 
type="text" 
name="Journey_Destination_Port" 
className="form-control" 
>    
</Form.Control>
</Form.Group>
<Form.Group>
<label className="form -control-label">Booking Type</label>
<Form.Control 
as="select" 
name="Location_From" 
className="form-control" 
>    
<option value="Pickup Loaded Container">Pickup Loaded Container</option>
    <option value="Pickup Empty Container">Pickup Empty Container</option>
    <option value="Empty">Empty</option>
    <option value="Export">Export</option>
</Form.Control>
</Form.Group>

<Form.Group>
<label className="form -control-label">Journey Start Date</label>
<Form.Control type="date" 
name="JourneyStartedDate" 
className="form-control" 
></Form.Control>

</Form.Group>
<Form.Group>
<label className="form -control-label">Expected Delivery Date</label>
<Form.Control type="date" 
name="ExpectedDeliveryDate" 
className="form-control" 
></Form.Control>
</Form.Group>

<Form.Group>
<label className="form -control-label">Lock Booking</label>
<Form.Control type="text" 
name="LockBooking" 
className="form-control" 
>
<option value="0">NO</option>
    <option value="1">YES</option>
</Form.Control>
</Form.Group>





          </Card.Body>
      </Card>
</>
}

export default JourneyDetails;