import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface IAiimma_Truck_Maintenance_Journey_Booking_Details {
id:number,
bookingid:number,
maintenanceDescription:string,
RecordAddedBy:number,
LoggedIP:string,
RecordCreatedAt:Date
}

interface IAiimma_Truck_Maintenance_Journey_Booking_DetailsData {
    list?: Array<IAiimma_Truck_Maintenance_Journey_Booking_Details>;
    pageNo: number;
    pageSize: number;
    searchKey?: string;
    totalCount?: number;
    message?: string;
}

const initialState: IAiimma_Truck_Maintenance_Journey_Booking_DetailsData = {
    pageNo: 1,
    pageSize: 20,
    searchKey: '',
    list: [],
    totalCount: 0,
    message: '',
};

const aiimma_truck_maintenance_journey_booking_detailsSlice = createSlice({
    name: "aiimma_truck_maintenance_journey_booking_details",
    initialState,
    reducers: {
        setAiimma_Truck_Maintenance_Journey_Booking_DetailsList: (state, _action: PayloadAction<IAiimma_Truck_Maintenance_Journey_Booking_DetailsData>) => {
            state.list = _action.payload.list;
            state.pageNo = _action.payload.pageNo;
            state.pageSize = _action.payload.pageSize;
            state.totalCount = _action.payload.totalCount;
        },
        resetAiimma_Truck_Maintenance_Journey_Booking_DetailsToInit: (state) => {
            state = initialState;
        },
        setAiimma_Truck_Maintenance_Journey_Booking_DetailsMessage: (state, _action: PayloadAction<string>) => {
            state.message = _action.payload;
        },
    },
});

export const { setAiimma_Truck_Maintenance_Journey_Booking_DetailsList, resetAiimma_Truck_Maintenance_Journey_Booking_DetailsToInit, setAiimma_Truck_Maintenance_Journey_Booking_DetailsMessage } = aiimma_truck_maintenance_journey_booking_detailsSlice.actions;

export default aiimma_truck_maintenance_journey_booking_detailsSlice.reducer;

