import React, { useState, useLayoutEffect, useEffect } from 'react'
import { useLocation } from 'react-router-dom';
//import '../styles/AdminDashboardStyles.css';
import Dashboardheader from 'pages/Dashboardheader';
import DashboardMenu from 'pages/DashboardMenu';
import DashboardHeaderGreetings from 'pages/DashboardHeaderGreetings';
import DashboardMetricCards from 'pages/DashboardMetricCards';
import DashboardSalesChart from 'pages/DashboardSalesChart';
import Adminfooter from 'pages/Adminfooter';
import DashboardTopLeaderTable from 'pages/DashboardTopLeaderTable';
import DashboardPagesArea from 'pages/DashboardPagesArea';
import { Aiimma_Agents, Aiimma_Bt_Hb_Customers, Aiimma_Drivers, Aiimma_Tdos, Aiimma_Transporters, Aiimma_Trucks } from 'components';

import { Col, Card } from "react-bootstrap";
import { useNavigate } from "react-router";
import { getAiimma_Bookingvw } from "services/aiimma_bookingvwService";
import { useAppDispatch } from "redux/store";
import { Aiimma_BookingvwTable } from "../bookingvw/table";
import {
  setAiima_Transporter_Driver_Bthb_BookingList,
  setAiima_Transporter_Driver_Bthb_BookingMessage,
  setAiimma_BookingvwList,
  setAiimma_BookingvwMessage,
} from "redux/actions";
import { useSelector } from "react-redux";
import { RootState } from "redux/reducers";

const Dashboardd: React.FC = () => {
    const navigate = useNavigate();
    const dispatch = useAppDispatch();
    const location = useLocation();
    
  const rData = useSelector(
    (state: RootState) => state.aiimma_bookingvw
  );
  const [row, setRow] = useState(undefined);
  const [action, setAction] = useState("");
  const getData = (page: any, pageSize: any, searchKey: any) => {
    getAiimma_Bookingvw(page, pageSize, searchKey).then(
      (response) => {
        if (response && response.records) {
          dispatch(
            setAiimma_BookingvwList({
              pageNo: page,
              pageSize: pageSize,
              list: response.records,
              totalCount: response.totalCount,
              searchKey: searchKey,
            })
          );
        } else {
          dispatch(
            setAiimma_BookingvwMessage("No Record Found")
          );
        }
      }
    );
  };

  const handleRowEdit = (rowData: any) => {
    setRow(rowData);
    dispatch(setAiimma_BookingvwMessage(""));
    setAction("edit");
  };


  return <>
  {/* <div className="preloader">
    <div className="lds-ripple">
      <div className="lds-pos" />
      <div className="lds-pos" />
    </div>
  </div> */}
   <div
    id="main-wrapper"
    data-theme="light"
    data-layout="vertical"
    data-navbarbg="skin6"
    data-sidebartype="full"
    data-sidebar-position="fixed"
    data-header-position="fixed"
    data-boxed-layout="full"
  >
    {/* ============================================================== */}
    {/* Topbar header - style you can find in pages.scss */}
    {/* ============================================================== */}
   <Dashboardheader />
   <DashboardMenu /> 
   
  {/* Page wrapper  */}
  {/* ============================================================== */}
  <div className="page-wrapper">

   <DashboardHeaderGreetings />
   
  <div className="container-fluid">
    {/* *************************************************************** */}
     <DashboardMetricCards />
    
  <DashboardSalesChart />
  
  <DashboardTopLeaderTable handleRowEdit={handleRowEdit}
                  hideShowForm={setAction}
                  getData={getData} /> 

 {/*  <DashboardPagesArea>
      <p>This dashboard will change for different logged in user based on their role</p>
  </DashboardPagesArea> */}

  </div>
  <Adminfooter />
  
  </div>
  </div>
  </>
  
}

export default Dashboardd;