import { useFormik } from "formik";
import React, { useEffect } from "react";
import { useSelector } from "react-redux";
import { RootState } from "redux/reducers";

import { Button, Card, Form } from "react-bootstrap";
import { setAiimma_Holdingbay_Containers_UploadMessage } from "redux/actions";
import { resetAiimma_Bt_Hb_CustomersToInit, setAiimma_Bt_Hb_CustomersList } from "redux/actions";

import { getAiimma_Bt_Hb_Customers } from "services/aiimma_bt_hb_customersService";

import { useAppDispatch } from "redux/store";
import { addAiimma_Holdingbay_Containers_Upload, updateAiimma_Holdingbay_Containers_Upload } from "services/aiimma_holdingbay_containers_uploadService";
import { Constant } from "template/Constant";
import * as yup from 'yup';
type Props = {
    row?: any,
    hideShowForm: (actionName: any) => void;
    getData: (page: any, pageSize: any, searchKey: any) => void;
    action?: string
};
export const Aiimma_Holdingbay_Containers_UploadForm: React.FC<Props> = ({ row, hideShowForm, getData, action }) => {
    const dispatch = useAppDispatch();
    const iValue={ID:'',bt_hb_customer_id:'',UploadCode:'',ShippingLine:'',ContainerMTU_Number:'',ContainerSize:'',ContainerSpec:'',LoggedIP:'',holdingbay_id:'',RecordCreatedAt:''};
    const initialValue = action === 'edit' ? row : iValue;
    const aiimma_bt_hb_customersData = useSelector((state: RootState) => state.aiimma_bt_hb_customers);

    useEffect(() => {
if (aiimma_bt_hb_customersData && aiimma_bt_hb_customersData.list && aiimma_bt_hb_customersData.list.length === 0) {
            dispatch(resetAiimma_Bt_Hb_CustomersToInit());
            getAiimma_Bt_Hb_Customers(Constant.defaultPageNumber, Constant.defaultDropdownPageSize, '').then((response) => {
                if (response && response.records) {
                  dispatch(setAiimma_Bt_Hb_CustomersList({ pageNo: Constant.defaultPageNumber, pageSize: Constant.defaultDropdownPageSize, list: response.records, totalCount: response.total_count, searchKey: '' }));
                } else {
                  dispatch(setAiimma_Holdingbay_Containers_UploadMessage("No Record Found For Aiimma_Bt_Hb_Customers"));
                }
              })
        }
})
    const formik = useFormik({
        initialValues: initialValue,
        onSubmit: async (values) => {
            if (action === 'edit') {
                const response = await updateAiimma_Holdingbay_Containers_Upload(values.ID,values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_Holdingbay_Containers_UploadMessage("Updated Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_Holdingbay_Containers_UploadMessage("Some error occured!"));
                }
            } else if (action === 'add') {
                const response = await addAiimma_Holdingbay_Containers_Upload(values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_Holdingbay_Containers_UploadMessage("Added Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_Holdingbay_Containers_UploadMessage("Some error occured!"));
                }
            }
        },
        validationSchema: yup.object({
           UploadCode: yup.string(),
ShippingLine: yup.string().required('ShippingLine is required'),
ContainerMTU_Number: yup.string().required('ContainerMTU_Number is required'),
ContainerSize: yup.string().required('ContainerSize is required'),
ContainerSpec: yup.string(),
LoggedIP: yup.string().required('LoggedIP is required'),
holdingbay_id: yup.number(),
RecordCreatedAt: yup.date().required('RecordCreatedAt is required'),
bt_hb_customer_id: yup.string().required('bt_hb_customer_id is required'),

        }),
    });
    return (
        <Card className="shadow mb-4">
            <Card.Header className="py-3">
                <h6 className="m-0 font-weight-bold text-primary text-capitalize">{action} Aiimma_Holdingbay_Containers_Upload
                    <Button className="btn-icon-split float-right" onClick={() => hideShowForm(false)}>
                        <span className="icon text-white-50">
                            <i className="fas fa-list"></i>
                        </span>
                        <span className="text">View Aiimma_Holdingbay_Containers_Upload</span>
                    </Button>
                </h6>

            </Card.Header>
            <Card.Body>
                <Form onSubmit={formik.handleSubmit}>
                  <Form.Group>
<label className="form -control-label">UploadCode</label>
<Form.Control type="text" name="UploadCode" className="form-control" value={formik.values.UploadCode}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.UploadCode && !!formik.errors.UploadCode}
isValid ={!!formik.touched.UploadCode && !formik.errors.UploadCode}
></Form.Control>
{
    formik.errors.UploadCode && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.UploadCode}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">ShippingLine</label>
<Form.Control type="text" name="ShippingLine" className="form-control" value={formik.values.ShippingLine}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.ShippingLine && !!formik.errors.ShippingLine}
isValid ={!!formik.touched.ShippingLine && !formik.errors.ShippingLine}
></Form.Control>
{
    formik.errors.ShippingLine && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.ShippingLine}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">ContainerMTU_Number</label>
<Form.Control type="text" name="ContainerMTU_Number" className="form-control" value={formik.values.ContainerMTU_Number}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.ContainerMTU_Number && !!formik.errors.ContainerMTU_Number}
isValid ={!!formik.touched.ContainerMTU_Number && !formik.errors.ContainerMTU_Number}
></Form.Control>
{
    formik.errors.ContainerMTU_Number && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.ContainerMTU_Number}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">ContainerSize</label>
<Form.Control type="text" name="ContainerSize" className="form-control" value={formik.values.ContainerSize}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.ContainerSize && !!formik.errors.ContainerSize}
isValid ={!!formik.touched.ContainerSize && !formik.errors.ContainerSize}
></Form.Control>
{
    formik.errors.ContainerSize && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.ContainerSize}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">ContainerSpec</label>
<Form.Control type="text" name="ContainerSpec" className="form-control" value={formik.values.ContainerSpec}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.ContainerSpec && !!formik.errors.ContainerSpec}
isValid ={!!formik.touched.ContainerSpec && !formik.errors.ContainerSpec}
></Form.Control>
{
    formik.errors.ContainerSpec && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.ContainerSpec}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">LoggedIP</label>
<Form.Control type="text" name="LoggedIP" className="form-control" value={formik.values.LoggedIP}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.LoggedIP && !!formik.errors.LoggedIP}
isValid ={!!formik.touched.LoggedIP && !formik.errors.LoggedIP}
></Form.Control>
{
    formik.errors.LoggedIP && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.LoggedIP}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">holdingbay_id</label>
<Form.Control type="number" name="holdingbay_id" className="form-control" value={formik.values.holdingbay_id}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.holdingbay_id && !!formik.errors.holdingbay_id}
isValid ={!!formik.touched.holdingbay_id && !formik.errors.holdingbay_id}
></Form.Control>
{
    formik.errors.holdingbay_id && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.holdingbay_id}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">RecordCreatedAt</label>
<Form.Control type="date" name="RecordCreatedAt" className="form-control" value={formik.values.RecordCreatedAt}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.RecordCreatedAt && !!formik.errors.RecordCreatedAt}
isValid ={!!formik.touched.RecordCreatedAt && !formik.errors.RecordCreatedAt}
></Form.Control>
{
    formik.errors.RecordCreatedAt && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.RecordCreatedAt}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">bt_hb_customer_id</label>
<Form.Control as="select"  name="bt_hb_customer_id" className="form-control" value={formik.values.bt_hb_customer_id}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.bt_hb_customer_id && !!formik.errors.bt_hb_customer_id}
isValid ={!!formik.touched.bt_hb_customer_id && !formik.errors.bt_hb_customer_id}
>
<option value={0}>Select Aiimma_Bt_Hb_Customers </option> 
{
aiimma_bt_hb_customersData.list.map((item, i) => {
return <option value={item.ID} key={`aiimma_bt_hb_customers-${i}`}>{item.CustomerID}</option>
})}
</Form.Control>
{
    formik.errors.bt_hb_customer_id && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.bt_hb_customer_id}
    </Form.Control.Feedback>
)}
</Form.Group>

                    <Form.Group>
                        <Button type="submit" className="float-right" variant="primary">Save</Button>
                    </Form.Group>
                </Form>
            </Card.Body>
        </Card>
    );
}

