import { useFormik } from "formik";
import React from "react";

import { Button, Card, Form } from "react-bootstrap";
import { setAiimma_RolesMessage } from "redux/actions";


import { useAppDispatch } from "redux/store";
import { addAiimma_Roles, updateAiimma_Roles } from "services/aiimma_rolesService";
import { Constant } from "template/Constant";
import * as yup from 'yup';
type Props = {
    row?: any,
    hideShowForm: (actionName: any) => void;
    getData: (page: any, pageSize: any, searchKey: any) => void;
    action?: string
};
export const Aiimma_RolesForm: React.FC<Props> = ({ row, hideShowForm, getData, action }) => {
    const dispatch = useAppDispatch();
    const iValue={RoleCode:'',RoleName:'',Description:'',LoggedIP:'',RecordCreatedAt:''};
    const initialValue = action === 'edit' ? row : iValue;
    
    
    const formik = useFormik({
        initialValues: initialValue,
        onSubmit: async (values) => {
            if (action === 'edit') {
                const response = await updateAiimma_Roles(values.RoleCode,values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_RolesMessage("Updated Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_RolesMessage("Some error occured!"));
                }
            } else if (action === 'add') {
                const response = await addAiimma_Roles(values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_RolesMessage("Added Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_RolesMessage("Some error occured!"));
                }
            }
        },
        validationSchema: yup.object({
            RoleCode: yup.string().required('Role Code is required'),
           RoleName: yup.string().required('Role Name is required'),
Description: yup.string().required('Description is required'),
LoggedIP: yup.string(),
RecordCreatedAt: yup.date(),

        }),
    });
    return (
        <Card className="shadow mb-4">
            <Card.Header className="py-3">
                <h6 className="m-0 font-weight-bold text-primary text-capitalize">{action} Application Roles
                    <Button className="btn-icon-split float-right" onClick={() => hideShowForm(false)}>
                        <span className="icon text-white-50">
                            <i className="fas fa-list"></i>
                        </span>
                        <span className="text">View Application Roles</span>
                    </Button>
                </h6>

            </Card.Header>
            <Card.Body>
                <Form onSubmit={formik.handleSubmit}>
                <Form.Group>
<label className="form -control-label">Role Code</label>
<Form.Control type="text" name="RoleCode" className="form-control" value={formik.values.RoleCode}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.RoleCode && !!formik.errors.RoleCode}
isValid ={!!formik.touched.RoleCode && !formik.errors.RoleCode}
></Form.Control>
{
    formik.errors.RoleCode && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.RoleCode}
    </Form.Control.Feedback>
)}
</Form.Group>
                  <Form.Group>
<label className="form -control-label">Role Name</label>
<Form.Control type="text" name="RoleName" className="form-control" value={formik.values.RoleName}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.RoleName && !!formik.errors.RoleName}
isValid ={!!formik.touched.RoleName && !formik.errors.RoleName}
></Form.Control>
{
    formik.errors.RoleName && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.RoleName}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Description</label>
<Form.Control type="text" name="Description" className="form-control" value={formik.values.Description}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Description && !!formik.errors.Description}
isValid ={!!formik.touched.Description && !formik.errors.Description}
></Form.Control>
{
    formik.errors.Description && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Description}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="LoggedIP" className="form-control" value={formik.values.LoggedIP}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.LoggedIP && !!formik.errors.LoggedIP}
isValid ={!!formik.touched.LoggedIP && !formik.errors.LoggedIP}
></Form.Control>
{
    formik.errors.LoggedIP && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.LoggedIP}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="RecordCreatedAt" className="form-control" value={formik.values.RecordCreatedAt}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.RecordCreatedAt && !!formik.errors.RecordCreatedAt}
isValid ={!!formik.touched.RecordCreatedAt && !formik.errors.RecordCreatedAt}
></Form.Control>
{
    formik.errors.RecordCreatedAt && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.RecordCreatedAt}
    </Form.Control.Feedback>
)}
</Form.Group>

                    <Form.Group>
                        <Button type="submit" className="float-right" variant="primary">Save</Button>
                    </Form.Group>
                </Form>
            </Card.Body>
        </Card>
    );
}

