import { APIService } from "services";

export const getAiimma__Users_Access_Auditing = async (pageNo: number,pageSize: number,search: string | any[]) => {
    let res;
    if(search.length===0) {
        res = await getAllAiimma__Users_Access_Auditing(pageNo,pageSize);
    }
    else{
        try {
            res = await searchAiimma__Users_Access_Auditing(search,pageNo,pageSize);
        } catch(err) {
             return { records:[], totalCount:0 }
        }
    }
    if(res && res.data){
    return res.data;
    }else{
    return { records:[], totalCount:0 }
    }
    
}


export const addAiimma__Users_Access_Auditing = (data: any) => {
return APIService.api().post(`/aiimma__users_access_auditing`,data)
}
export const updateAiimma__Users_Access_Auditing = (ID: any,data: any) => {
return APIService.api().patch(`/aiimma__users_access_auditing/${ID}`,data)
}
export const getAllAiimma__Users_Access_Auditing = (pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma__users_access_auditing/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const getOneAiimma__Users_Access_Auditing = (ID: any) => {
return APIService.api().get(`/aiimma__users_access_auditing/${ID}`)
}
export const searchAiimma__Users_Access_Auditing = (searchKey: any,pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma__users_access_auditing/search/${searchKey}/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const deleteAiimma__Users_Access_Auditing = (ID: any) => {
return APIService.api().delete(`/aiimma__users_access_auditing/${ID}`)
}
