import { APIService } from "services";

export const getAiimma_Notifyusers = async (pageNo: any,pageSize: any,search: string | any[]) => {
    let res;
    if(search.length===0) {
        res = await getAllAiimma_Notifyusers(pageNo,pageSize);
    }
    else{
        try {
            res = await searchAiimma_Notifyusers(search,pageNo,pageSize);
        } catch(err) {
             return { records:[], totalCount:0 }
        }
    }
    if(res && res.data){
    return res.data;
    }else{
    return { records:[], totalCount:0 }
    }
    
}


export const addAiimma_Notifyusers = (data: any) => {
return APIService.api().post(`/Aiimma_Notifyusers`,data)
}
export const updateAiimma_Notifyusers = (ID: any,data: any) => {
return APIService.api().patch(`/Aiimma_Notifyusers/${ID}`,data)
}
export const getAllAiimma_Notifyusers = (pageNo: any,pageSize: any) => {
return APIService.api().get(`/Aiimma_Notifyusers/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const getOneAiimma_Notifyusers = (ID: any) => {
return APIService.api().get(`/Aiimma_Notifyusers/${ID}`)
}
export const searchAiimma_Notifyusers = (searchKey: any,pageNo: any,pageSize: any) => {
return APIService.api().get(`/Aiimma_Notifyusers/search/${searchKey}/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const deleteAiimma_Notifyusers = (ID: any) => {
return APIService.api().delete(`/Aiimma_Notifyusers/${ID}`)
}
