import React, { useState } from "react";
import { Alert, Button } from "react-bootstrap";
import { useSelector } from "react-redux";
import {
  setAiima_Transporter_Driver_Bthb_BookingList,
  setAiima_Transporter_Driver_Bthb_BookingMessage,
  setAiimma_BookingvwList,
  setAiimma_BookingvwMessage,
} from "redux/actions";
import { RootState } from "redux/reducers";
import { useAppDispatch } from "redux/store";
import { getAiima_Transporter_Driver_Bthb_Booking } from "services/aiima_transporter_driver_bthb_bookingService";
import Layout from "template";
import { Constant } from "template/Constant";
import { Aiima_Transporter_Driver_Bthb_BookingForm } from "./form";
import { Aiima_Transporter_Driver_Bthb_BookingTable } from "./table";
import Dashboardheader from "pages/Dashboardheader";
import DashboardMenu from "pages/DashboardMenu";
import Adminfooter from "pages/Adminfooter";
import { getAiimma_Bookingvw } from "services/aiimma_bookingvwService";

export const Aiima_Transporter_Driver_Bthb_Booking: React.FC = () => {
  const dispatch = useAppDispatch();
  const rData = useSelector(
    (state: RootState) => state.aiimma_bookingvw
  );
  const [row, setRow] = useState(undefined);
  const [action, setAction] = useState("");
  const getData = (page: any, pageSize: any, searchKey: any) => {
    getAiimma_Bookingvw(page, pageSize, searchKey).then(
      (response) => {
        if (response && response.records) {
          dispatch(
            setAiimma_BookingvwList({
              pageNo: page,
              pageSize: pageSize,
              list: response.records,
              totalCount: response.totalCount,
              searchKey: searchKey,
            })
          );
        } else {
          dispatch(
            setAiimma_BookingvwMessage("No Record Found")
          );
        }
      }
    );
  };

  const handleRowEdit = (rowData: any) => {
    setRow(rowData);
    dispatch(setAiimma_BookingvwMessage(""));
    setAction("edit");
  };
  return (
    <>
      <div
        id="main-wrapper"
        data-theme="light"
        data-layout="vertical"
        data-navbarbg="skin6"
        data-sidebartype="full"
        data-sidebar-position="fixed"
        data-header-position="fixed"
        data-boxed-layout="full"
      >
        <Dashboardheader />
        <DashboardMenu />
        <div className="page-wrapper">
          <div className="container-fluid">
            <div className="d-sm-flex align-items-center justify-content-between mb-4">
              <h1 className="h3 mb-0 text-gray-800"></h1>
            </div>
            <div className="d-flex flex-column min-vh-100">
              {rData.message ? (
                <Alert
                  variant={Constant.defaultAlertVarient}
                  className="alert-dismissible fade"
                >
                  {rData.message}
                  <Button
                    type="button"
                    className="close"
                    data-dismiss="alert"
                    aria-label="Close"
                    onClick={() =>
                      dispatch(
                        setAiimma_BookingvwMessage("")
                      )
                    }
                  >
                    <span aria-hidden="true">&times;</span>
                  </Button>
                </Alert>
              ) : null}
              {action ? (
                <Aiima_Transporter_Driver_Bthb_BookingForm
                  hideShowForm={setAction}
                  action={action}
                  row={row}
                  getData={getData}
                />
              ) : (
                <Aiima_Transporter_Driver_Bthb_BookingTable
                  handleRowEdit={handleRowEdit}
                  hideShowForm={setAction}
                  getData={getData}
                />
              )}
            </div>
          </div>
        </div>
        <Adminfooter />
      </div>
      <div></div>
    </>
  );
};
