import { useFormik } from "formik";
import React, { useEffect } from "react";
import { useSelector } from "react-redux";
import { RootState } from "redux/reducers";

import { Button, Card, Form } from "react-bootstrap";
import { setAiimma_StaffsMessage } from "redux/actions";
import { resetAiimma_RolesToInit, setAiimma_RolesList } from "redux/actions";

import { getAiimma_Roles } from "services/aiimma_rolesService";

import { useAppDispatch } from "redux/store";
import { addAiimma_Staffs, updateAiimma_Staffs } from "services/aiimma_staffsService";
import { Constant } from "template/Constant";
import * as yup from 'yup';
type Props = {
    row?: any,
    hideShowForm: (actionName: any) => void;
    getData: (page: any, pageSize: any, searchKey: any) => void;
    action?: string
};
export const Aiimma_StaffsForm: React.FC<Props> = ({ row, hideShowForm, getData, action }) => {
    const dispatch = useAppDispatch();
    const iValue={id:'',staffCode:'',staffName:'',Email:'',Phone:'',Gender:'',staffRole:'',RecordCreatedBy:'',LoggedIP:'',RecordCreatedAt:''};
    const initialValue = action === 'edit' ? row : iValue;
    const aiimma_rolesData = useSelector((state: RootState) => state.aiimma_roles);

    useEffect(() => {
if (aiimma_rolesData && aiimma_rolesData.list && aiimma_rolesData.list.length === 0) {
            dispatch(resetAiimma_RolesToInit());
            getAiimma_Roles(Constant.defaultPageNumber, Constant.defaultDropdownPageSize, '').then((response) => {
                if (response && response.records) {
                  dispatch(setAiimma_RolesList({ pageNo: Constant.defaultPageNumber, pageSize: Constant.defaultDropdownPageSize, list: response.records, totalCount: response.total_count, searchKey: '' }));
                } else {
                  dispatch(setAiimma_StaffsMessage("No Record Found For Aiimma_Roles"));
                }
              })
        }
})
    const formik = useFormik({
        initialValues: initialValue,
        onSubmit: async (values) => {
            if (action === 'edit') {
                const response = await updateAiimma_Staffs(values.id,values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_StaffsMessage("Updated Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_StaffsMessage("Some error occured!"));
                }
            } else if (action === 'add') {
                const response = await addAiimma_Staffs(values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_StaffsMessage("Added Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_StaffsMessage("Some error occured!"));
                }
            }
        },
        validationSchema: yup.object({
           staffCode: yup.string().required('staffCode is required'),
staffName: yup.string().required('staffName is required'),
Email: yup.string().required('Email is required'),
Phone: yup.string().required('Phone is required'),
Gender: yup.string().required('Gender is required'),
RecordCreatedBy: yup.number().required('RecordCreatedBy is required'),
LoggedIP: yup.string().required('LoggedIP is required'),
RecordCreatedAt: yup.date().required('RecordCreatedAt is required'),
staffRole: yup.string().required('staffRole is required'),

        }),
    });
    return (
        <Card className="shadow mb-4">
            <Card.Header className="py-3">
                <h6 className="m-0 font-weight-bold text-primary text-capitalize">{action} Aiimma_Staffs
                    <Button className="btn-icon-split float-right" onClick={() => hideShowForm(false)}>
                        <span className="icon text-white-50">
                            <i className="fas fa-list"></i>
                        </span>
                        <span className="text">View Aiimma_Staffs</span>
                    </Button>
                </h6>

            </Card.Header>
            <Card.Body>
                <Form onSubmit={formik.handleSubmit}>
                  <Form.Group>
<label className="form -control-label">staffCode</label>
<Form.Control type="text" name="staffCode" className="form-control" value={formik.values.staffCode}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.staffCode && !!formik.errors.staffCode}
isValid ={!!formik.touched.staffCode && !formik.errors.staffCode}
></Form.Control>
{
    formik.errors.staffCode && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.staffCode}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">staffName</label>
<Form.Control type="text" name="staffName" className="form-control" value={formik.values.staffName}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.staffName && !!formik.errors.staffName}
isValid ={!!formik.touched.staffName && !formik.errors.staffName}
></Form.Control>
{
    formik.errors.staffName && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.staffName}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Email</label>
<Form.Control type="text" name="Email" className="form-control" value={formik.values.Email}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Email && !!formik.errors.Email}
isValid ={!!formik.touched.Email && !formik.errors.Email}
></Form.Control>
{
    formik.errors.Email && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Email}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Phone</label>
<Form.Control type="text" name="Phone" className="form-control" value={formik.values.Phone}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Phone && !!formik.errors.Phone}
isValid ={!!formik.touched.Phone && !formik.errors.Phone}
></Form.Control>
{
    formik.errors.Phone && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Phone}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Gender</label>
<Form.Control type="text" name="Gender" className="form-control" value={formik.values.Gender}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Gender && !!formik.errors.Gender}
isValid ={!!formik.touched.Gender && !formik.errors.Gender}
></Form.Control>
{
    formik.errors.Gender && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Gender}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">RecordCreatedBy</label>
<Form.Control type="number" name="RecordCreatedBy" className="form-control" value={formik.values.RecordCreatedBy}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.RecordCreatedBy && !!formik.errors.RecordCreatedBy}
isValid ={!!formik.touched.RecordCreatedBy && !formik.errors.RecordCreatedBy}
></Form.Control>
{
    formik.errors.RecordCreatedBy && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.RecordCreatedBy}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">LoggedIP</label>
<Form.Control type="text" name="LoggedIP" className="form-control" value={formik.values.LoggedIP}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.LoggedIP && !!formik.errors.LoggedIP}
isValid ={!!formik.touched.LoggedIP && !formik.errors.LoggedIP}
></Form.Control>
{
    formik.errors.LoggedIP && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.LoggedIP}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">RecordCreatedAt</label>
<Form.Control type="date" name="RecordCreatedAt" className="form-control" value={formik.values.RecordCreatedAt}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.RecordCreatedAt && !!formik.errors.RecordCreatedAt}
isValid ={!!formik.touched.RecordCreatedAt && !formik.errors.RecordCreatedAt}
></Form.Control>
{
    formik.errors.RecordCreatedAt && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.RecordCreatedAt}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">staffRole</label>
<Form.Control as="select"  name="staffRole" className="form-control" value={formik.values.staffRole}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.staffRole && !!formik.errors.staffRole}
isValid ={!!formik.touched.staffRole && !formik.errors.staffRole}
>
<option value={0}>Select Aiimma_Roles </option> 
{
aiimma_rolesData.list.map((item, i) => {
return <option value={item.RoleCode} key={`aiimma_roles-${i}`}>{item.RoleName}</option>
})}
</Form.Control>
{
    formik.errors.staffRole && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.staffRole}
    </Form.Control.Feedback>
)}
</Form.Group>

                    <Form.Group>
                        <Button type="submit" className="float-right" variant="primary">Save</Button>
                    </Form.Group>
                </Form>
            </Card.Body>
        </Card>
    );
}

