import { APIService } from "services";

export const getAiimma_Tdos = async (pageNo: any,pageSize: any,search: string | any[]) => {
    let res;
    if(search.length===0) {
        res = await getAllAiimma_Tdos(pageNo,pageSize);
    }
    else{
        try {
            res = await searchAiimma_Tdos(search,pageNo,pageSize);
        } catch(err) {
             return { records:[], totalCount:0 }
        }
    }
    if(res && res.data){
    return res.data;
    }else{
    return { records:[], totalCount:0 }
    }
    
}


export const addAiimma_Tdos = (data: any) => {
return APIService.api().post(`/aiimma_tdos`,data)
}
export const updateAiimma_Tdos = (ID: any,data: any) => {
return APIService.api().patch(`/aiimma_tdos/${ID}`,data)
}
export const getAllAiimma_Tdos = (pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_tdos/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const getOneAiimma_Tdos = (ID: any) => {
return APIService.api().get(`/aiimma_tdos/${ID}`)
}
export const searchAiimma_Tdos = (searchKey: any,pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_tdos/search/${searchKey}/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const deleteAiimma_Tdos = (ID: any) => {
return APIService.api().delete(`/aiimma_tdos/${ID}`)
}
