import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface IAiimma_Agents {
ID:number,
UserID:string,
FullName:string,
Email?:string,
Phone?:string,
Whatsapp?:string,
OfficeAddress?:string,
Avatar?:string,
IDType?:string,
IDNumber?:string,
IDAvatar?:string,
LoggedIP:string,
RecordCreatedAt:Date,
RecordUpdatedAt?:Date
}

interface IAiimma_AgentsData {
    list?: Array<IAiimma_Agents>;
    pageNo: number;
    pageSize: number;
    searchKey?: string;
    totalCount?: number;
    message?: string;
}

const initialState: IAiimma_AgentsData = {
    pageNo: 1,
    pageSize: 20,
    searchKey: '',
    list: [],
    totalCount: 0,
    message: '',
};

const aiimma_agentsSlice = createSlice({
    name: "aiimma_agents",
    initialState,
    reducers: {
        setAiimma_AgentsList: (state, _action: PayloadAction<IAiimma_AgentsData>) => {
            state.list = _action.payload.list;
            state.pageNo = _action.payload.pageNo;
            state.pageSize = _action.payload.pageSize;
            state.totalCount = _action.payload.totalCount;
        },
        resetAiimma_AgentsToInit: (state) => {
            state = initialState;
        },
        setAiimma_AgentsMessage: (state, _action: PayloadAction<string>) => {
            state.message = _action.payload;
        },
    },
});

export const { setAiimma_AgentsList, resetAiimma_AgentsToInit, setAiimma_AgentsMessage } = aiimma_agentsSlice.actions;

export default aiimma_agentsSlice.reducer;

