import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface IAiimma_Trucks {
ID:number,
TruckName?:string,
TruckPlateNumber?:string,
TruckChasisNumber?:string,
TruckColor?:string,
TruckSize:string,
Truck_Avatar_Url?:string,
TruckMSSCheckStatus?:string,
TruckMSSApprovedCert_Avatar_URL?:string,
TruckMSSCertNumber?:string,
TruckMSSCertValidDuration?:string,
TruckOwnedBy?:number,
LoggedIP:string,
RecordCreatedAt:Date,
RecordUpdatedAt?:Date
}

interface IAiimma_TrucksData {
    list?: Array<IAiimma_Trucks>;
    pageNo: number;
    pageSize: number;
    searchKey?: string;
    totalCount?: number;
    message?: string;
}

const initialState: IAiimma_TrucksData = {
    pageNo: 1,
    pageSize: 20,
    searchKey: '',
    list: [],
    totalCount: 0,
    message: '',
};

const aiimma_trucksSlice = createSlice({
    name: "aiimma_trucks",
    initialState,
    reducers: {
        setAiimma_TrucksList: (state, _action: PayloadAction<IAiimma_TrucksData>) => {
            state.list = _action.payload.list;
            state.pageNo = _action.payload.pageNo;
            state.pageSize = _action.payload.pageSize;
            state.totalCount = _action.payload.totalCount;
        },
        resetAiimma_TrucksToInit: (state) => {
            state = initialState;
        },
        setAiimma_TrucksMessage: (state, _action: PayloadAction<string>) => {
            state.message = _action.payload;
        },
    },
});

export const { setAiimma_TrucksList, resetAiimma_TrucksToInit, setAiimma_TrucksMessage } = aiimma_trucksSlice.actions;

export default aiimma_trucksSlice.reducer;

