import { APIService } from "services";

export const getAiimma_Transporter_Driver_Junction = async (pageNo: any,pageSize: any,search: string | any[]) => {
    let res;
    if(search.length===0) {
        res = await getAllAiimma_Transporter_Driver_Junction(pageNo,pageSize);
    }
    else{
        try {
            res = await searchAiimma_Transporter_Driver_Junction(search,pageNo,pageSize);
        } catch(err) {
             return { records:[], totalCount:0 }
        }
    }
    if(res && res.data){
    return res.data;
    }else{
    return { records:[], totalCount:0 }
    }
    
}


export const addAiimma_Transporter_Driver_Junction = (data: any) => {
return APIService.api().post(`/aiimma_transporter_driver_junction`,data)
}
export const updateAiimma_Transporter_Driver_Junction = (ID: any,data: any) => {
return APIService.api().patch(`/aiimma_transporter_driver_junction/${ID}`,data)
}
export const getAllAiimma_Transporter_Driver_Junction = (pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_transporter_driver_junction/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const getOneAiimma_Transporter_Driver_Junction = (ID: any) => {
return APIService.api().get(`/aiimma_transporter_driver_junction/${ID}`)
}
export const searchAiimma_Transporter_Driver_Junction = (searchKey: any,pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_transporter_driver_junction/search/${searchKey}/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const deleteAiimma_Transporter_Driver_Junction = (ID: any) => {
return APIService.api().delete(`/aiimma_transporter_driver_junction/${ID}`)
}
