import { APIService } from "services";

export const getAiimma_Trucks = async (pageNo: number,pageSize: number,search: string | any[]) => {
    let res;
    if(search.length===0) {
        res = await getAllAiimma_Trucks(pageNo,pageSize);
    }
    else{
        try {
            res = await searchAiimma_Trucks(search,pageNo,pageSize);
        } catch(err) {
             return { records:[], totalCount:0 }
        }
    }
    if(res && res.data){
    return res.data;
    }else{
    return { records:[], totalCount:0 }
    }
    
}


export const addAiimma_Trucks = (data: any) => {
return APIService.api().post(`/aiimma_trucks`,data)
}
export const updateAiimma_Trucks = (ID: any,data: any) => {
return APIService.api().patch(`/aiimma_trucks/${ID}`,data)
}
export const getAllAiimma_Trucks = (pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_trucks/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const getOneAiimma_Trucks = (ID: any) => {
return APIService.api().get(`/aiimma_trucks/${ID}`)
}
export const searchAiimma_Trucks = (searchKey: any,pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_trucks/search/${searchKey}/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const deleteAiimma_Trucks = (ID: any) => {
return APIService.api().delete(`/aiimma_trucks/${ID}`)
}
