import { useFormik } from "formik";
import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { RootState } from "redux/reducers";

import { Button, Card, Form } from "react-bootstrap";
import { setAiimma_ContainerDeliveryCallCardMessage } from "redux/actions";
import {
  resetAiimma_TransportersToInit,
  setAiimma_TransportersList,
} from "redux/actions";
import { resetAiimma_RolesToInit, setAiimma_RolesList } from "redux/actions";

import { getAiimma_Transporters } from "services/aiimma_transportersService";
import { getAiimma_Roles } from "services/aiimma_rolesService";

import { useAppDispatch } from "redux/store";
import {
  addAiimma_ContainerDeliveryCallCard,
  updateAiimma_ContainerDeliveryCallCard,
} from "services/aiimma_ContainerDeliveryCallCardService";
import { Constant } from "template/Constant";
import * as yup from "yup";

let numberOfRows = 7;
const SUPPORTED_FORMATS = ["image/jpg", "image/jpeg", "image/gif", "image/png"];
const FILE_SIZE = 300;

type Props = {
  row?: any;
  hideShowForm: (actionName: any) => void;
  getData: (page: any, pageSize: any, searchKey: any) => void;
  action?: string;
};
export const Aiimma_ContainerDeliveryCallCardForm: React.FC<Props> = ({
  row,
  hideShowForm,
  getData,
  action,
}) => {
  const dispatch = useAppDispatch();
  const iValue = {
    ID: "",
    BookingCode: "",
    DeliveryComment: "",
    DeliveryStatus: "",
    LoggedIP: "",
    RecordCreatedAt: "",
    CallCardAvatar: "",
    size: FILE_SIZE,
    type: SUPPORTED_FORMATS,
  };

  const initialValue = action === "edit" ? row : iValue;
  //const aiimma_transportersData = useSelector((state: RootState) => state.aiimma_transporters);
  //const aiimma_rolesData = useSelector((state: RootState) => state.aiimma_roles);

  useEffect(() => {
    /* if (aiimma_transportersData && aiimma_transportersData.list && aiimma_transportersData.list.length === 0) {
            dispatch(resetAiimma_TransportersToInit());
            getAiimma_Transporters(Constant.defaultPageNumber, Constant.defaultDropdownPageSize, '').then((response) => {
                if (response && response.records) {
                  dispatch(setAiimma_TransportersList({ pageNo: Constant.defaultPageNumber, pageSize: Constant.defaultDropdownPageSize, list: response.records, totalCount: response.total_count, searchKey: '' }));
                } else {
                  dispatch(setAiimma_NotifyusersMessage("No Record Found For Aiimma_Transporters"));
                }
              })
        }
if (aiimma_rolesData && aiimma_rolesData.list && aiimma_rolesData.list.length === 0) {
            dispatch(resetAiimma_RolesToInit());
            getAiimma_Roles(Constant.defaultPageNumber, Constant.defaultDropdownPageSize, '').then((response) => {
                if (response && response.records) {
                  dispatch(setAiimma_RolesList({ pageNo: Constant.defaultPageNumber, pageSize: Constant.defaultDropdownPageSize, list: response.records, totalCount: response.total_count, searchKey: '' }));
                } else {
                  dispatch(setAiimma_NotifyusersMessage("No Record Found For Aiimma_Roles"));
                }
              })
        }
 */
  });
  const formik = useFormik({
    initialValues: initialValue,
    onSubmit: async (values) => {
      if (action === "edit") {
        const response = await updateAiimma_ContainerDeliveryCallCard(
          values.ID,
          values
        );
        if (response && (response.status === 200 || response.status === 201)) {
          dispatch(
            setAiimma_ContainerDeliveryCallCardMessage("Updated Successfully")
          );
          getData(Constant.defaultPageNumber, Constant.defaultPageSize, "");
          hideShowForm("");
        } else {
          dispatch(
            setAiimma_ContainerDeliveryCallCardMessage("Some error occured!")
          );
        }
      } else if (action === "add") {
        const response = await addAiimma_ContainerDeliveryCallCard(values);
        if (response && (response.status === 200 || response.status === 201)) {
          dispatch(
            setAiimma_ContainerDeliveryCallCardMessage(
              "Notification Message Sent Successfully"
            )
          );
          getData(Constant.defaultPageNumber, Constant.defaultPageSize, "");
          hideShowForm("");
        } else {
          dispatch(
            setAiimma_ContainerDeliveryCallCardMessage("Some error occured!")
          );
        }
      }
    },
    validationSchema: yup.object({
      DeliveryComment: yup
        .string()
        .required("Please type some comment for this delivery required"),
      DeliveryStatus: yup
        .string()
        .required("Choose the status of this container status"),
      CallCardAvatar: yup
        .mixed()
        .test(
          "fileSize",
          "File Size is too large. 300k maximum size",
          (value) => value === null || (value && iValue.size <= FILE_SIZE)
        )
        .test("fileType", "Unsupported File Format", (value) =>
          SUPPORTED_FORMATS.includes(value.type)
        ),
    }),
  });

  const [imgData, setImgdata] = useState(null);

  /* const onChangeFileHandler = (event) =>{  
        if (event.target.files[0]) {         
          setFile(event.target.files[0]);
          
          const reader = new FileReader();
          reader.addEventListener("load", () => {
            setImgdata(reader.result);
          });
          
          reader.readAsDataURL(event.target.files[0]);
        }         
        } */

  return (
    <Card className="shadow mb-4">
      <Card.Header className="py-3">
        <h6 className="m-0 font-weight-bold text-primary text-capitalize">
          {action} Container Delivery
          <Button
            className="btn-icon-split float-right"
            onClick={() => hideShowForm(false)}
          >
            <span className="icon text-white-50">
              <i className="fas fa-list"></i>
            </span>
            <span className="text">View Containers To Be Delivered </span>
          </Button>
        </h6>
      </Card.Header>
      <Card.Body>
        <Form onSubmit={formik.handleSubmit} encType="multipart/form-data">
          <Form.Group>
            <label className="form -control-label">Booking Code</label>
            <Form.Control
              type="text"
              name="BookingCode"
              className="form-control"
              value={formik.values.BookingCode}
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              isInvalid={
                !!formik.touched.BookingCode && !!formik.errors.BookingCode
              }
              isValid={
                !!formik.touched.BookingCode && !formik.errors.BookingCode
              }
            ></Form.Control>
            {formik.errors.BookingCode && (
              <Form.Control.Feedback type="invalid">
                {formik.errors.BookingCode}
              </Form.Control.Feedback>
            )}
          </Form.Group>

          <Form.Group>
            <label className="form -control-label">Delivery Status</label>
            <Form.Control
              as="select"
              name="deliverystatus"
              className="form-control"
              readOnly
              value={formik.values.deliverystatus}
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              isInvalid={
                !!formik.touched.deliverystatus &&
                !!formik.errors.deliverystatus
              }
              isValid={
                !!formik.touched.deliverystatus && !formik.errors.deliverystatus
              }
            >
              <option value="">---Select Delivery Status---</option>
              <option value="Delivered">Delivered</option>
              <option value="Delivered with condition attached to container">
                Delivered with condition attached to container
              </option>
              <option value="Delivered with condition attached to Truck">
                Delivered with condition attached to Truck
              </option>
              <option value="Delivered with condition attached to Driver">
                Delivered with condition attached to Driver
              </option>
            </Form.Control>
            {formik.errors.deliverystatus && (
              <Form.Control.Feedback type="invalid">
                {formik.errors.deliverystatus}
              </Form.Control.Feedback>
            )}
          </Form.Group>
          <Form.Group>
            <label className="form -control-label">Delivery Comment</label>
            <Form.Control
              as="textarea"
              rows={numberOfRows}
              name="DeliveryComment"
              className="form-control"
              value={formik.values.DeliveryComment}
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              isInvalid={
                !!formik.touched.DeliveryComment &&
                !!formik.errors.DeliveryComment
              }
              isValid={
                !!formik.touched.DeliveryComment &&
                !formik.errors.DeliveryComment
              }
            ></Form.Control>
            {formik.errors.DeliveryComment && (
              <Form.Control.Feedback type="invalid">
                {formik.errors.DeliveryComment}
              </Form.Control.Feedback>
            )}
          </Form.Group>

          <Form.Group>
            <label className="form -control-label">Upload CallCard</label>
            <Form.Control
              type="file"
              name="CallCardAvatar"
              className="form-control"
              value={formik.values.CallCardAvatar}
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              isInvalid={
                !!formik.touched.CallCardAvatar &&
                !!formik.errors.CallCardAvatar
              }
              isValid={
                !!formik.touched.CallCardAvatar && !formik.errors.CallCardAvatar
              }
            ></Form.Control>
            {formik.errors.CallCardAvatar && (
              <Form.Control.Feedback type="invalid">
                {formik.errors.CallCardAvatar}
              </Form.Control.Feedback>
            )}
          </Form.Group>

          <Form.Group>
            <img
              src={imgData}
              alt=""
              style={{ overflow: "hidden" }}
              width="100px"
              height="100px"
            />
            <Button type="submit" className="float-right" variant="primary">
              Update Container Delivery Status
            </Button>
          </Form.Group>
        </Form>
      </Card.Body>
    </Card>
  );
};
