import React, { useState } from "react";
import { Alert, Button } from "react-bootstrap";
import { useSelector } from "react-redux";
import { setAiimma_StaffsList, setAiimma_StaffsMessage } from "redux/actions";
import { RootState } from "redux/reducers";
import { useAppDispatch } from "redux/store";
import { getAiimma_Staffs } from "services/aiimma_staffsService";
import Layout from "template";
import { Constant } from "template/Constant";
import { Aiimma_StaffsForm } from "./form";
import { Aiimma_StaffsTable } from "./table";

export const Aiimma_Staffs: React.FC = () => {
  const dispatch = useAppDispatch();
  const rData = useSelector((state: RootState) => state.aiimma_staffs);
  const [row, setRow] = useState(undefined);
  const [action, setAction] = useState('');
  const getData = (page: any, pageSize: any, searchKey: any) => {
    getAiimma_Staffs(page, pageSize, searchKey).then((response) => {
      if (response && response.records) {
        dispatch(setAiimma_StaffsList({ pageNo: page, pageSize: pageSize, list: response.records, totalCount: response.totalCount, searchKey: searchKey }));
      } else {
        dispatch(setAiimma_StaffsMessage("No Record Found"));
      }
    })
  }

  const handleRowEdit = (rowData: any) => {
    setRow(rowData);
    dispatch(setAiimma_StaffsMessage(''));
    setAction('edit');
  }
  return (
    <Layout>
      <div className="container-fluid">
        <div className="d-sm-flex align-items-center justify-content-between mb-4">
          <h1 className="h3 mb-0 text-gray-800">Aiimma_Staffs</h1>
        </div>
        <div className="d-flex flex-column min-vh-100">
          {rData.message ?
            <Alert variant={Constant.defaultAlertVarient} className="alert-dismissible fade">{rData.message}
              <Button type="button" className="close" data-dismiss="alert" aria-label="Close" onClick={() => dispatch(setAiimma_StaffsMessage(''))}>
                <span aria-hidden="true">&times;</span>
              </Button>
            </Alert> : null}
          {action ? <Aiimma_StaffsForm hideShowForm={setAction} action={action} row={row} getData={getData} /> :
            <Aiimma_StaffsTable handleRowEdit={handleRowEdit} hideShowForm={setAction} getData={getData} />}
        </div>

      </div>
    </Layout >
  );
};

