import { APIService } from "services";

export const getAiimma_ContainerDeliveryCallCard = async (pageNo: any,pageSize: any,search: string | any[]) => {
    let res;
    if(search.length===0) {
        res = await getAllAiimma_ContainerDeliveryCallCard(pageNo,pageSize);
    }
    else{
        try {
            res = await searchAiimma_ContainerDeliveryCallCard(search,pageNo,pageSize);
        } catch(err) {
             return { records:[], totalCount:0 }
        }
    }
    if(res && res.data){
    return res.data;
    }else{
    return { records:[], totalCount:0 }
    }
    
}


export const addAiimma_ContainerDeliveryCallCard = (data: any) => {
return APIService.api().post(`/deliverycallcard`,data)
}
export const updateAiimma_ContainerDeliveryCallCard = (ID: any,data: any) => {
return APIService.api().patch(`/deliverycallcard/${ID}`,data)
}
export const getAllAiimma_ContainerDeliveryCallCard = (pageNo: any,pageSize: any) => {
return APIService.api().get(`/deliverycallcard/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const getOneAiimma_ContainerDeliveryCallCard = (ID: any) => {
return APIService.api().get(`/deliverycallcard/${ID}`)
}
export const searchAiimma_ContainerDeliveryCallCard = (searchKey: any,pageNo: any,pageSize: any) => {
return APIService.api().get(`/deliverycallcard/search/${searchKey}/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const deleteAiimma_ContainerDeliveryCallCard = (ID: any) => {
return APIService.api().delete(`/deliverycallcard/${ID}`)
}
