import { useFormik } from "formik";
import React, { useEffect } from "react";
import { useSelector } from "react-redux";
import { RootState } from "redux/reducers";

import { Button, Card, Form } from "react-bootstrap";
import { setAiimma_Booking_PaymentsMessage } from "redux/actions";
import { resetAiima_Transporter_Driver_Bthb_BookingToInit, setAiima_Transporter_Driver_Bthb_BookingList } from "redux/actions";

import { getAiima_Transporter_Driver_Bthb_Booking } from "services/aiima_transporter_driver_bthb_bookingService";

import { useAppDispatch } from "redux/store";
import { addAiimma_Booking_Payments, updateAiimma_Booking_Payments } from "services/aiimma_booking_paymentsService";
import { Constant } from "template/Constant";
import * as yup from 'yup';
type Props = {
    row?: any,
    hideShowForm: (actionName: any) => void;
    getData: (page: any, pageSize: any, searchKey: any) => void;
    action?: string
};
export const Aiimma_Booking_PaymentsForm: React.FC<Props> = ({ row, hideShowForm, getData, action }) => {
    const dispatch = useAppDispatch();
    const iValue={ID:'',booking_id:'',bookingDate:'',bookingAmount:'',LoggedIP:'',RecordCreatedAt:'',RecordUpdatedAt:''};
    const initialValue = action === 'edit' ? row : iValue;
    const aiima_transporter_driver_bthb_bookingData = useSelector((state: RootState) => state.aiima_transporter_driver_bthb_booking);

    useEffect(() => {
if (aiima_transporter_driver_bthb_bookingData && aiima_transporter_driver_bthb_bookingData.list && aiima_transporter_driver_bthb_bookingData.list.length === 0) {
            dispatch(resetAiima_Transporter_Driver_Bthb_BookingToInit());
            getAiima_Transporter_Driver_Bthb_Booking(Constant.defaultPageNumber, Constant.defaultDropdownPageSize, '').then((response) => {
                if (response && response.records) {
                  dispatch(setAiima_Transporter_Driver_Bthb_BookingList({ pageNo: Constant.defaultPageNumber, pageSize: Constant.defaultDropdownPageSize, list: response.records, totalCount: response.total_count, searchKey: '' }));
                } else {
                  dispatch(setAiimma_Booking_PaymentsMessage("No Record Found For Aiima_Transporter_Driver_Bthb_Booking"));
                }
              })
        }
})
    const formik = useFormik({
        initialValues: initialValue,
        onSubmit: async (values) => {
            if (action === 'edit') {
                const response = await updateAiimma_Booking_Payments(values.ID,values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_Booking_PaymentsMessage("Updated Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_Booking_PaymentsMessage("Some error occured!"));
                }
            } else if (action === 'add') {
                const response = await addAiimma_Booking_Payments(values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_Booking_PaymentsMessage("Added Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_Booking_PaymentsMessage("Some error occured!"));
                }
            }
        },
        validationSchema: yup.object({
           bookingDate: yup.date().required('bookingDate is required'),
bookingAmount: yup.number().required('bookingAmount is required'),
LoggedIP: yup.string().required('LoggedIP is required'),
RecordCreatedAt: yup.date().required('RecordCreatedAt is required'),
RecordUpdatedAt: yup.date(),
booking_id: yup.string().required('booking_id is required'),

        }),
    });
    return (
        <Card className="shadow mb-4">
            <Card.Header className="py-3">
                <h6 className="m-0 font-weight-bold text-primary text-capitalize">{action} Booking Payments
                    <Button className="btn-icon-split float-right" onClick={() => hideShowForm(false)}>
                        <span className="icon text-white-50">
                            <i className="fas fa-list"></i>
                        </span>
                        <span className="text">View Booking Payments</span>
                    </Button>
                </h6>

            </Card.Header>
            <Card.Body>
                <Form onSubmit={formik.handleSubmit}>
                  <Form.Group>
<label className="form -control-label">Booking Date</label>
<Form.Control type="date" name="bookingDate" className="form-control" value={formik.values.bookingDate}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.bookingDate && !!formik.errors.bookingDate}
isValid ={!!formik.touched.bookingDate && !formik.errors.bookingDate}
></Form.Control>
{
    formik.errors.bookingDate && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.bookingDate}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Booking Amount</label>
<Form.Control type="number" name="bookingAmount" className="form-control" value={formik.values.bookingAmount}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.bookingAmount && !!formik.errors.bookingAmount}
isValid ={!!formik.touched.bookingAmount && !formik.errors.bookingAmount}
></Form.Control>
{
    formik.errors.bookingAmount && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.bookingAmount}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="LoggedIP" className="form-control" value={formik.values.LoggedIP}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.LoggedIP && !!formik.errors.LoggedIP}
isValid ={!!formik.touched.LoggedIP && !formik.errors.LoggedIP}
></Form.Control>
{
    formik.errors.LoggedIP && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.LoggedIP}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="RecordCreatedAt" className="form-control" value={formik.values.RecordCreatedAt}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.RecordCreatedAt && !!formik.errors.RecordCreatedAt}
isValid ={!!formik.touched.RecordCreatedAt && !formik.errors.RecordCreatedAt}
></Form.Control>
{
    formik.errors.RecordCreatedAt && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.RecordCreatedAt}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="RecordUpdatedAt" className="form-control" value={formik.values.RecordUpdatedAt}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.RecordUpdatedAt && !!formik.errors.RecordUpdatedAt}
isValid ={!!formik.touched.RecordUpdatedAt && !formik.errors.RecordUpdatedAt}
></Form.Control>
{
    formik.errors.RecordUpdatedAt && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.RecordUpdatedAt}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">booking_id</label>
<Form.Control as="select"  name="booking_id" className="form-control" value={formik.values.booking_id}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.booking_id && !!formik.errors.booking_id}
isValid ={!!formik.touched.booking_id && !formik.errors.booking_id}
>
<option value={0}>Select Aiima_Transporter_Driver_Bthb_Booking </option> 
{
aiima_transporter_driver_bthb_bookingData.list.map((item, i) => {
return <option value={item.ID} key={`aiima_transporter_driver_bthb_booking-${i}`}>{item.LoggedIP}</option>
})}
</Form.Control>
{
    formik.errors.booking_id && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.booking_id}
    </Form.Control.Feedback>
)}
</Form.Group>

                    <Form.Group>
                        <Button type="submit" className="float-right" variant="primary">Save</Button>
                    </Form.Group>
                </Form>
            </Card.Body>
        </Card>
    );
}

