import React, { useState } from "react";
import { Alert, Button } from "react-bootstrap";
import { useSelector } from "react-redux";
import { setAiimma_NotifyusersList, setAiimma_NotifyusersMessage } from "redux/actions";
import { RootState } from "redux/reducers";
import { useAppDispatch } from "redux/store";
import { getAiimma_Notifyusers } from "services/aiimma_NotifyusersService";
import Layout from "template";
import { Constant } from "template/Constant";
import { Aiimma_NotifyusersForm } from "./form";
import { Aiimma_NotifyusersTable } from "./table";
import Dashboardheader from 'pages/Dashboardheader';
import DashboardMenu from 'pages/DashboardMenu';
import Adminfooter from 'pages/Adminfooter';


export const Aiimma_Notifyusers: React.FC = () => {
  const dispatch = useAppDispatch();
  const rData = useSelector((state: RootState) => state.aiimma_notifyusers);
  const [row, setRow] = useState(undefined);
  const [action, setAction] = useState('');
  const getData = (page: any, pageSize: any, searchKey: any) => {
    getAiimma_Notifyusers(page, pageSize, searchKey).then((response) => {
      if (response && response.records) {
        dispatch(setAiimma_NotifyusersList({ pageNo: page, pageSize: pageSize, list: response.records, totalCount: response.totalCount, searchKey: searchKey }));
      } else {
        dispatch(setAiimma_NotifyusersMessage("No Record Found"));
      }
    })
  }

  const handleRowEdit = (rowData: any) => {
    setRow(rowData);
    dispatch(setAiimma_NotifyusersMessage(''));
    setAction('edit');
  }
  return <>
  <div
    id="main-wrapper"
    data-theme="light"
    data-layout="vertical"
    data-navbarbg="skin6"
    data-sidebartype="full"
    data-sidebar-position="fixed"
    data-header-position="fixed"
    data-boxed-layout="full"
  >
   <Dashboardheader />
   <DashboardMenu /> 
   <div className="page-wrapper">
   <div className="container-fluid">
        <div className="d-sm-flex align-items-center justify-content-between mb-4">
          <h1 className="h3 mb-0 text-gray-800"></h1>
        </div>
        <div className="d-flex flex-column min-vh-100">
          {rData.message ?
            <Alert variant={Constant.defaultAlertVarient} className="alert-dismissible fade">{rData.message}
              <Button type="button" className="close" data-dismiss="alert" aria-label="Close" onClick={() => dispatch(setAiimma_NotifyusersMessage(''))}>
                <span aria-hidden="true">&times;</span>
              </Button>
            </Alert> : null}
          {action ? <Aiimma_NotifyusersForm hideShowForm={setAction} action={action} row={row} getData={getData} /> :
            <Aiimma_NotifyusersTable handleRowEdit={handleRowEdit} hideShowForm={setAction} getData={getData} />}
        </div>
        </div>
</div>
        <Adminfooter />

</div>
        <div>
         </div>
     </>
};

