import { useFormik } from "formik";
import React from "react";

import { Button, Card, Form } from "react-bootstrap";
import { setAiimma_AgentsMessage } from "redux/actions";


import { useAppDispatch } from "redux/store";
import { addAiimma_Agents, updateAiimma_Agents } from "services/aiimma_agentsService";
import { Constant } from "template/Constant";
import * as yup from 'yup';

type Props = {
    row?: any,
    hideShowForm: (actionName: any) => void;
    getData: (page: any, pageSize: any, searchKey: any) => void;
    action?: string
};

export const Aiimma_AgentsForm: React.FC<Props> = ({ row, hideShowForm, getData, action }) => {
    const dispatch = useAppDispatch();
    const iValue={ID:'',UserID:'',FullName:'',Email:'',Phone:'',Whatsapp:'',OfficeAddress:'',Avatar:'',IDType:'',IDNumber:'',IDAvatar:'',LoggedIP:'',RecordCreatedAt:'',RecordUpdatedAt:''};
    const initialValue = action === 'edit' ? row : iValue;
    
    
    const formik = useFormik({
        initialValues: initialValue,
        onSubmit: async (values) => {
            if (action === 'edit') {
                const response = await updateAiimma_Agents(values.ID,values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_AgentsMessage("Updated Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_AgentsMessage("Some error occured!"));
                }
            } else if (action === 'add') {
                const response = await addAiimma_Agents(values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_AgentsMessage("Added Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_AgentsMessage("Some error occured!"));
                }
            }
        },
        validationSchema: yup.object({
           UserID: yup.string().required('UserID is required'),
FullName: yup.string().required('FullName is required'),
Email: yup.string(),
Phone: yup.string(),
Whatsapp: yup.string(),
OfficeAddress: yup.string(),
Avatar: yup.string(),
IDType: yup.string(),
IDNumber: yup.string(),
IDAvatar: yup.string(),
LoggedIP: yup.string(),
RecordCreatedAt: yup.date(),
RecordUpdatedAt: yup.date(),

        }),
    });
    return (
        <Card className="shadow mb-4">
            <Card.Header className="py-3">
                <h6 className="m-0 font-weight-bold text-primary text-capitalize">{action} Clearing Agents
                    <Button className="btn-icon-split float-right" onClick={() => hideShowForm(false)}>
                        <span className="icon text-white-50">
                            <i className="fas fa-list"></i>
                        </span>
                        <span className="text">View Clearing Agents</span>
                    </Button>
                </h6>

            </Card.Header>
            <Card.Body>
                <Form onSubmit={formik.handleSubmit}>
                  <Form.Group>
<label className="form -control-label">UserID</label>
<Form.Control type="text" name="UserID" className="form-control" value={formik.values.UserID}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.UserID && !!formik.errors.UserID}
isValid ={!!formik.touched.UserID && !formik.errors.UserID}
></Form.Control>
{
    formik.errors.UserID && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.UserID}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">FullName</label>
<Form.Control type="text" name="FullName" className="form-control" value={formik.values.FullName}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.FullName && !!formik.errors.FullName}
isValid ={!!formik.touched.FullName && !formik.errors.FullName}
></Form.Control>
{
    formik.errors.FullName && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.FullName}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Email</label>
<Form.Control type="text" name="Email" className="form-control" value={formik.values.Email}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Email && !!formik.errors.Email}
isValid ={!!formik.touched.Email && !formik.errors.Email}
></Form.Control>
{
    formik.errors.Email && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Email}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Phone</label>
<Form.Control type="text" name="Phone" className="form-control" value={formik.values.Phone}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Phone && !!formik.errors.Phone}
isValid ={!!formik.touched.Phone && !formik.errors.Phone}
></Form.Control>
{
    formik.errors.Phone && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Phone}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Whatsapp</label>
<Form.Control type="text" name="Whatsapp" className="form-control" value={formik.values.Whatsapp}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Whatsapp && !!formik.errors.Whatsapp}
isValid ={!!formik.touched.Whatsapp && !formik.errors.Whatsapp}
></Form.Control>
{
    formik.errors.Whatsapp && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Whatsapp}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Office Address</label>
<Form.Control type="text" name="OfficeAddress" className="form-control" value={formik.values.OfficeAddress}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.OfficeAddress && !!formik.errors.OfficeAddress}
isValid ={!!formik.touched.OfficeAddress && !formik.errors.OfficeAddress}
></Form.Control>
{
    formik.errors.OfficeAddress && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.OfficeAddress}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Agent Avatar</label>
<Form.Control type="text" name="Avatar" className="form-control" value={formik.values.Avatar}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Avatar && !!formik.errors.Avatar}
isValid ={!!formik.touched.Avatar && !formik.errors.Avatar}
></Form.Control>
{
    formik.errors.Avatar && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Avatar}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">IDType</label>
<Form.Control type="text" name="IDType" className="form-control" value={formik.values.IDType}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.IDType && !!formik.errors.IDType}
isValid ={!!formik.touched.IDType && !formik.errors.IDType}
></Form.Control>
{
    formik.errors.IDType && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.IDType}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Driver's License ID Number</label>
<Form.Control type="text" name="IDNumber" className="form-control" value={formik.values.IDNumber}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.IDNumber && !!formik.errors.IDNumber}
isValid ={!!formik.touched.IDNumber && !formik.errors.IDNumber}
></Form.Control>
{
    formik.errors.IDNumber && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.IDNumber}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Driver's License ID Avatar</label>
<Form.Control type="text" name="IDAvatar" className="form-control" value={formik.values.IDAvatar}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.IDAvatar && !!formik.errors.IDAvatar}
isValid ={!!formik.touched.IDAvatar && !formik.errors.IDAvatar}
></Form.Control>
{
    formik.errors.IDAvatar && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.IDAvatar}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="LoggedIP" className="form-control" value={formik.values.LoggedIP}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.LoggedIP && !!formik.errors.LoggedIP}
isValid ={!!formik.touched.LoggedIP && !formik.errors.LoggedIP}
></Form.Control>
{
    formik.errors.LoggedIP && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.LoggedIP}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="RecordCreatedAt" className="form-control" value={formik.values.RecordCreatedAt}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.RecordCreatedAt && !!formik.errors.RecordCreatedAt}
isValid ={!!formik.touched.RecordCreatedAt && !formik.errors.RecordCreatedAt}
></Form.Control>
{
    formik.errors.RecordCreatedAt && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.RecordCreatedAt}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="RecordUpdatedAt" className="form-control" value={formik.values.RecordUpdatedAt}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.RecordUpdatedAt && !!formik.errors.RecordUpdatedAt}
isValid ={!!formik.touched.RecordUpdatedAt && !formik.errors.RecordUpdatedAt}
></Form.Control>
{
    formik.errors.RecordUpdatedAt && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.RecordUpdatedAt}
    </Form.Control.Feedback>
)}
</Form.Group>

                    <Form.Group>
                        <Button type="submit" className="float-right" variant="primary">Save</Button>
                    </Form.Group>
                </Form>
            </Card.Body>
        </Card>
    );
}

