import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface IAiimma_Bt_Hb_Customers {
ID:number,
CustomerID:string,
CompanyName:string,
CompanyRegCertNo?:string,
CustomerType?:string,
Address?:string,
Phone?:string,
WhatsApp_Phone?:string,
Email?:string,
Website?:string,
NPA_Approved:string,
Customer_Logo_Avatar?:string,
Company_Cert_Avatar?:string,
LoggedIP:string,
RecordCreatedAt:Date,
RecordUpdatedAt?:Date
}

interface IAiimma_Bt_Hb_CustomersData {
    list?: Array<IAiimma_Bt_Hb_Customers>;
    pageNo: number;
    pageSize: number;
    searchKey?: string;
    totalCount?: number;
    message?: string;
}

const initialState: IAiimma_Bt_Hb_CustomersData = {
    pageNo: 1,
    pageSize: 20,
    searchKey: '',
    list: [],
    totalCount: 0,
    message: '',
};

const aiimma_bt_hb_customersSlice = createSlice({
    name: "aiimma_bt_hb_customers",
    initialState,
    reducers: {
        setAiimma_Bt_Hb_CustomersList: (state, _action: PayloadAction<IAiimma_Bt_Hb_CustomersData>) => {
            state.list = _action.payload.list;
            state.pageNo = _action.payload.pageNo;
            state.pageSize = _action.payload.pageSize;
            state.totalCount = _action.payload.totalCount;
        },
        resetAiimma_Bt_Hb_CustomersToInit: (state) => {
            state = initialState;
        },
        setAiimma_Bt_Hb_CustomersMessage: (state, _action: PayloadAction<string>) => {
            state.message = _action.payload;
        },
    },
});

export const { setAiimma_Bt_Hb_CustomersList, resetAiimma_Bt_Hb_CustomersToInit, setAiimma_Bt_Hb_CustomersMessage } = aiimma_bt_hb_customersSlice.actions;

export default aiimma_bt_hb_customersSlice.reducer;

