import React from "react";
import { BrowserRouter, Routes, Route } from 'react-router-dom';
//import loginn from "./login";
//import Register from "./register";
import Loginn from "./Loginn";
import Registerr from "./Registerr";
import ForgotPassword from "./ForgotPassword";

import AuthenticatedRoute from "components/auth";
import { Dashboard } from "components/dashboard";
import { FileUpload } from "components/upload";
import { NotFound } from "./404";
import { Aiimma_Trucks, Aiimma_Truck_Maintenance_Journey_Booking_Details, Aiimma_Transporters, Aiimma_Transporter_Truck_Junction, Aiimma_Transporter_Driver_Junction, Aiimma_Tdos, Aiimma_Staffs, Aiimma_Roles, Aiimma_Membersaccess, Aiimma_Holdingbay_Containers_Upload, Aiimma_Drivers, Aiimma_Customers_Deposit_Balance, Aiimma_Bt_Hb_Customers, Aiimma_Booking_Payments, Aiimma_Agents, Aiimma__Users_Access_Auditing, Aiima_Transporter_Driver_Bthb_Booking, Aiimma_Bookingvw} from "components";
import Termsofuse from "./Termsofuse";
import Privacypolicy from "./Privacypolicy";
import Dashboardd from "components/dashboard/Dashboardd";
import { Aiimma_ContainerDeliveryCallCard } from "components/aiimma_uploadcallcard";
import { Aiimma_Notifyusers } from "components/aiimma_notifyusers";

const Pages: React.FC = () => {
  //const basename = process.env.REACT_APP_BASENAME || null;

  return (
    <BrowserRouter>
      <Routes>
        <Route path="/" element={<Loginn />}>
        </Route>
        <Route path="/login" element={<Loginn />}>
        </Route>
        <Route path="/register" element={<Registerr />}>
        </Route>
        <Route path="/termsandcondition" element={<Termsofuse />}>
        </Route>
        <Route path="/privacypolicy" element={<Privacypolicy />}>
        </Route>
        <Route path="/forgotpassword" element={<ForgotPassword />}>
        </Route>
        {<Route path="/dashboardd" element={<Dashboardd />}>
        </Route>}
        <Route path="/dashboard" element={<AuthenticatedRoute element={<Dashboard />} />}></Route>
        <Route path="/upload" element={<AuthenticatedRoute element={<FileUpload />} />}></Route>
        <Route path="/aiimma_trucks" element={ <Aiimma_Trucks />} ></Route>
       {/*  <Route path="/aiimma_trucks" element={<AuthenticatedRoute element={<Aiimma_Trucks />} />}></Route> */}
<Route path="/aiimma_truck_maintenance_journey_booking_details" 
element={<AuthenticatedRoute element={<Aiimma_Truck_Maintenance_Journey_Booking_Details />} />}></Route>
<Route path="/aiimma_transporters" element={<AuthenticatedRoute element={<Aiimma_Transporters />} />}></Route>
<Route path="/aiimma_transporter_truck_junction" element={<AuthenticatedRoute element={<Aiimma_Transporter_Truck_Junction />} />}></Route>
<Route path="/aiimma_transporter_driver_junction" element={<AuthenticatedRoute element={<Aiimma_Transporter_Driver_Junction />} />}></Route>
<Route path="/aiimma_tdos" element={<AuthenticatedRoute element={<Aiimma_Tdos />} />}></Route>
<Route path="/aiimma_staffs" element={<AuthenticatedRoute element={<Aiimma_Staffs />} />}></Route>
<Route path="/aiimma_roles" element={<AuthenticatedRoute element={<Aiimma_Roles />} />}></Route>
<Route path="/aiimma_membersaccess" element={<AuthenticatedRoute element={<Aiimma_Membersaccess />} />}></Route>
<Route path="/aiimma_holdingbay_containers_upload" element={<AuthenticatedRoute element={<Aiimma_Holdingbay_Containers_Upload />} />}></Route>
<Route path="/aiimma_drivers" element={<AuthenticatedRoute element={<Aiimma_Drivers />} />}></Route>
<Route path="/aiimma_customers_deposit_balance" element={<AuthenticatedRoute element={<Aiimma_Customers_Deposit_Balance />} />}></Route>
<Route path="/aiimma_bt_hb_customers" element={<AuthenticatedRoute element={<Aiimma_Bt_Hb_Customers />} />}></Route>
<Route path="/aiimma_booking_payments" element={<AuthenticatedRoute element={<Aiimma_Booking_Payments />} />}></Route>
<Route path="/aiimma_agents" element={<AuthenticatedRoute element={<Aiimma_Agents />} />}></Route>
<Route path="/containerdeliverybooking" element={<AuthenticatedRoute element={<Aiimma_Bookingvw />} />}></Route>
<Route path="/aiimma__users_access_auditing" element={<AuthenticatedRoute element={<Aiimma__Users_Access_Auditing />} />}></Route>
<Route path="/aiima_transporter_driver_bthb_booking" element={<AuthenticatedRoute element={<Aiima_Transporter_Driver_Bthb_Booking />} />}></Route>
<Route path="/aiima_ContainerDeliveryCallCard" element={<AuthenticatedRoute element={<Aiimma_ContainerDeliveryCallCard />} />}></Route>
<Route path="/aiima_Notifyusers" element={<AuthenticatedRoute element={<Aiimma_Notifyusers />} />}></Route>


        <Route path="*" element={<NotFound />} />
      </Routes>
    </BrowserRouter>
  );
};

export default Pages;

