import { useFormik } from "formik";
import React, { useEffect } from "react";
import { useSelector } from "react-redux";
import { RootState } from "redux/reducers";

import { Button, Card, Form } from "react-bootstrap";
import { setAiimma_Truck_Maintenance_Journey_Booking_DetailsMessage } from "redux/actions";
import { resetAiima_Transporter_Driver_Bthb_BookingToInit, setAiima_Transporter_Driver_Bthb_BookingList } from "redux/actions";

import { getAiima_Transporter_Driver_Bthb_Booking } from "services/aiima_transporter_driver_bthb_bookingService";

import { useAppDispatch } from "redux/store";
import { addAiimma_Truck_Maintenance_Journey_Booking_Details, updateAiimma_Truck_Maintenance_Journey_Booking_Details } from "services/aiimma_truck_maintenance_journey_booking_detailsService";
import { Constant } from "template/Constant";
import * as yup from 'yup';
type Props = {
    row?: any,
    hideShowForm: (actionName: any) => void;
    getData: (page: any, pageSize: any, searchKey: any) => void;
    action?: string
};
export const Aiimma_Truck_Maintenance_Journey_Booking_DetailsForm: React.FC<Props> = ({ row, hideShowForm, getData, action }) => {
    const dispatch = useAppDispatch();
    const iValue={id:'',bookingid:'',maintenanceDescription:'',RecordAddedBy:'',LoggedIP:'',RecordCreatedAt:''};
    const initialValue = action === 'edit' ? row : iValue;
    const aiima_transporter_driver_bthb_bookingData = useSelector((state: RootState) => state.aiima_transporter_driver_bthb_booking);

    useEffect(() => {
if (aiima_transporter_driver_bthb_bookingData && aiima_transporter_driver_bthb_bookingData.list && aiima_transporter_driver_bthb_bookingData.list.length === 0) {
            dispatch(resetAiima_Transporter_Driver_Bthb_BookingToInit());
            getAiima_Transporter_Driver_Bthb_Booking(Constant.defaultPageNumber, Constant.defaultDropdownPageSize, '').then((response) => {
                if (response && response.records) {
                  dispatch(setAiima_Transporter_Driver_Bthb_BookingList({ pageNo: Constant.defaultPageNumber, pageSize: Constant.defaultDropdownPageSize, list: response.records, totalCount: response.total_count, searchKey: '' }));
                } else {
                  dispatch(setAiimma_Truck_Maintenance_Journey_Booking_DetailsMessage("No Record Found For Aiima_Transporter_Driver_Bthb_Booking"));
                }
              })
        }
})
    const formik = useFormik({
        initialValues: initialValue,
        onSubmit: async (values) => {
            if (action === 'edit') {
                const response = await updateAiimma_Truck_Maintenance_Journey_Booking_Details(values.id,values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_Truck_Maintenance_Journey_Booking_DetailsMessage("Updated Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_Truck_Maintenance_Journey_Booking_DetailsMessage("Some error occured!"));
                }
            } else if (action === 'add') {
                const response = await addAiimma_Truck_Maintenance_Journey_Booking_Details(values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_Truck_Maintenance_Journey_Booking_DetailsMessage("Added Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_Truck_Maintenance_Journey_Booking_DetailsMessage("Some error occured!"));
                }
            }
        },
        validationSchema: yup.object({
           maintenanceDescription: yup.string().required('maintenanceDescription is required'),
RecordAddedBy: yup.number().required('RecordAddedBy is required'),
LoggedIP: yup.string().required('LoggedIP is required'),
RecordCreatedAt: yup.date().required('RecordCreatedAt is required'),
bookingid: yup.string().required('bookingid is required'),

        }),
    });
    return (
        <Card className="shadow mb-4">
            <Card.Header className="py-3">
                <h6 className="m-0 font-weight-bold text-primary text-capitalize">{action} Aiimma_Truck_Maintenance_Journey_Booking_Details
                    <Button className="btn-icon-split float-right" onClick={() => hideShowForm(false)}>
                        <span className="icon text-white-50">
                            <i className="fas fa-list"></i>
                        </span>
                        <span className="text">View Aiimma_Truck_Maintenance_Journey_Booking_Details</span>
                    </Button>
                </h6>

            </Card.Header>
            <Card.Body>
                <Form onSubmit={formik.handleSubmit}>
                  <Form.Group>
<label className="form -control-label">maintenanceDescription</label>
<Form.Control type="text" name="maintenanceDescription" className="form-control" value={formik.values.maintenanceDescription}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.maintenanceDescription && !!formik.errors.maintenanceDescription}
isValid ={!!formik.touched.maintenanceDescription && !formik.errors.maintenanceDescription}
></Form.Control>
{
    formik.errors.maintenanceDescription && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.maintenanceDescription}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">RecordAddedBy</label>
<Form.Control type="number" name="RecordAddedBy" className="form-control" value={formik.values.RecordAddedBy}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.RecordAddedBy && !!formik.errors.RecordAddedBy}
isValid ={!!formik.touched.RecordAddedBy && !formik.errors.RecordAddedBy}
></Form.Control>
{
    formik.errors.RecordAddedBy && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.RecordAddedBy}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">LoggedIP</label>
<Form.Control type="text" name="LoggedIP" className="form-control" value={formik.values.LoggedIP}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.LoggedIP && !!formik.errors.LoggedIP}
isValid ={!!formik.touched.LoggedIP && !formik.errors.LoggedIP}
></Form.Control>
{
    formik.errors.LoggedIP && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.LoggedIP}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">RecordCreatedAt</label>
<Form.Control type="date" name="RecordCreatedAt" className="form-control" value={formik.values.RecordCreatedAt}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.RecordCreatedAt && !!formik.errors.RecordCreatedAt}
isValid ={!!formik.touched.RecordCreatedAt && !formik.errors.RecordCreatedAt}
></Form.Control>
{
    formik.errors.RecordCreatedAt && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.RecordCreatedAt}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">bookingid</label>
<Form.Control as="select"  name="bookingid" className="form-control" value={formik.values.bookingid}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.bookingid && !!formik.errors.bookingid}
isValid ={!!formik.touched.bookingid && !formik.errors.bookingid}
>
<option value={0}>Select Aiima_Transporter_Driver_Bthb_Booking </option> 
{
aiima_transporter_driver_bthb_bookingData.list.map((item, i) => {
return <option value={item.ID} key={`aiima_transporter_driver_bthb_booking-${i}`}>{item.LoggedIP}</option>
})}
</Form.Control>
{
    formik.errors.bookingid && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.bookingid}
    </Form.Control.Feedback>
)}
</Form.Group>

                    <Form.Group>
                        <Button type="submit" className="float-right" variant="primary">Save</Button>
                    </Form.Group>
                </Form>
            </Card.Body>
        </Card>
    );
}

