import { useFormik } from "formik";
import React, { useEffect } from "react";
import { useSelector } from "react-redux";
import { RootState } from "redux/reducers";

import { Button, Card, Form } from "react-bootstrap";
import { setAiimma__Users_Access_AuditingMessage } from "redux/actions";
import { resetAiimma__Users_Access_AuditingToInit, setAiimma__Users_Access_AuditingList } from "redux/actions";

import { getAiimma__Users_Access_Auditing } from "services/aiimma__users_access_auditingService";

import { useAppDispatch } from "redux/store";
import { addAiimma__Users_Access_Auditing, updateAiimma__Users_Access_Auditing } from "services/aiimma__users_access_auditingService";
import { Constant } from "template/Constant";
import * as yup from 'yup';
type Props = {
    row?: any,
    hideShowForm: (actionName: any) => void;
    getData: (page: any, pageSize: any, searchKey: any) => void;
    action?: string
};
export const Aiimma__Users_Access_AuditingForm: React.FC<Props> = ({ row, hideShowForm, getData, action }) => {
    const dispatch = useAppDispatch();
    const iValue={ID:'',LoginID:'',UserRole:'',Logged_In_At:'',Logged_Out_At:'',Logged_In_Ip:'',Logged_Out_Ip:'',LoginTrackingToken:'',LogoutTrackingToken:'',RecordCreatedAt:''};
    const initialValue = action === 'edit' ? row : iValue;
    const aiimma__users_access_auditingData = useSelector((state: RootState) => state.aiimma__users_access_auditing);

    useEffect(() => {
if (aiimma__users_access_auditingData && aiimma__users_access_auditingData.list && aiimma__users_access_auditingData.list.length === 0) {
            dispatch(resetAiimma__Users_Access_AuditingToInit());
            getAiimma__Users_Access_Auditing(Constant.defaultPageNumber, Constant.defaultDropdownPageSize, '').then((response) => {
                if (response && response.records) {
                  dispatch(setAiimma__Users_Access_AuditingList({ pageNo: Constant.defaultPageNumber, pageSize: Constant.defaultDropdownPageSize, list: response.records, totalCount: response.total_count, searchKey: '' }));
                } else {
                  dispatch(setAiimma__Users_Access_AuditingMessage("No Record Found For Aiimma__Users_Access_Auditing"));
                }
              })
        }
})
    const formik = useFormik({
        initialValues: initialValue,
        onSubmit: async (values) => {
            if (action === 'edit') {
                const response = await updateAiimma__Users_Access_Auditing(values.ID,values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma__Users_Access_AuditingMessage("Updated Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma__Users_Access_AuditingMessage("Some error occured!"));
                }
            } else if (action === 'add') {
                const response = await addAiimma__Users_Access_Auditing(values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma__Users_Access_AuditingMessage("Added Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma__Users_Access_AuditingMessage("Some error occured!"));
                }
            }
        },
        validationSchema: yup.object({
           UserRole: yup.string().required('UserRole is required'),
Logged_In_At: yup.date(),
Logged_Out_At: yup.date(),
Logged_In_Ip: yup.string(),
Logged_Out_Ip: yup.string(),
LoginTrackingToken: yup.string(),
LogoutTrackingToken: yup.string(),
RecordCreatedAt: yup.date().required('RecordCreatedAt is required'),
LoginID: yup.string().required('LoginID is required'),

        }),
    });
    return (
        <Card className="shadow mb-4">
            <Card.Header className="py-3">
                <h6 className="m-0 font-weight-bold text-primary text-capitalize">{action} Aiimma__Users_Access_Auditing
                    <Button className="btn-icon-split float-right" onClick={() => hideShowForm(false)}>
                        <span className="icon text-white-50">
                            <i className="fas fa-list"></i>
                        </span>
                        <span className="text">View Aiimma__Users_Access_Auditing</span>
                    </Button>
                </h6>

            </Card.Header>
            <Card.Body>
                <Form onSubmit={formik.handleSubmit}>
                  <Form.Group>
<label className="form -control-label">UserRole</label>
<Form.Control type="text" name="UserRole" className="form-control" value={formik.values.UserRole}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.UserRole && !!formik.errors.UserRole}
isValid ={!!formik.touched.UserRole && !formik.errors.UserRole}
></Form.Control>
{
    formik.errors.UserRole && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.UserRole}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Logged_In_At</label>
<Form.Control type="date" name="Logged_In_At" className="form-control" value={formik.values.Logged_In_At}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Logged_In_At && !!formik.errors.Logged_In_At}
isValid ={!!formik.touched.Logged_In_At && !formik.errors.Logged_In_At}
></Form.Control>
{
    formik.errors.Logged_In_At && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Logged_In_At}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Logged_Out_At</label>
<Form.Control type="date" name="Logged_Out_At" className="form-control" value={formik.values.Logged_Out_At}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Logged_Out_At && !!formik.errors.Logged_Out_At}
isValid ={!!formik.touched.Logged_Out_At && !formik.errors.Logged_Out_At}
></Form.Control>
{
    formik.errors.Logged_Out_At && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Logged_Out_At}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Logged_In_Ip</label>
<Form.Control type="text" name="Logged_In_Ip" className="form-control" value={formik.values.Logged_In_Ip}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Logged_In_Ip && !!formik.errors.Logged_In_Ip}
isValid ={!!formik.touched.Logged_In_Ip && !formik.errors.Logged_In_Ip}
></Form.Control>
{
    formik.errors.Logged_In_Ip && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Logged_In_Ip}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Logged_Out_Ip</label>
<Form.Control type="text" name="Logged_Out_Ip" className="form-control" value={formik.values.Logged_Out_Ip}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Logged_Out_Ip && !!formik.errors.Logged_Out_Ip}
isValid ={!!formik.touched.Logged_Out_Ip && !formik.errors.Logged_Out_Ip}
></Form.Control>
{
    formik.errors.Logged_Out_Ip && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Logged_Out_Ip}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">LoginTrackingToken</label>
<Form.Control type="text" name="LoginTrackingToken" className="form-control" value={formik.values.LoginTrackingToken}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.LoginTrackingToken && !!formik.errors.LoginTrackingToken}
isValid ={!!formik.touched.LoginTrackingToken && !formik.errors.LoginTrackingToken}
></Form.Control>
{
    formik.errors.LoginTrackingToken && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.LoginTrackingToken}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">LogoutTrackingToken</label>
<Form.Control type="text" name="LogoutTrackingToken" className="form-control" value={formik.values.LogoutTrackingToken}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.LogoutTrackingToken && !!formik.errors.LogoutTrackingToken}
isValid ={!!formik.touched.LogoutTrackingToken && !formik.errors.LogoutTrackingToken}
></Form.Control>
{
    formik.errors.LogoutTrackingToken && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.LogoutTrackingToken}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">RecordCreatedAt</label>
<Form.Control type="date" name="RecordCreatedAt" className="form-control" value={formik.values.RecordCreatedAt}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.RecordCreatedAt && !!formik.errors.RecordCreatedAt}
isValid ={!!formik.touched.RecordCreatedAt && !formik.errors.RecordCreatedAt}
></Form.Control>
{
    formik.errors.RecordCreatedAt && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.RecordCreatedAt}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">LoginID</label>
<Form.Control as="select"  name="LoginID" className="form-control" value={formik.values.LoginID}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.LoginID && !!formik.errors.LoginID}
isValid ={!!formik.touched.LoginID && !formik.errors.LoginID}
>
<option value={0}>Select Aiimma__Users_Access_Auditing </option> 
{
aiimma__users_access_auditingData.list.map((item, i) => {
return <option value={item.ID} key={`aiimma__users_access_auditing-${i}`}>{item.Logged_In_Ip}</option>
})}
</Form.Control>
{
    formik.errors.LoginID && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.LoginID}
    </Form.Control.Feedback>
)}
</Form.Group>

                    <Form.Group>
                        <Button type="submit" className="float-right" variant="primary">Save</Button>
                    </Form.Group>
                </Form>
            </Card.Body>
        </Card>
    );
}

