import axios from "axios";
export class APIService {

    static apiCustomHeader(headers: any) {
        return axios.create({
            //baseURL: process.env.REACT_APP_API_BASE_URL_LOCAL,            
            baseURL: "https://52.87.25.92:8184/api/v1",           
            headers: headers,
        })
    }
    
    static api() {
        const jwtToken = localStorage.getItem("token");
        
        //Use axios to get the base api endpoint and pass every request header with the token
        const api = axios.create({
            baseURL: "https://52.87.25.92:8184/api/v1",
            headers: {
                'Access-Control-Allow-Origin': '*',
                'Content-type': 'application/json',
                'Authorization': 'Bearer ' + jwtToken
            }
        });

          /*we have the api endpoint base, use axios interceptors to intercept every request*/
          api.interceptors.response.use(
            (res) => {
                //return the response to the request if request was valid
                return res;
            },
            (err) => {
                if (err.response) {
                    //if response was not valid or found, return back to login page
                    if (err.response.status === 401 || err.response.status === 403) {
                        return window.location.href = '/'
                    } else {

                        //if response was found return response data
                        return err.response.data;
                    }
                }
            }
        );
        return api;
    }

    //this works for an api without authorization
    static apiNoAuth() {
        return axios.create({
            baseURL: "https://52.87.25.92:8184/api/v1",
            headers: {
                'Access-Control-Allow-Origin': '*',
                'Content-Type': 'application/json',
            }
        })
    }
}
