import { useFormik } from "formik";
import React from "react";

import { Button, Card, Form } from "react-bootstrap";
import { setAiimma_TransportersMessage } from "redux/actions";


import { useAppDispatch } from "redux/store";
import { addAiimma_Transporters, updateAiimma_Transporters } from "services/aiimma_transportersService";
import { Constant } from "template/Constant";
import * as yup from 'yup';
type Props = {
    row?: any,
    hideShowForm: (actionName: any) => void;
    getData: (page: any, pageSize: any, searchKey: any) => void;
    action?: string
};
export const Aiimma_TransportersForm: React.FC<Props> = ({ row, hideShowForm, getData, action }) => {
    const dispatch = useAppDispatch();
    const iValue={ID:'',UserID:'',FullName:'',Gender:'',Phone:'',Whatsapp:'',Email:'',Website:'',Address:'',StateOfOrigin:'',Nationality:'',Transporter_Avatar_Url:'',Transporter_Type:'',TransporterAvatar:'',LoggedIP:'',RecordCreatedAt:'',RecordUpdatedAt:''};
    const initialValue = action === 'edit' ? row : iValue;
    
    
    const formik = useFormik({
        initialValues: initialValue,
        onSubmit: async (values) => {
            if (action === 'edit') {
                const response = await updateAiimma_Transporters(values.ID,values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_TransportersMessage("Updated Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_TransportersMessage("Some error occured!"));
                }
            } else if (action === 'add') {
                const response = await addAiimma_Transporters(values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_TransportersMessage("Added Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_TransportersMessage("Some error occured!"));
                }
            }
        },
        validationSchema: yup.object({
           UserID: yup.string(),
FullName: yup.string().required('Full Name is required'),
Gender: yup.string(),
Phone: yup.string(),
Whatsapp: yup.string(),
Email: yup.string(),
Website: yup.string(),
Address: yup.string(),
StateOfOrigin: yup.string(),
Nationality: yup.string(),
Transporter_Avatar_Url: yup.string(),
Transporter_Type: yup.string(),
TransporterAvatar: yup.string(),
LoggedIP: yup.string(),
RecordCreatedAt: yup.date(),
RecordUpdatedAt: yup.date(),

        }),
    });
    return (
        <Card className="shadow mb-4">
            <Card.Header className="py-3">
                <h6 className="m-0 font-weight-bold text-primary text-capitalize">{action} Transporters
                    <Button className="btn-icon-split float-right" onClick={() => hideShowForm(false)}>
                        <span className="icon text-white-50">
                            <i className="fas fa-list"></i>
                        </span>
                        <span className="text">View Transporters</span>
                    </Button>
                </h6>

            </Card.Header>
            <Card.Body>
                <Form onSubmit={formik.handleSubmit}>
                  <Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="UserID" className="form-control" value={formik.values.UserID}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.UserID && !!formik.errors.UserID}
isValid ={!!formik.touched.UserID && !formik.errors.UserID}
></Form.Control>
{
    formik.errors.UserID && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.UserID}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Full Name</label>
<Form.Control type="text" name="FullName" className="form-control" value={formik.values.FullName}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.FullName && !!formik.errors.FullName}
isValid ={!!formik.touched.FullName && !formik.errors.FullName}
></Form.Control>
{
    formik.errors.FullName && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.FullName}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Gender</label>
<Form.Control as ="select" name="Gender" className="form-control" value={formik.values.Gender}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Gender && !!formik.errors.Gender}
isValid ={!!formik.touched.Gender && !formik.errors.Gender}
>
<option value="Female">Female</option>
<option value="Male">Male</option>

</Form.Control>
{
    formik.errors.Gender && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Gender}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Phone</label>
<Form.Control type="text" name="Phone" className="form-control" value={formik.values.Phone}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Phone && !!formik.errors.Phone}
isValid ={!!formik.touched.Phone && !formik.errors.Phone}
></Form.Control>
{
    formik.errors.Phone && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Phone}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Whatsapp Number(Optional)</label>
<Form.Control type="text" name="Whatsapp" className="form-control" value={formik.values.Whatsapp}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Whatsapp && !!formik.errors.Whatsapp}
isValid ={!!formik.touched.Whatsapp && !formik.errors.Whatsapp}
></Form.Control>
{
    formik.errors.Whatsapp && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Whatsapp}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Email</label>
<Form.Control type="text" name="Email" className="form-control" value={formik.values.Email}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Email && !!formik.errors.Email}
isValid ={!!formik.touched.Email && !formik.errors.Email}
></Form.Control>
{
    formik.errors.Email && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Email}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Website(Optional)</label>
<Form.Control type="text" name="Website" className="form-control" value={formik.values.Website}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Website && !!formik.errors.Website}
isValid ={!!formik.touched.Website && !formik.errors.Website}
></Form.Control>
{
    formik.errors.Website && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Website}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Address</label>
<Form.Control type="text" name="Address" className="form-control" value={formik.values.Address}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Address && !!formik.errors.Address}
isValid ={!!formik.touched.Address && !formik.errors.Address}
></Form.Control>
{
    formik.errors.Address && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Address}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">State Of Origin(Optional)</label>
<Form.Control type="text" name="StateOfOrigin" className="form-control" value={formik.values.StateOfOrigin}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.StateOfOrigin && !!formik.errors.StateOfOrigin}
isValid ={!!formik.touched.StateOfOrigin && !formik.errors.StateOfOrigin}
></Form.Control>
{
    formik.errors.StateOfOrigin && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.StateOfOrigin}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Nationality</label>
<Form.Control type="text" name="Nationality" className="form-control" value={formik.values.Nationality}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Nationality && !!formik.errors.Nationality}
isValid ={!!formik.touched.Nationality && !formik.errors.Nationality}
></Form.Control>
{
    formik.errors.Nationality && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Nationality}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Transporter Avatar(Optional)</label>
<Form.Control type="file" name="Transporter_Avatar_Url" className="form-control" value={formik.values.Transporter_Avatar_Url}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Transporter_Avatar_Url && !!formik.errors.Transporter_Avatar_Url}
isValid ={!!formik.touched.Transporter_Avatar_Url && !formik.errors.Transporter_Avatar_Url}
></Form.Control>
{
    formik.errors.Transporter_Avatar_Url && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Transporter_Avatar_Url}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Transporter Type</label>
<Form.Control as ="select" name="Transporter_Type" className="form-control" value={formik.values.Transporter_Type}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Transporter_Type && !!formik.errors.Transporter_Type}
isValid ={!!formik.touched.Transporter_Type && !formik.errors.Transporter_Type}
>
<option value="Company">Company</option>
<option value="Individual">Individual</option>
</Form.Control>
{
    formik.errors.Transporter_Type && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Transporter_Type}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="TransporterAvatar" className="form-control" value={formik.values.TransporterAvatar}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.TransporterAvatar && !!formik.errors.TransporterAvatar}
isValid ={!!formik.touched.TransporterAvatar && !formik.errors.TransporterAvatar}
></Form.Control>
{
    formik.errors.TransporterAvatar && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.TransporterAvatar}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="LoggedIP" className="form-control" value={formik.values.LoggedIP}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.LoggedIP && !!formik.errors.LoggedIP}
isValid ={!!formik.touched.LoggedIP && !formik.errors.LoggedIP}
></Form.Control>
{
    formik.errors.LoggedIP && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.LoggedIP}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="RecordCreatedAt" className="form-control" value={formik.values.RecordCreatedAt}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.RecordCreatedAt && !!formik.errors.RecordCreatedAt}
isValid ={!!formik.touched.RecordCreatedAt && !formik.errors.RecordCreatedAt}
></Form.Control>
{
    formik.errors.RecordCreatedAt && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.RecordCreatedAt}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="RecordUpdatedAt" className="form-control" value={formik.values.RecordUpdatedAt}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.RecordUpdatedAt && !!formik.errors.RecordUpdatedAt}
isValid ={!!formik.touched.RecordUpdatedAt && !formik.errors.RecordUpdatedAt}
></Form.Control>
{
    formik.errors.RecordUpdatedAt && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.RecordUpdatedAt}
    </Form.Control.Feedback>
)}
</Form.Group>

                    <Form.Group>
                        <Button type="submit" className="float-right" variant="primary">Save</Button>
                    </Form.Group>
                </Form>
            </Card.Body>
        </Card>
    );
}

