import { useFormik } from "formik";
import React, { useState, useEffect, useLayoutEffect, Component } from "react";

import { useSelector } from "react-redux";
import { RootState } from "redux/reducers";

import { setAiimma_BookingvwMessage } from "redux/actions";
import {
  resetAiimma_TransportersToInit,
  setAiimma_TransportersList,
} from "redux/actions";
import {
  resetAiimma_Bt_Hb_CustomersToInit,
  setAiimma_Bt_Hb_CustomersList,
} from "redux/actions";
import { resetAiimma_TrucksToInit, setAiimma_TrucksList } from "redux/actions";

import { getAiimma_Transporters } from "services/aiimma_transportersService";
import { getAiimma_Bt_Hb_Customers } from "services/aiimma_bt_hb_customersService";
import { getAiimma_Trucks } from "services/aiimma_trucksService";

import {
  addAiimma_Bookingvw,
  updateAiimma_Bookingvw,
} from "services/aiimma_bookingvwService";

import DriverBayTruckDetails from "./FormStep/DriverBayTruckDetails";
import ContainerDetails from "./FormStep/ContainerDetails";
import JourneyDetails from "./FormStep/JourneyDetails";
import PaymentDetails from "./FormStep/PaymentDetails";
import { Aiimma_Drivers } from "components/aiimma_drivers";
import "./stepzilla.css";

import { Button, Card, Form } from "react-bootstrap";
import { setAiimma_AgentsMessage } from "redux/actions";

import { useAppDispatch } from "redux/store";
import {
  addAiimma_Agents,
  updateAiimma_Agents,
} from "services/aiimma_agentsService";
import { Constant } from "template/Constant";
import * as yup from "yup";
//import StepZilla from "react-stepzilla";
import StepZilla from './main';


type Props = {
  row?: any;
  hideShowForm: (actionName: any) => void;
  getData: (page: any, pageSize: any, searchKey: any) => void;
  action?: string;
};

//Aiimma_BookingvwForm
export const Aiimma_BookingvwForm: React.FC<Props> = ({
  row,
  hideShowForm,
  getData,
  action,
}) => {
  const dispatch = useAppDispatch();
  const iValue = {
    ID: "",
    BookingCode: "",
    JourneyCode: "",
    Transporter_id: "",
    Driver_id: "",
    bt_hb_customer_id: "",
    Truck_id: "",
    Container_MTU_No: "",
    Container_Size: "",
    Location_From: "",
    Journey_Destination_Port: "",
    Booking_Journey_Type: "",
    Booking_Category: "",
    JourneyStartedDate: "",
    ExpectedDeliveryDate: "",
    LockBooking: "",
    TruckStayDuration: "",
    BookingStatus: "",
    CallCardAvatar: "",
    CallCardUploadedAt: "",
    LoggedIP: "",
    RecordCreatedAt: "",
    RecordUpdatedAt: "",
  };
  const initialValue = action === "edit" ? row : iValue;
  const aiimma_transportersData = useSelector(
    (state: RootState) => state.aiimma_transporters
  );
  const aiimma_bt_hb_customersData = useSelector(
    (state: RootState) => state.aiimma_bt_hb_customers
  );
  const aiimma_trucksData = useSelector(
    (state: RootState) => state.aiimma_trucks
  );



  const formik = useFormik({
    initialValues: initialValue,
    onSubmit: async (values) => {
      if (action === "edit") {
        const response = await updateAiimma_Bookingvw(
          values.ID,
          values
        );
        if (response && (response.status === 200 || response.status === 201)) {
          dispatch(
            setAiimma_BookingvwMessage(
              "Updated Successfully"
            )
          );
          getData(Constant.defaultPageNumber, Constant.defaultPageSize, "");
          hideShowForm("");
        } else {
          dispatch(
            setAiimma_BookingvwMessage(
              "Some error occured!"
            )
          );
        }
      } else if (action === "add") {
        const response = await addAiimma_Bookingvw(values);
        if (response && (response.status === 200 || response.status === 201)) {
          dispatch(
            setAiimma_BookingvwMessage(
              "Added Successfully"
            )
          );
          getData(Constant.defaultPageNumber, Constant.defaultPageSize, "");
          hideShowForm("");
        } else {
          dispatch(
            setAiimma_BookingvwMessage(
              "Some error occured!"
            )
          );
        }
      }
    },
    validationSchema: yup.object({
      BookingCode: yup.number().required("Booking Code is required"),
      JourneyCode: yup.string(),
      Container_MTU_No: yup.string().required("Container number is required"),
      Container_Size: yup.string().required("Container size is required"),
      Location_From: yup.string().required("Please type location from"),
      Journey_Destination_Port: yup
        .string()
        .required("Destination port is required"),
      Booking_Journey_Type: yup.string(),
      Booking_Category: yup.string(),
      JourneyStartedDate: yup.date(),
      ExpectedDeliveryDate: yup.date(),
      LockBooking: yup.string(),
      TruckStayDuration: yup.number(),
      BookingStatus: yup.string(),
      CallCardAvatar: yup.string(),
      CallCardUploadedAt: yup.date(),
      LoggedIP: yup.string(),
      RecordCreatedAt: yup.date(),
      RecordUpdatedAt: yup.date(),
      Transporter_id: yup.string().required("Transporter_id is required"),
      bt_hb_customer_id: yup.string().required("Bay or Terminal is required"),
      Truck_id: yup
        .string()
        .required("You need truck to complete a booking please"),
      driver_id: yup
        .string()
        .required("You need driver to complete a booking please"),
    }),
  });

  /* const [steps, setSteps] = useState([
    {
      key: "DriverBayTruckDetails",
      label: "Driver, Holding Bay / Bonded Terminal, Truck",
      isDone: true,
      component: DriverBayTruckDetails,
    },
    {
      key: "ContainerDetails",
      label: "Container Details",
      isDone: false,
      component: ContainerDetails,
    },
    {
      key: "JourneyDetails",
      label: "Journey Details",
      isDone: false,
      component: JourneyDetails,
    },
    {
      key: "PaymentDetails",
      label: "Proceed To Payment",
      isDone: false,
      component: PaymentDetails,
    },
  ]);

  const [activeStep, setActiveStep] = useState(steps[0]);

  const handleNext = () => {
    if (steps[steps.length - 1].key === activeStep.key) {
      alert("You have completed all steps.");
      return;
    }

    const index = steps.findIndex((x) => x.key === activeStep.key);
    setSteps((prevStep) =>
      prevStep.map((x) => {
        if (x.key === activeStep.key) x.isDone = true;
        return x;
      })
    );
    setActiveStep(steps[index + 1]);
  };

  const handleBack = () => {
    const index = steps.findIndex((x) => x.key === activeStep.key);
    if (index === 0) return;

    setSteps((prevStep) =>
      prevStep.map((x) => {
        if (x.key === activeStep.key) x.isDone = false;
        return x;
      })
    );
    setActiveStep(steps[index - 1]);
  }; */

 /*  componentDidMount(() => {

  })

  componentWillUnmount(() => {

  }) */

  useEffect(() => {
    if (
      aiimma_transportersData &&
      aiimma_transportersData.list &&
      aiimma_transportersData.list.length === 0
    ) {
      dispatch(resetAiimma_TransportersToInit());
      getAiimma_Transporters(
        Constant.defaultPageNumber,
        Constant.defaultDropdownPageSize,
        ""
      ).then((response) => {
        if (response && response.records) {
          dispatch(
            setAiimma_TransportersList({
              pageNo: Constant.defaultPageNumber,
              pageSize: Constant.defaultDropdownPageSize,
              list: response.records,
              totalCount: response.total_count,
              searchKey: "",
            })
          );
        } else {
          dispatch(
            setAiimma_BookingvwMessage(
              "No Record Found For Aiimma_Transporters"
            )
          );
        }
      });
    }
    if (
      aiimma_bt_hb_customersData &&
      aiimma_bt_hb_customersData.list &&
      aiimma_bt_hb_customersData.list.length === 0
    ) {
      dispatch(resetAiimma_Bt_Hb_CustomersToInit());
      getAiimma_Bt_Hb_Customers(
        Constant.defaultPageNumber,
        Constant.defaultDropdownPageSize,
        ""
      ).then((response) => {
        if (response && response.records) {
          dispatch(
            setAiimma_Bt_Hb_CustomersList({
              pageNo: Constant.defaultPageNumber,
              pageSize: Constant.defaultDropdownPageSize,
              list: response.records,
              totalCount: response.total_count,
              searchKey: "",
            })
          );
        } else {
          dispatch(
            setAiimma_BookingvwMessage(
              "No Record Found For Aiimma_Bt_Hb_Customers"
            )
          );
        }
      });
    }
    if (
      aiimma_trucksData &&
      aiimma_trucksData.list &&
      aiimma_trucksData.list.length === 0
    ) {
      dispatch(resetAiimma_TrucksToInit());
      getAiimma_Trucks(
        Constant.defaultPageNumber,
        Constant.defaultDropdownPageSize,
        ""
      ).then((response) => {
        if (response && response.records) {
          dispatch(
            setAiimma_TrucksList({
              pageNo: Constant.defaultPageNumber,
              pageSize: Constant.defaultDropdownPageSize,
              list: response.records,
              totalCount: response.total_count,
              searchKey: "",
            })
          );
        } else {
          dispatch(
            setAiimma_BookingvwMessage(
              "No Record Found For Aiimma_Trucks"
            )
          );
        }
      });
    }
  });

  const steps = [
    { name: "Driver, Truck, Holding Bay and Bonded Terminal", component: <DriverBayTruckDetails /> },
    { name: "Container", component: <ContainerDetails /> },
    { name: "Journey Details", component: <JourneyDetails /> },
    { name: "Payment", component: <PaymentDetails /> },
  ];

  return <>
    <Card className="shadow mb-4">
      <Card.Header className="py-3">
        <h6 className="m-0 font-weight-bold text-primary text-capitalize">
          {action} Request A Container Delivery Process
          <Button
            className="btn-icon-split float-right"
            onClick={() => hideShowForm(false)}
          >
            <span className="icon text-white-50">
              <i className="fas fa-list"></i>
            </span>
            <span className="text">View Container Delivery Records</span>
          </Button>
        </h6>
      </Card.Header>
      <Card.Body>
        <Form onSubmit={formik.handleSubmit}>
        <div className='step-progress'>
        <StepZilla steps={steps} />
        </div>
        </Form>
      </Card.Body>
    </Card>
 </>
};
