import React, { useEffect, useState } from "react";
import DataTable from 'react-data-table-component';
import { useSelector } from "react-redux";
import { RootState } from "redux/reducers";
import { useAppDispatch } from "redux/store";
import { resetAiimma_MembersaccessToInit, setAiimma_MembersaccessMessage } from "redux/actions";
import { Button, Card, Col, Form, InputGroup } from "react-bootstrap";
import { Constant } from "template/Constant";
import ConfirmationModal from "template/ConfirmationModal";
import { deleteAiimma_Membersaccess } from "services/aiimma_membersaccessService";
type Props = {
    hideShowForm: (action: any) => void;
    handleRowEdit: (row: any) => void;
    getData: (page: any, pageSize: any, searchKey: any) => void;
};
export const Aiimma_MembersaccessTable: React.FC<Props> = ({ hideShowForm, handleRowEdit, getData }) => {
    const dispatch = useAppDispatch();
    const [search, setSearch] = useState('');
    const [showDelete, setShowDelete] = useState(false);
    const [rowData, setRowData] = useState(undefined);
    const rData = useSelector((state: RootState) => state.aiimma_membersaccess);
    const handleSearch = () => {
        if (search.length > 0) {
            getData(Constant.defaultPageNumber, Constant.defaultPageSize, search);
        }
    }
    const handlePerRowsChange = async (newPerPage: any, page: any) => {
        await getData(page, newPerPage, '');
    }
    const handlePageChange = (page: any) => {
        getData(page, rData.pageSize, '');
    };
    const handleRowDeleteClick = (row: any) => {
        setRowData(row);
        setShowDelete(true);
    }
    useEffect(() => {
        if (rData && rData.list && rData.list.length === 0) {
            dispatch(resetAiimma_MembersaccessToInit());
            getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
        }
    })
    const handleReset = () => {
        dispatch(resetAiimma_MembersaccessToInit());
        getData(Constant.defaultPageNumber, rData.pageSize, '');
    }
    const handleServerDelete = async () => {
        if (rowData) {
            const response = await deleteAiimma_Membersaccess(rowData.ID);
            if (response) {
                dispatch(resetAiimma_MembersaccessToInit());
                dispatch(setAiimma_MembersaccessMessage("Deleted Successfully"));
                getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                setShowDelete(false);
            } else {
                dispatch(setAiimma_MembersaccessMessage("Some error occured!"));
            }
        }
    }

    const handleRowSelection = (row: any) => {
        console.log(row); // Row Selection Functionality can be written here
    }
    const handleAddButtonClick = () => {
        dispatch(setAiimma_MembersaccessMessage(''));
        hideShowForm('add');
    }

    const columns = [
        {name: 'ID', omit:true, selector: (row: { ID: any; }) => row.ID, sortable: true},
{name: 'login_id', omi:true, selector: (row: { login_id: any; }) => row.login_id, sortable: true},
{name: 'Login_Role', omit: true, selector: (row: { Login_Role: any; }) => row.Login_Role, sortable: true},
{name: 'Pwd', omit: true, selector: (row: { Pwd: any; }) => row.Pwd, sortable: true},
{name: 'EmailID', selector: (row: { EmailID: any; }) => row.EmailID, sortable: true},
{name: 'emailValidated', omit: true, selector: (row: { emailValidated: any; }) => row.emailValidated, sortable: true},
{name: 'emailActivationCode', omit: true, selector: (row: { emailActivationCode: any; }) => row.emailActivationCode, sortable: true},
{name: 'PhoneNo', selector: (row: { PhoneNo: any; }) => row.PhoneNo, sortable: true},
{name: 'phoneValidated', selector: (row: { phoneValidated: any; }) => row.phoneValidated, sortable: true},
{name: 'phoneOneTimeValidationCode', omit: true, selector: (row: { phoneOneTimeValidationCode: any; }) => row.phoneOneTimeValidationCode, sortable: true},
{name: 'phoneOneTimeValidationCodeSentAt', omit: true, selector: (row: { phoneOneTimeValidationCodeSentAt: any; }) => row.phoneOneTimeValidationCodeSentAt, sortable: true},
{name: 'phoneOneTimeValidationCodeValidPeriod', omit: true, selector: (row: { phoneOneTimeValidationCodeValidPeriod: any; }) => row.phoneOneTimeValidationCodeValidPeriod, sortable: true},
{name: 'phoneLoginValidationCode', omit: true, selector: (row: { phoneLoginValidationCode: any; }) => row.phoneLoginValidationCode, sortable: true},
{name: 'loginValidationCodeSentAt', omit: true, selector: (row: { loginValidationCodeSentAt: any; }) => row.loginValidationCodeSentAt, sortable: true},
{name: 'loginValidationCodeValidPeriod', omit: true, selector: (row: { loginValidationCodeValidPeriod: any; }) => row.loginValidationCodeValidPeriod, sortable: true},
{name: 'RoleCode', selector: (row: { RoleCode: any; }) => row.RoleCode, sortable: true},
{name: 'LoginStatus', selector: (row: { LoginStatus: any; }) => row.LoginStatus, sortable: true},
{name: 'LoggedIP', omit: true, selector: (row: { LoggedIP: any; }) => row.LoggedIP, sortable: true},
{name: 'RecorCreatedAt', selector: (row: { RecorCreatedAt: any; }) => row.RecorCreatedAt, sortable: true},

        {
            name: '',
            button: true,
            cell: (row: any) => <Button variant="link" className="btn-sm" onClick={() => handleRowEdit(row)}>Edit</Button>,
        },
        {
            name: '',
            button: true,
            cell: (row: any) => <Button variant="link" className="btn-sm" onClick={() => handleRowDeleteClick(row)}>Delete</Button>,
        },
    ];
    return (
        <Card className="shadow mb-4">
            <Card.Header className="py-3">
                <h4 className="m-0 font-weight-bold text-primary">Staff Login Details List ({rData.totalCount})
                    <Button variant="light" className="btn-circle btn-sm ml-2" onClick={handleReset}><i className="fa fa-refresh"></i></Button>
                    <Button className="btn-icon-split float-right" onClick={handleAddButtonClick}>
                        <span className="icon text-white-50">
                            <i className="fas fa-add"></i>
                        </span>
                        <span className="text">Add New</span>
                    </Button></h4>

            </Card.Header>
            <Card.Body>
                <Col className="ml-auto" md={3} xs={12} xl={3}>
                    <InputGroup>
                        <Form.Control
                            placeholder="Search"
                            aria-label="Search"
                            aria-describedby="basic-search"
                            onChange={(e) => setSearch(e.target.value)}
                        />
                        <Button disabled={search.length <= 2} variant="dark" className="rounded-0 rounded-right" id="button-search" onClick={handleSearch}>
                            Search
                        </Button>
                    </InputGroup>
                </Col>
                <div className="table-responsive">
                    <DataTable
                        selectableRows={true}
                        onSelectedRowsChange={handleRowSelection}
                        paginationPerPage={Constant.defaultPageNumber}
                        paginationRowsPerPageOptions={Constant.paginationRowsPerPageOptions}
                        columns={columns} data={rData.list}
                        onChangeRowsPerPage={handlePerRowsChange}
                        paginationTotalRows={rData.totalCount}
                        className="table table-bordered"
                        pagination
                        paginationServer
                        onChangePage={handlePageChange}></DataTable>
                </div>
            </Card.Body>
            <ConfirmationModal buttonNegative="Cancel" buttonPositive="Delete" title="Delete Confirmation" show={showDelete} body={"Are you sure?"} onNegative={() => setShowDelete(false)} onPositive={handleServerDelete} />
        </Card>
    );
}

