import React from 'react';
import { useFormik } from "formik";
import { Button, Card, Form } from "react-bootstrap";

const ContainerDetails: React.FC = () => {
  return <>
    <Card className="shadow mb-4">
            <Card.Header className="py-3">
                <h3 className="m-0 font-weight-bold text-primary text-capitalize">Define Container                 
                </h3>

            </Card.Header>
            <Card.Body>
<Form.Group>
<label className="form -control-label">Container MTU Number</label>
<Form.Control 
type="text" 
name="Container_MTU_No" 
className="form-control" 
>    
</Form.Control>
</Form.Group>

<Form.Group>
<label className="form -control-label">Container Size</label>
<Form.Control 
type="text" 
name="Container_Size" 
className="form-control" 
>    
</Form.Control>
</Form.Group>
            </Card.Body>
        </Card>
 </>
}

export default ContainerDetails;