import { APIService } from "services";

export const getAiimma_Staffs = async (pageNo: any,pageSize: any,search: string | any[]) => {
    let res;
    if(search.length===0) {
        res = await getAllAiimma_Staffs(pageNo,pageSize);
    }
    else{
        try {
            res = await searchAiimma_Staffs(search,pageNo,pageSize);
        } catch(err) {
             return { records:[], totalCount:0 }
        }
    }
    if(res && res.data){
    return res.data;
    }else{
    return { records:[], totalCount:0 }
    }
    
}


export const addAiimma_Staffs = (data: any) => {
return APIService.api().post(`/aiimma_staffs`,data)
}
export const updateAiimma_Staffs = (id: any,data: any) => {
return APIService.api().patch(`/aiimma_staffs/${id}`,data)
}
export const getAllAiimma_Staffs = (pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_staffs/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const getOneAiimma_Staffs = (id: any) => {
return APIService.api().get(`/aiimma_staffs/${id}`)
}
export const searchAiimma_Staffs = (searchKey: any,pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_staffs/search/${searchKey}/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const deleteAiimma_Staffs = (id: any) => {
return APIService.api().delete(`/aiimma_staffs/${id}`)
}
