import { APIService } from "services";

export const getAiimma_Agents = async (pageNo: any,pageSize: any,search: string | any[]) => {
    let res;
    if(search.length===0) {
        res = await getAllAiimma_Agents(pageNo,pageSize);
    }
    else{
        try {
            res = await searchAiimma_Agents(search,pageNo,pageSize);
        } catch(err) {
             return { records:[], totalCount:0 }
        }
    }
    if(res && res.data){
    return res.data;
    }else{
    return { records:[], totalCount:0 }
    }
    
}


export const addAiimma_Agents = (data: any) => {
return APIService.api().post(`/aiimma_agents`,data)
}
export const updateAiimma_Agents = (ID: any,data: any) => {
return APIService.api().patch(`/aiimma_agents/${ID}`,data)
}
export const getAllAiimma_Agents = (pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_agents/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const getOneAiimma_Agents = (ID: any) => {
return APIService.api().get(`/aiimma_agents/${ID}`)
}
export const searchAiimma_Agents = (searchKey: any,pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_agents/search/${searchKey}/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const deleteAiimma_Agents = (ID: any) => {
return APIService.api().delete(`/aiimma_agents/${ID}`)
}
