import { useFormik } from "formik";
import React from "react";

import { Button, Card, Form } from "react-bootstrap";
import { setAiimma_TrucksMessage } from "redux/actions";


import { useAppDispatch } from "redux/store";
import { addAiimma_Trucks, updateAiimma_Trucks } from "services/aiimma_trucksService";
import { Constant } from "template/Constant";
import * as yup from 'yup';
type Props = {
    row?: any,
    hideShowForm: (actionName: any) => void;
    getData: (page: any, pageSize: any, searchKey: any) => void;
    action?: string
};
export const Aiimma_TrucksForm: React.FC<Props> = ({ row, hideShowForm, getData, action }) => {
    const dispatch = useAppDispatch();
    const iValue={ID:'',TruckName:'',TruckPlateNumber:'',TruckChasisNumber:'',TruckColor:'',TruckSize:'',Truck_Avatar_Url:'',TruckMSSCheckStatus:'',TruckMSSApprovedCert_Avatar_URL:'',TruckMSSCertNumber:'',TruckMSSCertValidDuration:'',TruckOwnedBy:'',LoggedIP:'',RecordCreatedAt:'',RecordUpdatedAt:''};
    const initialValue = action === 'edit' ? row : iValue;
    
    
    const formik = useFormik({
        initialValues: initialValue,
        onSubmit: async (values) => {
            if (action === 'edit') {
                const response = await updateAiimma_Trucks(values.ID,values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_TrucksMessage("Updated Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_TrucksMessage("Some error occured!"));
                }
            } else if (action === 'add') {
                const response = await addAiimma_Trucks(values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_TrucksMessage("Added Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_TrucksMessage("Some error occured!"));
                }
            }
        },
        validationSchema: yup.object({
           TruckName: yup.string().required('Truck name is required'),
TruckPlateNumber: yup.string().required('Truck Plate Number is required'),
TruckChasisNumber: yup.string().required('Truck Chasis Number is required'),
TruckColor: yup.string().required('Truck color is required'),
TruckSize: yup.string().required('Truck size is required'),
Truck_Avatar_Url: yup.string(),
TruckMSSCheckStatus: yup.string(),
TruckMSSApprovedCert_Avatar_URL: yup.string(),
TruckMSSCertNumber: yup.string(),
TruckMSSCertValidDuration: yup.string(),
TruckOwnedBy: yup.number(),
LoggedIP: yup.string(),
RecordCreatedAt: yup.date(),
RecordUpdatedAt: yup.date(),

        }),
    });
    return (
        <Card className="shadow mb-4">
            <Card.Header className="py-3">
                <h6 className="m-0 font-weight-bold text-primary text-capitalize">{action} Aiimma_Trucks
                    <Button className="btn-icon-split float-right" onClick={() => hideShowForm(false)}>
                        <span className="icon text-white-50">
                            <i className="fas fa-list"></i>
                        </span>
                        <span className="text">View Trucks</span>
                    </Button>
                </h6>

            </Card.Header>
            <Card.Body>
                <Form onSubmit={formik.handleSubmit}>
                  <Form.Group>
<label className="form -control-label">Truck Name</label>
<Form.Control type="text" name="TruckName" className="form-control" value={formik.values.TruckName}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.TruckName && !!formik.errors.TruckName}
isValid ={!!formik.touched.TruckName && !formik.errors.TruckName}
></Form.Control>
{
    formik.errors.TruckName && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.TruckName}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Truck Plate Number</label>
<Form.Control type="text" name="TruckPlateNumber" className="form-control" value={formik.values.TruckPlateNumber}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.TruckPlateNumber && !!formik.errors.TruckPlateNumber}
isValid ={!!formik.touched.TruckPlateNumber && !formik.errors.TruckPlateNumber}
></Form.Control>
{
    formik.errors.TruckPlateNumber && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.TruckPlateNumber}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Truck Chasis Number</label>
<Form.Control type="text" name="TruckChasisNumber" className="form-control" value={formik.values.TruckChasisNumber}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.TruckChasisNumber && !!formik.errors.TruckChasisNumber}
isValid ={!!formik.touched.TruckChasisNumber && !formik.errors.TruckChasisNumber}
></Form.Control>
{
    formik.errors.TruckChasisNumber && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.TruckChasisNumber}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Truck Color</label>
<Form.Control type="text" name="TruckColor" className="form-control" value={formik.values.TruckColor}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.TruckColor && !!formik.errors.TruckColor}
isValid ={!!formik.touched.TruckColor && !formik.errors.TruckColor}
></Form.Control>
{
    formik.errors.TruckColor && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.TruckColor}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Truck Size</label>
<Form.Control as ="select" name="TruckSize" className="form-control" value={formik.values.TruckSize}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.TruckSize && !!formik.errors.TruckSize}
isValid ={!!formik.touched.TruckSize && !formik.errors.TruckSize}
>
    <option value="28">28</option>
    <option value="48">48</option>
</Form.Control>
{
    formik.errors.TruckSize && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.TruckSize}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Truck Picture Avatar(Optional)</label>
<Form.Control type="text" name="Truck_Avatar_Url" className="form-control" value={formik.values.Truck_Avatar_Url}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Truck_Avatar_Url && !!formik.errors.Truck_Avatar_Url}
isValid ={!!formik.touched.Truck_Avatar_Url && !formik.errors.Truck_Avatar_Url}
></Form.Control>
{
    formik.errors.Truck_Avatar_Url && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Truck_Avatar_Url}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Truck MSS CheckStatus</label>
<Form.Control as ="select" name="TruckMSSCheckStatus" className="form-control" value={formik.values.TruckMSSCheckStatus}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.TruckMSSCheckStatus && !!formik.errors.TruckMSSCheckStatus}
isValid ={!!formik.touched.TruckMSSCheckStatus && !formik.errors.TruckMSSCheckStatus}
>
<option value="Unverified">Unverified</option>
    <option value="Verified">Verified</option>
</Form.Control>
{
    formik.errors.TruckMSSCheckStatus && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.TruckMSSCheckStatus}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Truck MSS Approved Cert. Picture</label>
<Form.Control type="file" name="TruckMSSApprovedCert_Avatar_URL" className="form-control" value={formik.values.TruckMSSApprovedCert_Avatar_URL}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.TruckMSSApprovedCert_Avatar_URL && !!formik.errors.TruckMSSApprovedCert_Avatar_URL}
isValid ={!!formik.touched.TruckMSSApprovedCert_Avatar_URL && !formik.errors.TruckMSSApprovedCert_Avatar_URL}
></Form.Control>
{
    formik.errors.TruckMSSApprovedCert_Avatar_URL && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.TruckMSSApprovedCert_Avatar_URL}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Truck MSS Cert. Number</label>
<Form.Control type="text" name="TruckMSSCertNumber" className="form-control" value={formik.values.TruckMSSCertNumber}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.TruckMSSCertNumber && !!formik.errors.TruckMSSCertNumber}
isValid ={!!formik.touched.TruckMSSCertNumber && !formik.errors.TruckMSSCertNumber}
></Form.Control>
{
    formik.errors.TruckMSSCertNumber && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.TruckMSSCertNumber}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Truck MSS Expired Date</label>
<Form.Control type="date" name="TruckMSSCertValidDuration" className="form-control" value={formik.values.TruckMSSCertValidDuration}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.TruckMSSCertValidDuration && !!formik.errors.TruckMSSCertValidDuration}
isValid ={!!formik.touched.TruckMSSCertValidDuration && !formik.errors.TruckMSSCertValidDuration}
></Form.Control>
{
    formik.errors.TruckMSSCertValidDuration && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.TruckMSSCertValidDuration}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Truck Owned By</label>
<Form.Control type="number" name="TruckOwnedBy" className="form-control" value={formik.values.TruckOwnedBy}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.TruckOwnedBy && !!formik.errors.TruckOwnedBy}
isValid ={!!formik.touched.TruckOwnedBy && !formik.errors.TruckOwnedBy}
></Form.Control>
{
    formik.errors.TruckOwnedBy && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.TruckOwnedBy}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="LoggedIP" className="form-control" value={formik.values.LoggedIP}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.LoggedIP && !!formik.errors.LoggedIP}
isValid ={!!formik.touched.LoggedIP && !formik.errors.LoggedIP}
></Form.Control>
{
    formik.errors.LoggedIP && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.LoggedIP}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="RecordCreatedAt" className="form-control" value={formik.values.RecordCreatedAt}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.RecordCreatedAt && !!formik.errors.RecordCreatedAt}
isValid ={!!formik.touched.RecordCreatedAt && !formik.errors.RecordCreatedAt}
></Form.Control>
{
    formik.errors.RecordCreatedAt && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.RecordCreatedAt}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="RecordUpdatedAt" className="form-control" value={formik.values.RecordUpdatedAt}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.RecordUpdatedAt && !!formik.errors.RecordUpdatedAt}
isValid ={!!formik.touched.RecordUpdatedAt && !formik.errors.RecordUpdatedAt}
></Form.Control>
{
    formik.errors.RecordUpdatedAt && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.RecordUpdatedAt}
    </Form.Control.Feedback>
)}
</Form.Group>

                    <Form.Group>
                        <Button type="submit" className="float-right" variant="primary">Save</Button>
                    </Form.Group>
                </Form>
            </Card.Body>
        </Card>
    );
}

