import { useFormik } from "formik";
import React, { useEffect } from "react";
import { useSelector } from "react-redux";
import { RootState } from "redux/reducers";

import { Button, Card, Form } from "react-bootstrap";
import { setAiimma_MembersaccessMessage } from "redux/actions";
import { resetAiimma_TransportersToInit, setAiimma_TransportersList } from "redux/actions";
import { resetAiimma_RolesToInit, setAiimma_RolesList } from "redux/actions";

import { getAiimma_Transporters } from "services/aiimma_transportersService";
import { getAiimma_Roles } from "services/aiimma_rolesService";

import { useAppDispatch } from "redux/store";
import { addAiimma_Membersaccess, updateAiimma_Membersaccess } from "services/aiimma_membersaccessService";
import { Constant } from "template/Constant";
import * as yup from 'yup';
type Props = {
    row?: any,
    hideShowForm: (actionName: any) => void;
    getData: (page: any, pageSize: any, searchKey: any) => void;
    action?: string
};
export const Aiimma_MembersaccessForm: React.FC<Props> = ({ row, hideShowForm, getData, action }) => {
    const dispatch = useAppDispatch();
    const iValue={ID:'',login_id:'',Login_Role:'',Pwd:'',EmailID:'',emailValidated:'',emailActivationCode:'',PhoneNo:'',phoneValidated:'',phoneOneTimeValidationCode:'',phoneOneTimeValidationCodeSentAt:'',phoneOneTimeValidationCodeValidPeriod:'',phoneLoginValidationCode:'',loginValidationCodeSentAt:'',loginValidationCodeValidPeriod:'',RoleCode:'',LoginStatus:'',LoggedIP:'',RecorCreatedAt:''};
    const initialValue = action === 'edit' ? row : iValue;
    const aiimma_transportersData = useSelector((state: RootState) => state.aiimma_transporters);
const aiimma_rolesData = useSelector((state: RootState) => state.aiimma_roles);

    useEffect(() => {
if (aiimma_transportersData && aiimma_transportersData.list && aiimma_transportersData.list.length === 0) {
            dispatch(resetAiimma_TransportersToInit());
            getAiimma_Transporters(Constant.defaultPageNumber, Constant.defaultDropdownPageSize, '').then((response) => {
                if (response && response.records) {
                  dispatch(setAiimma_TransportersList({ pageNo: Constant.defaultPageNumber, pageSize: Constant.defaultDropdownPageSize, list: response.records, totalCount: response.total_count, searchKey: '' }));
                } else {
                  dispatch(setAiimma_MembersaccessMessage("No Record Found For Transporters"));
                }
              })
        }
if (aiimma_rolesData && aiimma_rolesData.list && aiimma_rolesData.list.length === 0) {
            dispatch(resetAiimma_RolesToInit());
            getAiimma_Roles(Constant.defaultPageNumber, Constant.defaultDropdownPageSize, '').then((response) => {
                if (response && response.records) {
                  dispatch(setAiimma_RolesList({ pageNo: Constant.defaultPageNumber, pageSize: Constant.defaultDropdownPageSize, list: response.records, totalCount: response.total_count, searchKey: '' }));
                } else {
                  dispatch(setAiimma_MembersaccessMessage("No Record Found For User Roles"));
                }
              })
        }
})
    const formik = useFormik({
        initialValues: initialValue,
        onSubmit: async (values) => {
            if (action === 'edit') {
                const response = await updateAiimma_Membersaccess(values.ID,values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_MembersaccessMessage("Updated Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_MembersaccessMessage("Some error occured!"));
                }
            } else if (action === 'add') {
                const response = await addAiimma_Membersaccess(values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_MembersaccessMessage("Added Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_MembersaccessMessage("Some error occured!"));
                }
            }
        },
        validationSchema: yup.object({
           Login_Role: yup.string().required('Login Role is required'),
Pwd: yup.string().required('Password is required'),
EmailID: yup.string().required('Email Address is required'),
emailValidated: yup.string(),
emailActivationCode: yup.string(),
PhoneNo: yup.string().required('PhoneNo is required'),
phoneValidated: yup.string(),
phoneOneTimeValidationCode: yup.string(),
phoneOneTimeValidationCodeSentAt: yup.date(),
phoneOneTimeValidationCodeValidPeriod: yup.number(),
phoneLoginValidationCode: yup.string(),
loginValidationCodeSentAt: yup.date(),
loginValidationCodeValidPeriod: yup.number(),
LoginStatus: yup.string(),
LoggedIP: yup.string(),
RecorCreatedAt: yup.date(),
login_id: yup.string(),
RoleCode: yup.string(),

        }),
    });
    return (
        <Card className="shadow mb-4">
            <Card.Header className="py-3">
                <h6 className="m-0 font-weight-bold text-primary text-capitalize">{action} Members Login Details
                    <Button className="btn-icon-split float-right" onClick={() => hideShowForm(false)}>
                        <span className="icon text-white-50">
                            <i className="fas fa-list"></i>
                        </span>
                        <span className="text">View Members Login</span>
                    </Button>
                </h6>

            </Card.Header>
            <Card.Body>
                <Form onSubmit={formik.handleSubmit}>
                  <Form.Group>
<label className="form -control-label">User Role</label>
<Form.Control type="text" name="Login_Role" className="form-control" value={formik.values.Login_Role}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Login_Role && !!formik.errors.Login_Role}
isValid ={!!formik.touched.Login_Role && !formik.errors.Login_Role}
>

<option value={0}>Select Roles </option> 
{
aiimma_rolesData.list.map((item, i) => {
return <option value={item.RoleCode} key={`aiimma_roles-${i}`}>{item.RoleName}</option>
})}

</Form.Control>
{
    formik.errors.Login_Role && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Login_Role}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Pwd</label>
<Form.Control type="text" name="Pwd" className="form-control" disabled value={formik.values.Pwd}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Pwd && !!formik.errors.Pwd}
isValid ={!!formik.touched.Pwd && !formik.errors.Pwd}
></Form.Control>
{
    formik.errors.Pwd && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Pwd}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Email Address</label>
<Form.Control type="text" name="EmailID" className="form-control" value={formik.values.EmailID}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.EmailID && !!formik.errors.EmailID}
isValid ={!!formik.touched.EmailID && !formik.errors.EmailID}
></Form.Control>
{
    formik.errors.EmailID && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.EmailID}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="emailValidated" className="form-control" value={formik.values.emailValidated}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.emailValidated && !!formik.errors.emailValidated}
isValid ={!!formik.touched.emailValidated && !formik.errors.emailValidated}
></Form.Control>
{
    formik.errors.emailValidated && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.emailValidated}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="emailActivationCode" className="form-control" value={formik.values.emailActivationCode}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.emailActivationCode && !!formik.errors.emailActivationCode}
isValid ={!!formik.touched.emailActivationCode && !formik.errors.emailActivationCode}
></Form.Control>
{
    formik.errors.emailActivationCode && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.emailActivationCode}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Phone No.</label>
<Form.Control type="text" name="PhoneNo" className="form-control" value={formik.values.PhoneNo}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.PhoneNo && !!formik.errors.PhoneNo}
isValid ={!!formik.touched.PhoneNo && !formik.errors.PhoneNo}
></Form.Control>
{
    formik.errors.PhoneNo && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.PhoneNo}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="phoneValidated" className="form-control" value={formik.values.phoneValidated}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.phoneValidated && !!formik.errors.phoneValidated}
isValid ={!!formik.touched.phoneValidated && !formik.errors.phoneValidated}
></Form.Control>
{
    formik.errors.phoneValidated && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.phoneValidated}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="phoneOneTimeValidationCode" className="form-control" value={formik.values.phoneOneTimeValidationCode}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.phoneOneTimeValidationCode && !!formik.errors.phoneOneTimeValidationCode}
isValid ={!!formik.touched.phoneOneTimeValidationCode && !formik.errors.phoneOneTimeValidationCode}
></Form.Control>
{
    formik.errors.phoneOneTimeValidationCode && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.phoneOneTimeValidationCode}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="phoneOneTimeValidationCodeSentAt" className="form-control" value={formik.values.phoneOneTimeValidationCodeSentAt}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.phoneOneTimeValidationCodeSentAt && !!formik.errors.phoneOneTimeValidationCodeSentAt}
isValid ={!!formik.touched.phoneOneTimeValidationCodeSentAt && !formik.errors.phoneOneTimeValidationCodeSentAt}
></Form.Control>
{
    formik.errors.phoneOneTimeValidationCodeSentAt && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.phoneOneTimeValidationCodeSentAt}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="phoneOneTimeValidationCodeValidPeriod" className="form-control" value={formik.values.phoneOneTimeValidationCodeValidPeriod}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.phoneOneTimeValidationCodeValidPeriod && !!formik.errors.phoneOneTimeValidationCodeValidPeriod}
isValid ={!!formik.touched.phoneOneTimeValidationCodeValidPeriod && !formik.errors.phoneOneTimeValidationCodeValidPeriod}
></Form.Control>
{
    formik.errors.phoneOneTimeValidationCodeValidPeriod && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.phoneOneTimeValidationCodeValidPeriod}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="phoneLoginValidationCode" className="form-control" value={formik.values.phoneLoginValidationCode}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.phoneLoginValidationCode && !!formik.errors.phoneLoginValidationCode}
isValid ={!!formik.touched.phoneLoginValidationCode && !formik.errors.phoneLoginValidationCode}
></Form.Control>
{
    formik.errors.phoneLoginValidationCode && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.phoneLoginValidationCode}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="loginValidationCodeSentAt" className="form-control" value={formik.values.loginValidationCodeSentAt}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.loginValidationCodeSentAt && !!formik.errors.loginValidationCodeSentAt}
isValid ={!!formik.touched.loginValidationCodeSentAt && !formik.errors.loginValidationCodeSentAt}
></Form.Control>
{
    formik.errors.loginValidationCodeSentAt && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.loginValidationCodeSentAt}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="loginValidationCodeValidPeriod" className="form-control" value={formik.values.loginValidationCodeValidPeriod}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.loginValidationCodeValidPeriod && !!formik.errors.loginValidationCodeValidPeriod}
isValid ={!!formik.touched.loginValidationCodeValidPeriod && !formik.errors.loginValidationCodeValidPeriod}
></Form.Control>
{
    formik.errors.loginValidationCodeValidPeriod && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.loginValidationCodeValidPeriod}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Login Status</label>
<Form.Control as="select" name="LoginStatus" className="form-control" value={formik.values.LoginStatus}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.LoginStatus && !!formik.errors.LoginStatus}
isValid ={!!formik.touched.LoginStatus && !formik.errors.LoginStatus}
>
    <option value="Disabled">Disabled</option>
    <option value="Enabled">Enabled</option>
</Form.Control>
{
    formik.errors.LoginStatus && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.LoginStatus}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="LoggedIP" className="form-control" value={formik.values.LoggedIP}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.LoggedIP && !!formik.errors.LoggedIP}
isValid ={!!formik.touched.LoggedIP && !formik.errors.LoggedIP}
></Form.Control>
{
    formik.errors.LoggedIP && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.LoggedIP}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="RecorCreatedAt" className="form-control" value={formik.values.RecorCreatedAt}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.RecorCreatedAt && !!formik.errors.RecorCreatedAt}
isValid ={!!formik.touched.RecorCreatedAt && !formik.errors.RecorCreatedAt}
></Form.Control>
{
    formik.errors.RecorCreatedAt && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.RecorCreatedAt}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">login_id</label>
<Form.Control as="select"  name="login_id" className="form-control" value={formik.values.login_id}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.login_id && !!formik.errors.login_id}
isValid ={!!formik.touched.login_id && !formik.errors.login_id}
>
<option value={0}>Select Aiimma_Transporters </option> 
{
aiimma_transportersData.list.map((item, i) => {
return <option value={item.ID} key={`aiimma_transporters-${i}`}>{item.UserID}</option>
})}
</Form.Control>
{
    formik.errors.login_id && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.login_id}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Role Code</label>
<Form.Control as="select"  name="RoleCode" className="form-control" value={formik.values.RoleCode}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.RoleCode && !!formik.errors.RoleCode}
isValid ={!!formik.touched.RoleCode && !formik.errors.RoleCode}
>
<option value={0}>Select Roles </option> 
{
aiimma_rolesData.list.map((item, i) => {
return <option value={item.RoleCode} key={`aiimma_roles-${i}`}>{item.RoleName}</option>
})}
</Form.Control>
{
    formik.errors.RoleCode && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.RoleCode}
    </Form.Control.Feedback>
)}
</Form.Group>

                    <Form.Group>
                        <Button type="submit" className="float-right" variant="primary">Save</Button>
                    </Form.Group>
                </Form>
            </Card.Body>
        </Card>
    );
}

