import { APIService } from "services";

export const getAiimma_Transporters = async (pageNo: number,pageSize: number,search: string | any[]) => {
    let res;
    if(search.length===0) {
        res = await getAllAiimma_Transporters(pageNo,pageSize);
    }
    else{
        try {
            res = await searchAiimma_Transporters(search,pageNo,pageSize);
        } catch(err) {
             return { records:[], totalCount:0 }
        }
    }
    if(res && res.data){
    return res.data;
    }else{
    return { records:[], totalCount:0 }
    }
    
}


export const addAiimma_Transporters = (data: any) => {
return APIService.api().post(`/aiimma_transporters`,data)
}
export const updateAiimma_Transporters = (ID: any,data: any) => {
return APIService.api().patch(`/aiimma_transporters/${ID}`,data)
}
export const getAllAiimma_Transporters = (pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_transporters/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const getOneAiimma_Transporters = (ID: any) => {
return APIService.api().get(`/aiimma_transporters/${ID}`)
}
export const searchAiimma_Transporters = (searchKey: any,pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_transporters/search/${searchKey}/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const deleteAiimma_Transporters = (ID: any) => {
return APIService.api().delete(`/aiimma_transporters/${ID}`)
}
