import { APIService } from "services";

export const getAiimma_Bt_Hb_Customers = async (pageNo: number,pageSize: number,search: string | any[]) => {
    let res;
    if(search.length===0) {
        res = await getAllAiimma_Bt_Hb_Customers(pageNo,pageSize);
    }
    else{
        try {
            res = await searchAiimma_Bt_Hb_Customers(search,pageNo,pageSize);
        } catch(err) {
             return { records:[], totalCount:0 }
        }
    }
    if(res && res.data){
    return res.data;
    }else{
    return { records:[], totalCount:0 }
    }
    
}


export const addAiimma_Bt_Hb_Customers = (data: any) => {
return APIService.api().post(`/aiimma_bt_hb_customers`,data)
}
export const updateAiimma_Bt_Hb_Customers = (ID: any,data: any) => {
return APIService.api().patch(`/aiimma_bt_hb_customers/${ID}`,data)
}
export const getAllAiimma_Bt_Hb_Customers = (pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_bt_hb_customers/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const getOneAiimma_Bt_Hb_Customers = (ID: any) => {
return APIService.api().get(`/aiimma_bt_hb_customers/${ID}`)
}
export const searchAiimma_Bt_Hb_Customers = (searchKey: any,pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_bt_hb_customers/search/${searchKey}/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const deleteAiimma_Bt_Hb_Customers = (ID: any) => {
return APIService.api().delete(`/aiimma_bt_hb_customers/${ID}`)
}
