import { useFormik } from "formik";
import React from "react";

import { Button, Card, Form } from "react-bootstrap";
import { setAiimma_Customers_Deposit_BalanceMessage } from "redux/actions";


import { useAppDispatch } from "redux/store";
import { addAiimma_Customers_Deposit_Balance, updateAiimma_Customers_Deposit_Balance } from "services/aiimma_customers_deposit_balanceService";
import { Constant } from "template/Constant";
import * as yup from 'yup';
type Props = {
    row?: any,
    hideShowForm: (actionName: any) => void;
    getData: (page: any, pageSize: any, searchKey: any) => void;
    action?: string
};
export const Aiimma_Customers_Deposit_BalanceForm: React.FC<Props> = ({ row, hideShowForm, getData, action }) => {
    const dispatch = useAppDispatch();
    const iValue={ID:'',customer_id:'',customer_role:'',AccountBalance:'',FirstTimeBalance:'',LoggedIP:''};
    const initialValue = action === 'edit' ? row : iValue;
    
    
    const formik = useFormik({
        initialValues: initialValue,
        onSubmit: async (values) => {
            if (action === 'edit') {
                const response = await updateAiimma_Customers_Deposit_Balance(values.ID,values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_Customers_Deposit_BalanceMessage("Updated Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_Customers_Deposit_BalanceMessage("Some error occured!"));
                }
            } else if (action === 'add') {
                const response = await addAiimma_Customers_Deposit_Balance(values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_Customers_Deposit_BalanceMessage("Added Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_Customers_Deposit_BalanceMessage("Some error occured!"));
                }
            }
        },
        validationSchema: yup.object({
           customer_id: yup.number().required('customer_id is required'),
customer_role: yup.string().required('customer_role is required'),
AccountBalance: yup.number().required('AccountBalance is required'),
FirstTimeBalance: yup.string().required('FirstTimeBalance is required'),
LoggedIP: yup.string().required('LoggedIP is required'),

        }),
    });
    return (
        <Card className="shadow mb-4">
            <Card.Header className="py-3">
                <h6 className="m-0 font-weight-bold text-primary text-capitalize">{action} Transporters Wallet Balance
                    <Button className="btn-icon-split float-right" onClick={() => hideShowForm(false)}>
                        <span className="icon text-white-50">
                            <i className="fas fa-list"></i>
                        </span>
                        <span className="text">View Transporters Wallet Balance</span>
                    </Button>
                </h6>

            </Card.Header>
            <Card.Body>
                <Form onSubmit={formik.handleSubmit}>
                  <Form.Group>
<label className="form -control-label">customer_id</label>
<Form.Control type="number" name="customer_id" className="form-control" value={formik.values.customer_id}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.customer_id && !!formik.errors.customer_id}
isValid ={!!formik.touched.customer_id && !formik.errors.customer_id}
></Form.Control>
{
    formik.errors.customer_id && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.customer_id}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">customer_role</label>
<Form.Control type="text" name="customer_role" className="form-control" value={formik.values.customer_role}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.customer_role && !!formik.errors.customer_role}
isValid ={!!formik.touched.customer_role && !formik.errors.customer_role}
></Form.Control>
{
    formik.errors.customer_role && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.customer_role}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">AccountBalance</label>
<Form.Control type="number" name="AccountBalance" className="form-control" value={formik.values.AccountBalance}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.AccountBalance && !!formik.errors.AccountBalance}
isValid ={!!formik.touched.AccountBalance && !formik.errors.AccountBalance}
></Form.Control>
{
    formik.errors.AccountBalance && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.AccountBalance}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">FirstTimeBalance</label>
<Form.Control type="text" name="FirstTimeBalance" className="form-control" value={formik.values.FirstTimeBalance}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.FirstTimeBalance && !!formik.errors.FirstTimeBalance}
isValid ={!!formik.touched.FirstTimeBalance && !formik.errors.FirstTimeBalance}
></Form.Control>
{
    formik.errors.FirstTimeBalance && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.FirstTimeBalance}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">LoggedIP</label>
<Form.Control type="text" name="LoggedIP" className="form-control" value={formik.values.LoggedIP}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.LoggedIP && !!formik.errors.LoggedIP}
isValid ={!!formik.touched.LoggedIP && !formik.errors.LoggedIP}
></Form.Control>
{
    formik.errors.LoggedIP && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.LoggedIP}
    </Form.Control.Feedback>
)}
</Form.Group>

                    <Form.Group>
                        <Button type="submit" className="float-right" variant="primary">Save</Button>
                    </Form.Group>
                </Form>
            </Card.Body>
        </Card>
    );
}

