import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface IAiimma_Staffs {
id:number,
staffCode:string,
staffName:string,
Email:string,
Phone:string,
Gender:string,
staffRole:string,
RecordCreatedBy:number,
LoggedIP:string,
RecordCreatedAt:Date
}

interface IAiimma_StaffsData {
    list?: Array<IAiimma_Staffs>;
    pageNo: number;
    pageSize: number;
    searchKey?: string;
    totalCount?: number;
    message?: string;
}

const initialState: IAiimma_StaffsData = {
    pageNo: 1,
    pageSize: 20,
    searchKey: '',
    list: [],
    totalCount: 0,
    message: '',
};

const aiimma_staffsSlice = createSlice({
    name: "aiimma_staffs",
    initialState,
    reducers: {
        setAiimma_StaffsList: (state, _action: PayloadAction<IAiimma_StaffsData>) => {
            state.list = _action.payload.list;
            state.pageNo = _action.payload.pageNo;
            state.pageSize = _action.payload.pageSize;
            state.totalCount = _action.payload.totalCount;
        },
        resetAiimma_StaffsToInit: (state) => {
            state = initialState;
        },
        setAiimma_StaffsMessage: (state, _action: PayloadAction<string>) => {
            state.message = _action.payload;
        },
    },
});

export const { setAiimma_StaffsList, resetAiimma_StaffsToInit, setAiimma_StaffsMessage } = aiimma_staffsSlice.actions;

export default aiimma_staffsSlice.reducer;

