import { APIService } from "services";

export const getAiimma_Membersaccess = async (pageNo: any,pageSize: any,search: string | any[]) => {
    let res;
    if(search.length===0) {
        res = await getAllAiimma_Membersaccess(pageNo,pageSize);
    }
    else{
        try {
            res = await searchAiimma_Membersaccess(search,pageNo,pageSize);
        } catch(err) {
             return { records:[], totalCount:0 }
        }
    }
    if(res && res.data){
    return res.data;
    }else{
    return { records:[], totalCount:0 }
    }
    
}


export const addAiimma_Membersaccess = (data: any) => {
return APIService.api().post(`/aiimma_membersaccess`,data)
}
export const updateAiimma_Membersaccess = (ID: any,data: any) => {
return APIService.api().patch(`/aiimma_membersaccess/${ID}`,data)
}
export const getAllAiimma_Membersaccess = (pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_membersaccess/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const getOneAiimma_Membersaccess = (ID: any) => {
return APIService.api().get(`/aiimma_membersaccess/${ID}`)
}
export const searchAiimma_Membersaccess = (searchKey: any,pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_membersaccess/search/${searchKey}/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const deleteAiimma_Membersaccess = (ID: any) => {
return APIService.api().delete(`/aiimma_membersaccess/${ID}`)
}
