import { combineReducers } from "redux";

import template from "redux/slices/template";
import authToken from "redux/slices/auth";

import aiimma_trucks from "redux/slices/aiimma_trucks";
import aiimma_truck_maintenance_journey_booking_details from "redux/slices/aiimma_truck_maintenance_journey_booking_details";
import aiimma_transporters from "redux/slices/aiimma_transporters";
import aiimma_transporter_truck_junction from "redux/slices/aiimma_transporter_truck_junction";
import aiimma_transporter_driver_junction from "redux/slices/aiimma_transporter_driver_junction";
import aiimma_tdos from "redux/slices/aiimma_tdos";
import aiimma_staffs from "redux/slices/aiimma_staffs";
import aiimma_roles from "redux/slices/aiimma_roles";
import aiimma_membersaccess from "redux/slices/aiimma_membersaccess";
import aiimma_notifyusers from "redux/slices/aiimma_Notifyusers";
import aiimma_containerdeliverycallcard from "redux/slices/aiimma_ContainerDeliveryCallCard";
import aiimma_holdingbay_containers_upload from "redux/slices/aiimma_holdingbay_containers_upload";
import aiimma_drivers from "redux/slices/aiimma_drivers";
import aiimma_customers_deposit_balance from "redux/slices/aiimma_customers_deposit_balance";
import aiimma_bt_hb_customers from "redux/slices/aiimma_bt_hb_customers";
import aiimma_booking_payments from "redux/slices/aiimma_booking_payments";
import aiimma_agents from "redux/slices/aiimma_agents";
import aiimma_bookingvw from "redux/slices/aiimma_bookingvw";
import aiimma__users_access_auditing from "redux/slices/aiimma__users_access_auditing";
import aiima_transporter_driver_bthb_booking from "redux/slices/aiima_transporter_driver_bthb_booking";

const rootReducer = combineReducers({
  template,
  authToken,
  aiimma_trucks,
  aiimma_truck_maintenance_journey_booking_details,
  aiimma_transporters,
  aiimma_transporter_truck_junction,
  aiimma_transporter_driver_junction,
  aiimma_tdos,
  aiimma_staffs,
  aiimma_roles,
  aiimma_membersaccess,
  aiimma_notifyusers,
  aiimma_holdingbay_containers_upload,
  aiimma_drivers,
  aiimma_customers_deposit_balance,
  aiimma_bt_hb_customers,
  aiimma_booking_payments,
  aiimma_agents,
  aiimma_bookingvw,
  aiimma__users_access_auditing,
  aiima_transporter_driver_bthb_booking,
  aiimma_containerdeliverycallcard,
});

export type RootState = ReturnType<typeof rootReducer>;

export default rootReducer;
