import { APIService } from "services";

export const getAiimma_Roles = async (pageNo: number,pageSize: number,search: string | any[]) => {
    let res;
    if(search.length===0) {
        res = await getAllAiimma_Roles(pageNo,pageSize);
    }
    else{
        try {
            res = await searchAiimma_Roles(search,pageNo,pageSize);
        } catch(err) {
             return { records:[], totalCount:0 }
        }
    }
    if(res && res.data){
    return res.data;
    }else{
    return { records:[], totalCount:0 }
    }
    
}


export const addAiimma_Roles = (data: any) => {
return APIService.api().post(`/aiimma_roles`,data)
}
export const updateAiimma_Roles = (RoleCode: any,data: any) => {
return APIService.api().patch(`/aiimma_roles/${RoleCode}`,data)
}
export const getAllAiimma_Roles = (pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_roles/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const getOneAiimma_Roles = (RoleCode: any) => {
return APIService.api().get(`/aiimma_roles/${RoleCode}`)
}
export const searchAiimma_Roles = (searchKey: any,pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_roles/search/${searchKey}/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const deleteAiimma_Roles = (RoleCode: any) => {
return APIService.api().delete(`/aiimma_roles/${RoleCode}`)
}
