import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface IAiimma_Bookingvw {
  BookingCode: string;
  JourneyCode?: string;
  Transporter_id: bigint;
  Driver_id?: bigint;
  Truck_id?: bigint;
  bt_hb_customer_id?: bigint;
  Container_MTU_No?: string;
  Container_Size?: string;
  Location_From?: string;
  Journey_Destination_Port?: string;
  Booking_Journey_Type?: string;
  Booking_Category: string;
  JourneyStartedDate?: Date;
  ExpectedDeliveryDate: Date;
  LockBooking?: string;
  TruckStayDuration?: string;
  BookingStatus?: string;
  CallCardAvatar?: string;
  CallCardUploadedAt?: string;
  BookingDate?: Date;
  BookingAmount?: bigint;
  Tran_FullName?: string;
  Dri_FullName: string;
  TruckName?: string;
  TruckPlateNumber?: string;
  TruckChasisNumber?: string;
  TruckColor?: string;
  TruckSize?: string;
  CompanyName?: string;
  LoggedIP: string;
  RecordCreatedAt: Date;
  RecordUpdatedAt?: Date;
}

interface IAiimma_BookingvwData {
  list?: Array<IAiimma_Bookingvw>;
  pageNo: number;
  pageSize: number;
  searchKey?: string;
  totalCount?: number;
  message?: string;
}

const initialState: IAiimma_BookingvwData = {
  pageNo: 1,
  pageSize: 20,
  searchKey: "",
  list: [],
  totalCount: 0,
  message: "",
};

const aiimma_BookingvwSlice = createSlice({
  name: "aiimma_Bookingvw",
  initialState,
  reducers: {
    setAiimma_BookingvwList: (
      state,
      _action: PayloadAction<IAiimma_BookingvwData>
    ) => {
      state.list = _action.payload.list;
      state.pageNo = _action.payload.pageNo;
      state.pageSize = _action.payload.pageSize;
      state.totalCount = _action.payload.totalCount;
    },
    resetAiimma_BookingvwToInit: (state) => {
      state = initialState;
    },
    setAiimma_BookingvwMessage: (state, _action: PayloadAction<string>) => {
      state.message = _action.payload;
    },
  },
});

export const {
  setAiimma_BookingvwList,
  resetAiimma_BookingvwToInit,
  setAiimma_BookingvwMessage,
} = aiimma_BookingvwSlice.actions;

export default aiimma_BookingvwSlice.reducer;
