import { APIService } from "services";

export const getAiimma_Truck_Maintenance_Journey_Booking_Details = async (pageNo: any,pageSize: any,search: string | any[]) => {
    let res;
    if(search.length===0) {
        res = await getAllAiimma_Truck_Maintenance_Journey_Booking_Details(pageNo,pageSize);
    }
    else{
        try {
            res = await searchAiimma_Truck_Maintenance_Journey_Booking_Details(search,pageNo,pageSize);
        } catch(err) {
             return { records:[], totalCount:0 }
        }
    }
    if(res && res.data){
    return res.data;
    }else{
    return { records:[], totalCount:0 }
    }
    
}


export const addAiimma_Truck_Maintenance_Journey_Booking_Details = (data: any) => {
return APIService.api().post(`/aiimma_truck_maintenance_journey_booking_details`,data)
}
export const updateAiimma_Truck_Maintenance_Journey_Booking_Details = (id: any,data: any) => {
return APIService.api().patch(`/aiimma_truck_maintenance_journey_booking_details/${id}`,data)
}
export const getAllAiimma_Truck_Maintenance_Journey_Booking_Details = (pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_truck_maintenance_journey_booking_details/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const getOneAiimma_Truck_Maintenance_Journey_Booking_Details = (id: any) => {
return APIService.api().get(`/aiimma_truck_maintenance_journey_booking_details/${id}`)
}
export const searchAiimma_Truck_Maintenance_Journey_Booking_Details = (searchKey: any,pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_truck_maintenance_journey_booking_details/search/${searchKey}/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const deleteAiimma_Truck_Maintenance_Journey_Booking_Details = (id: any) => {
return APIService.api().delete(`/aiimma_truck_maintenance_journey_booking_details/${id}`)
}
