import React, { useEffect, useState } from "react";
import DataTable from 'react-data-table-component';
import { useSelector } from "react-redux";
import { RootState } from "redux/reducers";
import { useAppDispatch } from "redux/store";
import { resetAiimma_TdosToInit, setAiimma_TdosMessage } from "redux/actions";
import { Button, Card, Col, Form, InputGroup } from "react-bootstrap";
import { Constant } from "template/Constant";
import ConfirmationModal from "template/ConfirmationModal";
import { deleteAiimma_Tdos } from "services/aiimma_tdosService";
type Props = {
    hideShowForm: (action: any) => void;
    handleRowEdit: (row: any) => void;
    getData: (page: any, pageSize: any, searchKey: any) => void;
};
export const Aiimma_TdosTable: React.FC<Props> = ({ hideShowForm, handleRowEdit, getData }) => {
    const dispatch = useAppDispatch();
    const [search, setSearch] = useState('');
    const [showDelete, setShowDelete] = useState(false);
    const [rowData, setRowData] = useState(undefined);
    const rData = useSelector((state: RootState) => state.aiimma_tdos);
    const handleSearch = () => {
        if (search.length > 0) {
            getData(Constant.defaultPageNumber, Constant.defaultPageSize, search);
        }
    }
    const handlePerRowsChange = async (newPerPage: any, page: any) => {
        await getData(page, newPerPage, '');
    }
    const handlePageChange = (page: any) => {
        getData(page, rData.pageSize, '');
    };
    const handleRowDeleteClick = (row: any) => {
        setRowData(row);
        setShowDelete(true);
    }
    useEffect(() => {
        if (rData && rData.list && rData.list.length === 0) {
            dispatch(resetAiimma_TdosToInit());
            getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
        }
    })
    const handleReset = () => {
        dispatch(resetAiimma_TdosToInit());
        getData(Constant.defaultPageNumber, rData.pageSize, '');
    }
    const handleServerDelete = async () => {
        if (rowData) {
            const response = await deleteAiimma_Tdos(rowData.ID);
            if (response) {
                dispatch(resetAiimma_TdosToInit());
                dispatch(setAiimma_TdosMessage("Deleted Successfully"));
                getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                setShowDelete(false);
            } else {
                dispatch(setAiimma_TdosMessage("Some error occured!"));
            }
        }
    }

    const handleRowSelection = (row: any) => {
        console.log(row); // Row Selection Functionality can be written here
    }
    const handleAddButtonClick = () => {
        dispatch(setAiimma_TdosMessage(''));
        hideShowForm('add');
    }

    const columns = [
        {name: 'ID', selector: (row: { ID: any; }) => row.ID, sortable: true},
{name: 'TDO Code', selector: (row: { TDOGeneralCode: any; }) => row.TDOGeneralCode, sortable: true},
{name: 'TDOIndividualCode', omit: true, selector: (row: { TDOIndividualCode: any; }) => row.TDOIndividualCode, sortable: true},
{name: 'Shipping Line', selector: (row: { ShippingLine: any; }) => row.ShippingLine, sortable: true},
{name: 'Container Number', selector: (row: { ContainerNumber: any; }) => row.ContainerNumber, sortable: true},
{name: 'Tonage', selector: (row: { Tonage: any; }) => row.Tonage, sortable: true},
{name: 'Tonage Details', selector: (row: { TonageDetails: any; }) => row.TonageDetails, sortable: true},
{name: 'Destination', selector: (row: { Destination: any; }) => row.Destination, sortable: true},
{name: 'Destination Details', selector: (row: { DestinationDetails: any; }) => row.DestinationDetails, sortable: true},
{name: 'Service Status', selector: (row: { ServiceStatus: any; }) => row.ServiceStatus, sortable: true},
{name: 'LoggedIP', omit: true, selector: (row: { LoggedIP: any; }) => row.LoggedIP, sortable: true},
{name: 'UploadedBy', omit: true, selector: (row: { UploadedBy: any; }) => row.UploadedBy, sortable: true},
{name: 'User_Uploaded_ID', omit: true, selector: (row: { User_Uploaded_ID: any; }) => row.User_Uploaded_ID, sortable: true},
{name: 'RecordCreatedAt', omit: true, selector: (row: { RecordCreatedAt: any; }) => row.RecordCreatedAt, sortable: true},

        {
            name: '',
            button: true,
            cell: (row: any) => <Button variant="link" className="btn-sm" onClick={() => handleRowEdit(row)}>Edit</Button>,
        },
        {
            name: '',
            button: true,
            cell: (row: any) => <Button variant="link" className="btn-sm" onClick={() => handleRowDeleteClick(row)}>Delete</Button>,
        },
    ];
    return (
        <Card className="shadow mb-4">
            <Card.Header className="py-3">
                <h3 className="m-0 font-weight-bold text-primary">TDOs List 
                    <Button variant="light" className="btn-circle btn-sm ml-2" onClick={handleReset}><i className="fa fa-refresh"></i></Button>
                    <Button className="btn-icon-split float-right" onClick={handleAddButtonClick}>
                        <span className="icon text-white-50">
                            <i className="fas fa-add"></i>
                        </span>
                        <span className="text">Add New</span>
                    </Button></h3>

            </Card.Header>
            <Card.Body>
                <Col className="ml-auto" md={3} xs={12} xl={3}>
                    <InputGroup>
                        <Form.Control
                            placeholder="Search"
                            aria-label="Search"
                            aria-describedby="basic-search"
                            onChange={(e) => setSearch(e.target.value)}
                        />
                        <Button disabled={search.length <= 2} variant="dark" className="rounded-0 rounded-right" id="button-search" onClick={handleSearch}>
                            Search
                        </Button>
                    </InputGroup>
                </Col>
                <div className="table-responsive">
                    <DataTable
                        selectableRows={true}
                        onSelectedRowsChange={handleRowSelection}
                        paginationPerPage={Constant.defaultPageNumber}
                        paginationRowsPerPageOptions={Constant.paginationRowsPerPageOptions}
                        columns={columns} data={rData.list}
                        onChangeRowsPerPage={handlePerRowsChange}
                        paginationTotalRows={rData.totalCount}
                        className="table table-bordered"
                        pagination
                        paginationServer
                        onChangePage={handlePageChange}></DataTable>
                </div>
            </Card.Body>
            <ConfirmationModal buttonNegative="Cancel" buttonPositive="Delete" title="Delete Confirmation" show={showDelete} body={"Are you sure?"} onNegative={() => setShowDelete(false)} onPositive={handleServerDelete} />
        </Card>
    );
}

