import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface IAiimma_Transporter_Truck_Junction {
ID:number,
transporter_id:number,
truck_id:number,
truck_in_use?:string,
connection_started?:Date,
connection_ended?:Date,
PrimaryTruck:string,
LoggedIP:string,
RecordCreationAt?:Date
}

interface IAiimma_Transporter_Truck_JunctionData {
    list?: Array<IAiimma_Transporter_Truck_Junction>;
    pageNo: number;
    pageSize: number;
    searchKey?: string;
    totalCount?: number;
    message?: string;
}

const initialState: IAiimma_Transporter_Truck_JunctionData = {
    pageNo: 1,
    pageSize: 20,
    searchKey: '',
    list: [],
    totalCount: 0,
    message: '',
};

const aiimma_transporter_truck_junctionSlice = createSlice({
    name: "aiimma_transporter_truck_junction",
    initialState,
    reducers: {
        setAiimma_Transporter_Truck_JunctionList: (state, _action: PayloadAction<IAiimma_Transporter_Truck_JunctionData>) => {
            state.list = _action.payload.list;
            state.pageNo = _action.payload.pageNo;
            state.pageSize = _action.payload.pageSize;
            state.totalCount = _action.payload.totalCount;
        },
        resetAiimma_Transporter_Truck_JunctionToInit: (state) => {
            state = initialState;
        },
        setAiimma_Transporter_Truck_JunctionMessage: (state, _action: PayloadAction<string>) => {
            state.message = _action.payload;
        },
    },
});

export const { setAiimma_Transporter_Truck_JunctionList, resetAiimma_Transporter_Truck_JunctionToInit, setAiimma_Transporter_Truck_JunctionMessage } = aiimma_transporter_truck_junctionSlice.actions;

export default aiimma_transporter_truck_junctionSlice.reducer;

