import { APIService } from "services";

export const getAiimma_Booking_Payments = async (pageNo: any,pageSize: any,search: string | any[]) => {
    let res;
    if(search.length===0) {
        res = await getAllAiimma_Booking_Payments(pageNo,pageSize);
    }
    else{
        try {
            res = await searchAiimma_Booking_Payments(search,pageNo,pageSize);
        } catch(err) {
             return { records:[], totalCount:0 }
        }
    }
    if(res && res.data){
    return res.data;
    }else{
    return { records:[], totalCount:0 }
    }
    
}


export const addAiimma_Booking_Payments = (data: any) => {
return APIService.api().post(`/aiimma_booking_payments`,data)
}
export const updateAiimma_Booking_Payments = (ID: any,data: any) => {
return APIService.api().patch(`/aiimma_booking_payments/${ID}`,data)
}
export const getAllAiimma_Booking_Payments = (pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_booking_payments/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const getOneAiimma_Booking_Payments = (ID: any) => {
return APIService.api().get(`/aiimma_booking_payments/${ID}`)
}
export const searchAiimma_Booking_Payments = (searchKey: any,pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_booking_payments/search/${searchKey}/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const deleteAiimma_Booking_Payments = (ID: any) => {
return APIService.api().delete(`/aiimma_booking_payments/${ID}`)
}
