import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface IAiimma_Booking_Payments {
ID:number,
booking_id:number,
bookingDate:Date,
bookingAmount:number,
LoggedIP:string,
RecordCreatedAt:Date,
RecordUpdatedAt?:Date
}

interface IAiimma_Booking_PaymentsData {
    list?: Array<IAiimma_Booking_Payments>;
    pageNo: number;
    pageSize: number;
    searchKey?: string;
    totalCount?: number;
    message?: string;
}

const initialState: IAiimma_Booking_PaymentsData = {
    pageNo: 1,
    pageSize: 20,
    searchKey: '',
    list: [],
    totalCount: 0,
    message: '',
};

const aiimma_booking_paymentsSlice = createSlice({
    name: "aiimma_booking_payments",
    initialState,
    reducers: {
        setAiimma_Booking_PaymentsList: (state, _action: PayloadAction<IAiimma_Booking_PaymentsData>) => {
            state.list = _action.payload.list;
            state.pageNo = _action.payload.pageNo;
            state.pageSize = _action.payload.pageSize;
            state.totalCount = _action.payload.totalCount;
        },
        resetAiimma_Booking_PaymentsToInit: (state) => {
            state = initialState;
        },
        setAiimma_Booking_PaymentsMessage: (state, _action: PayloadAction<string>) => {
            state.message = _action.payload;
        },
    },
});

export const { setAiimma_Booking_PaymentsList, resetAiimma_Booking_PaymentsToInit, setAiimma_Booking_PaymentsMessage } = aiimma_booking_paymentsSlice.actions;

export default aiimma_booking_paymentsSlice.reducer;

