import React, { useState } from "react";
import { Alert, Button } from "react-bootstrap";
import { useSelector } from "react-redux";
import { setAiimma__Users_Access_AuditingList, setAiimma__Users_Access_AuditingMessage } from "redux/actions";
import { RootState } from "redux/reducers";
import { useAppDispatch } from "redux/store";
import { getAiimma__Users_Access_Auditing } from "services/aiimma__users_access_auditingService";
import Layout from "template";
import { Constant } from "template/Constant";
import { Aiimma__Users_Access_AuditingForm } from "./form";
import { Aiimma__Users_Access_AuditingTable } from "./table";

export const Aiimma__Users_Access_Auditing: React.FC = () => {
  const dispatch = useAppDispatch();
  const rData = useSelector((state: RootState) => state.aiimma__users_access_auditing);
  const [row, setRow] = useState(undefined);
  const [action, setAction] = useState('');
  const getData = (page: number, pageSize: number, searchKey: any) => {
    getAiimma__Users_Access_Auditing(page, pageSize, searchKey).then((response) => {
      if (response && response.records) {
        dispatch(setAiimma__Users_Access_AuditingList({ pageNo: page, pageSize: pageSize, list: response.records, totalCount: response.totalCount, searchKey: searchKey }));
      } else {
        dispatch(setAiimma__Users_Access_AuditingMessage("No Record Found"));
      }
    })
  }

  const handleRowEdit = (rowData: any) => {
    setRow(rowData);
    dispatch(setAiimma__Users_Access_AuditingMessage(''));
    setAction('edit');
  }
  return (
    <Layout>
      <div className="container-fluid">
        <div className="d-sm-flex align-items-center justify-content-between mb-4">
          <h1 className="h3 mb-0 text-gray-800">Aiimma__Users_Access_Auditing</h1>
        </div>
        <div className="d-flex flex-column min-vh-100">
          {rData.message ?
            <Alert variant={Constant.defaultAlertVarient} className="alert-dismissible fade">{rData.message}
              <Button type="button" className="close" data-dismiss="alert" aria-label="Close" onClick={() => dispatch(setAiimma__Users_Access_AuditingMessage(''))}>
                <span aria-hidden="true">&times;</span>
              </Button>
            </Alert> : null}
          {action ? <Aiimma__Users_Access_AuditingForm hideShowForm={setAction} action={action} row={row} getData={getData} /> :
            <Aiimma__Users_Access_AuditingTable handleRowEdit={handleRowEdit} hideShowForm={setAction} getData={getData} />}
        </div>

      </div>
    </Layout >
  );
};

