import React, { useEffect, useState } from "react";
import DataTable from "react-data-table-component";
import { useSelector } from "react-redux";
import { RootState } from "redux/reducers";
import { useAppDispatch } from "redux/store";
import {
  resetAiimma_BookingvwToInit,
  setAiimma_BookingvwMessage,
} from "redux/actions";
import {
  resetAiima_Transporter_Driver_Bthb_BookingToInit,
  setAiima_Transporter_Driver_Bthb_BookingMessage,
} from "redux/actions";
import { Button, Card, Col, Form, InputGroup } from "react-bootstrap";
import { Constant } from "template/Constant";
import ConfirmationModal from "template/ConfirmationModal";
import { deleteAiimma_Bookingvw } from "services/aiimma_bookingvwService";
type Props = {
  hideShowForm: (action: any) => void;
  handleRowEdit: (row: any) => void;
  getData: (page: any, pageSize: any, searchKey: any) => void;
};
export const Aiima_Transporter_Driver_Bthb_BookingTable: React.FC<Props> = ({
  hideShowForm,
  handleRowEdit,
  getData,
}) => {
  const dispatch = useAppDispatch();
  const [search, setSearch] = useState("");
  const [showDelete, setShowDelete] = useState(false);
  const [rowData, setRowData] = useState(undefined);
  const rData = useSelector((state: RootState) => state.aiimma_bookingvw);
  const handleSearch = () => {
    if (search.length > 0) {
      getData(Constant.defaultPageNumber, Constant.defaultPageSize, search);
    }
  };
  const handlePerRowsChange = async (newPerPage: any, page: any) => {
    await getData(page, newPerPage, "");
  };
  const handlePageChange = (page: any) => {
    getData(page, rData.pageSize, "");
  };
  const handleRowDeleteClick = (row: any) => {
    setRowData(row);
    setShowDelete(true);
  };
  useEffect(() => {
    if (rData && rData.list && rData.list.length === 0) {
      dispatch(resetAiimma_BookingvwToInit());
      getData(Constant.defaultPageNumber, Constant.defaultPageSize, "");
    }
  });
  const handleReset = () => {
    dispatch(resetAiimma_BookingvwToInit());
    getData(Constant.defaultPageNumber, rData.pageSize, "");
  };
  const handleServerDelete = async () => {
    if (rowData) {
      const response = await deleteAiimma_Bookingvw(
        rowData.ID
      );
      if (response) {
        dispatch(resetAiimma_BookingvwToInit());
        dispatch(
          setAiimma_BookingvwMessage(
            "Deleted Successfully"
          )
        );
        getData(Constant.defaultPageNumber, Constant.defaultPageSize, "");
        setShowDelete(false);
      } else {
        dispatch(
          setAiimma_BookingvwMessage("Some error occured!")
        );
      }
    }
  };

  const handleRowSelection = (row: any) => {
    console.log(row); // Row Selection Functionality can be written here
  };
  const handleAddButtonClick = () => {
    dispatch(setAiima_Transporter_Driver_Bthb_BookingMessage(""));
    hideShowForm("add");
  };

  const columns = [
    { name: "ID", omit:true, selector: (row: { ID: any; }) => row.ID, sortable: true },
    { name: "Booking Code", selector: (row: { BookingCode: any; }) => row.BookingCode, sortable: true },
    { name: "Journey Code", selector: (row: { JourneyCode: any; }) => row.JourneyCode, sortable: true },
    {
      name: "Transporter", omit: true,
      selector: (row: { Transporter_id: any; }) => row.Transporter_id,
      sortable: true,
    },
    {
      name: "Transporter",
      selector: (row: { Tran_FullName: any; }) => row.Tran_FullName,
      sortable: true,
    },
    { name: "Driver", omit:true, selector: (row: { Driver_id: any; }) => row.Driver_id, sortable: true },
    { name: "Driver", selector: (row: { Dri_FullName: any; }) => row.Dri_FullName, sortable: true },
    {
      name: "Holding Bay / Terminal", omit: true,
      selector: (row: { bt_hb_customer_id: any; }) => row.bt_hb_customer_id,
      sortable: true,
    },
    {
      name: "Holding Bay / Bonded Terminal",
      selector: (row: { CompanyName: any; }) => row.CompanyName,
      sortable: true,
    },
    { name: "Truck", omit:true, selector: (row: { Truck_id: any; }) => row.Truck_id, sortable: true },
    { name: "Truck Name", selector: (row: { TruckName: any; }) => row.TruckName, sortable: true },
    { name: "Truck Plate No.", selector: (row: { TruckPlateNumber: any; }) => row.TruckPlateNumber, sortable: true },
    {
      name: "Container MTU No",
      selector: (row: { Container_MTU_No: any; }) => row.Container_MTU_No,
      sortable: true,
    },
    {
      name: "Container Size",
      selector: (row: { Container_Size: any; }) => row.Container_Size,
      sortable: true,
    },
    {
      name: "Movement From",
      selector: (row: { Location_From: any; }) => row.Location_From,
      sortable: true,
    },
    {
      name: "Destination Port",
      selector: (row: { Journey_Destination_Port: any; }) => row.Journey_Destination_Port,
      sortable: true,
    },
    {
      name: "Journey Type",
      selector: (row: { Booking_Journey_Type: any; }) => row.Booking_Journey_Type,
      sortable: true,
    },
    {
      name: "Booking_Category", omit: true,
      selector: (row: { Booking_Category: any; }) => row.Booking_Category,
      sortable: true,
    },
    {
      name: "Started On",
      selector: (row: { JourneyStartedDate: any; }) => row.JourneyStartedDate,
      sortable: true,
    },
    {
      name: "Delivery Expected On",
      selector: (row: { ExpectedDeliveryDate: any; }) => row.ExpectedDeliveryDate,
      sortable: true,
    },
    { name: "Booking Locked?", selector: (row: { LockBooking: any; }) => row.LockBooking, sortable: true },
    {
      name: "TruckStayDuration", omit: true,
      selector: (row: { TruckStayDuration: any; }) => row.TruckStayDuration,
      sortable: true,
    },
    {
      name: "Status",
      selector: (row: { BookingStatus: any; }) => row.BookingStatus,
      sortable: true,
    },
    {
      name: "CallCardAvatar", omit: true,
      selector: (row: { CallCardAvatar: any; }) => row.CallCardAvatar,
      sortable: true,
    },
    {
      name: "CallCardUploadedAt", omit: true,
      selector: (row: { CallCardUploadedAt: any; }) => row.CallCardUploadedAt,
      sortable: true,
    },
    { name: "LoggedIP", omit:true, selector: (row: { LoggedIP: any; }) => row.LoggedIP, sortable: true },
    {
      name: "RecordCreatedAt", omit:true,
      selector: (row: { RecordCreatedAt: any; }) => row.RecordCreatedAt,
      sortable: true,
    },
    {
      name: "RecordUpdatedAt", omit:true,
      selector: (row: { RecordUpdatedAt: any; }) => row.RecordUpdatedAt,
      sortable: true,
    },

    {
      name: "",
      button: true,
      cell: (row: any) => (
        <Button
          variant="link"
          className="btn-sm"
          onClick={() => handleRowEdit(row)}
        >
          Edit
        </Button>
      ),
    },
    {
      name: "",
      button: true,
      cell: (row: any) => (
        <Button
          variant="link"
          className="btn-sm"
          onClick={() => handleRowDeleteClick(row)}
        >
          Delete
        </Button>
      ),
    },
  ];
  return (
    <Card className="shadow mb-4">
      <Card.Header className="py-3">
        <h3 className="m-0 font-weight-bold text-primary">
          Booking Details List ({rData.totalCount} total records)
          <Button
            variant="light"
            className="btn-circle btn-sm ml-2"
            onClick={handleReset}
          >
            <i className="fa fa-refresh"></i>
          </Button>
          <Button
            className="btn-icon-split float-right"
            onClick={handleAddButtonClick}
          >
            <span className="icon text-white-50">
              <i className="fas fa-add"></i>
            </span>
            <span className="text">Make Booking</span>
          </Button>
        </h3>
      </Card.Header>
      <Card.Body>
        <Col className="ml-auto" md={3} xs={12} xl={3}>
          <InputGroup>
            <Form.Control
              placeholder="Search"
              aria-label="Search"
              aria-describedby="basic-search"
              onChange={(e) => setSearch(e.target.value)}
            />
            <Button
              disabled={search.length <= 2}
              variant="dark"
              className="rounded-0 rounded-right"
              id="button-search"
              onClick={handleSearch}
            >
              Search
            </Button>
          </InputGroup>
        </Col>
        <div className="table-responsive">
          <DataTable
            selectableRows={true}
            onSelectedRowsChange={handleRowSelection}
            paginationPerPage={Constant.defaultPageNumber}
            paginationRowsPerPageOptions={Constant.paginationRowsPerPageOptions}
            columns={columns}
            data={rData.list}
            onChangeRowsPerPage={handlePerRowsChange}
            paginationTotalRows={rData.totalCount}
            className="table table-bordered"
            pagination
            paginationServer
            onChangePage={handlePageChange}
          ></DataTable>
        </div>
      </Card.Body>
      <ConfirmationModal
        buttonNegative="Cancel"
        buttonPositive="Delete"
        title="Delete Confirmation"
        show={showDelete}
        body={"Are you sure?"}
        onNegative={() => setShowDelete(false)}
        onPositive={handleServerDelete}
      />
    </Card>
  );
};
