import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface IAiimma_Roles {
RoleCode:string,
RoleName:string,
Description:string,
LoggedIP:string,
RecordCreatedAt:Date
}

interface IAiimma_RolesData {
    list?: Array<IAiimma_Roles>;
    pageNo: number;
    pageSize: number;
    searchKey?: string;
    totalCount?: number;
    message?: string;
}

const initialState: IAiimma_RolesData = {
    pageNo: 1,
    pageSize: 20,
    searchKey: '',
    list: [],
    totalCount: 0,
    message: '',
};

const aiimma_rolesSlice = createSlice({
    name: "aiimma_roles",
    initialState,
    reducers: {
        setAiimma_RolesList: (state, _action: PayloadAction<IAiimma_RolesData>) => {
            state.list = _action.payload.list;
            state.pageNo = _action.payload.pageNo;
            state.pageSize = _action.payload.pageSize;
            state.totalCount = _action.payload.totalCount;
        },
        resetAiimma_RolesToInit: (state) => {
            state = initialState;
        },
        setAiimma_RolesMessage: (state, _action: PayloadAction<string>) => {
            state.message = _action.payload;
        },
    },
});

export const { setAiimma_RolesList, resetAiimma_RolesToInit, setAiimma_RolesMessage } = aiimma_rolesSlice.actions;

export default aiimma_rolesSlice.reducer;

