import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface IAiimma_Membersaccess {
ID:number,
login_id:number,
Login_Role:string,
Pwd:string,
EmailID:string,
emailValidated:string,
emailActivationCode?:string,
PhoneNo:string,
phoneValidated:string,
phoneOneTimeValidationCode?:string,
phoneOneTimeValidationCodeSentAt?:Date,
phoneOneTimeValidationCodeValidPeriod?:number,
phoneLoginValidationCode?:string,
loginValidationCodeSentAt?:Date,
loginValidationCodeValidPeriod?:number,
RoleCode:string,
LoginStatus:string,
LoggedIP:string,
RecorCreatedAt:Date
}

interface IAiimma_MembersaccessData {
    list?: Array<IAiimma_Membersaccess>;
    pageNo: number;
    pageSize: number;
    searchKey?: string;
    totalCount?: number;
    message?: string;
}

const initialState: IAiimma_MembersaccessData = {
    pageNo: 1,
    pageSize: 20,
    searchKey: '',
    list: [],
    totalCount: 0,
    message: '',
};

const aiimma_membersaccessSlice = createSlice({
    name: "aiimma_membersaccess",
    initialState,
    reducers: {
        setAiimma_MembersaccessList: (state, _action: PayloadAction<IAiimma_MembersaccessData>) => {
            state.list = _action.payload.list;
            state.pageNo = _action.payload.pageNo;
            state.pageSize = _action.payload.pageSize;
            state.totalCount = _action.payload.totalCount;
        },
        resetAiimma_MembersaccessToInit: (state) => {
            state = initialState;
        },
        setAiimma_MembersaccessMessage: (state, _action: PayloadAction<string>) => {
            state.message = _action.payload;
        },
    },
});

export const { setAiimma_MembersaccessList, resetAiimma_MembersaccessToInit, setAiimma_MembersaccessMessage } = aiimma_membersaccessSlice.actions;

export default aiimma_membersaccessSlice.reducer;

