import { useFormik } from "formik";
import React from "react";

import { Button, Card, Form } from "react-bootstrap";
import { setAiimma_Bt_Hb_CustomersMessage } from "redux/actions";


import { useAppDispatch } from "redux/store";
import { addAiimma_Bt_Hb_Customers, updateAiimma_Bt_Hb_Customers } from "services/aiimma_bt_hb_customersService";
import { Constant } from "template/Constant";
import * as yup from 'yup';
type Props = {
    row?: any,
    hideShowForm: (actionName: any) => void;
    getData: (page: any, pageSize: any, searchKey: any) => void;
    action?: string
};
export const Aiimma_Bt_Hb_CustomersForm: React.FC<Props> = ({ row, hideShowForm, getData, action }) => {
    const dispatch = useAppDispatch();
    const iValue={ID:'',CustomerID:'',CompanyName:'',CompanyRegCertNo:'',CustomerType:'',Address:'',Phone:'',WhatsApp_Phone:'',Email:'',Website:'',NPA_Approved:'',Customer_Logo_Avatar:'',Company_Cert_Avatar:'',LoggedIP:'',RecordCreatedAt:'',RecordUpdatedAt:''};
    const initialValue = action === 'edit' ? row : iValue;
    
    
    const formik = useFormik({
        initialValues: initialValue,
        onSubmit: async (values) => {
            if (action === 'edit') {
                const response = await updateAiimma_Bt_Hb_Customers(values.ID,values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_Bt_Hb_CustomersMessage("Updated Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_Bt_Hb_CustomersMessage("Some error occured!"));
                }
            } else if (action === 'add') {
                const response = await addAiimma_Bt_Hb_Customers(values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_Bt_Hb_CustomersMessage("Added Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_Bt_Hb_CustomersMessage("Some error occured!"));
                }
            }
        },
        validationSchema: yup.object({
           CustomerID: yup.string(),
CompanyName: yup.string().required('Holding Bay / Terminal name is required'),
CompanyRegCertNo: yup.string(),
CustomerType: yup.string(),
Address: yup.string(),
Phone: yup.string(),
WhatsApp_Phone: yup.string(),
Email: yup.string(),
Website: yup.string(),
NPA_Approved: yup.string(),
Customer_Logo_Avatar: yup.string(),
Company_Cert_Avatar: yup.string(),
LoggedIP: yup.string(),
RecordCreatedAt: yup.date(),
RecordUpdatedAt: yup.date(),

        }),
    });
    return (
        <Card className="shadow mb-4">
            <Card.Header className="py-3">
                <h6 className="m-0 font-weight-bold text-primary text-capitalize">{action} Holding Bay / Bonded Terminal
                    <Button className="btn-icon-split float-right" onClick={() => hideShowForm(false)}>
                        <span className="icon text-white-50">
                            <i className="fas fa-list"></i>
                        </span>
                        <span className="text">View Holding Bay/Terminal</span>
                    </Button>
                </h6>

            </Card.Header>
            <Card.Body>
                <Form onSubmit={formik.handleSubmit}>
                  <Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="CustomerID" className="form-control" value={formik.values.CustomerID}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.CustomerID && !!formik.errors.CustomerID}
isValid ={!!formik.touched.CustomerID && !formik.errors.CustomerID}
></Form.Control>
{
    formik.errors.CustomerID && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.CustomerID}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Company Name</label>
<Form.Control type="text" name="CompanyName" className="form-control" value={formik.values.CompanyName}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.CompanyName && !!formik.errors.CompanyName}
isValid ={!!formik.touched.CompanyName && !formik.errors.CompanyName}
></Form.Control>
{
    formik.errors.CompanyName && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.CompanyName}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Registered Cert. No</label>
<Form.Control type="hidden" name="CompanyRegCertNo" className="form-control" value={formik.values.CompanyRegCertNo}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.CompanyRegCertNo && !!formik.errors.CompanyRegCertNo}
isValid ={!!formik.touched.CompanyRegCertNo && !formik.errors.CompanyRegCertNo}
></Form.Control>
{
    formik.errors.CompanyRegCertNo && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.CompanyRegCertNo}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Operation Type</label>
<Form.Control type="text" name="CustomerType" placeholder="e.g Holding Bay or Terminal" className="form-control" value={formik.values.CustomerType}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.CustomerType && !!formik.errors.CustomerType}
isValid ={!!formik.touched.CustomerType && !formik.errors.CustomerType}
></Form.Control>
{
    formik.errors.CustomerType && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.CustomerType}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Operation Address</label>
<Form.Control type="text" name="Address" className="form-control" value={formik.values.Address}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Address && !!formik.errors.Address}
isValid ={!!formik.touched.Address && !formik.errors.Address}
></Form.Control>
{
    formik.errors.Address && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Address}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Phone No.</label>
<Form.Control type="text" name="Phone" className="form-control" value={formik.values.Phone}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Phone && !!formik.errors.Phone}
isValid ={!!formik.touched.Phone && !formik.errors.Phone}
></Form.Control>
{
    formik.errors.Phone && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Phone}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">WhatsApp Phone No(Optional)</label>
<Form.Control type="text" name="WhatsApp_Phone" className="form-control" value={formik.values.WhatsApp_Phone}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.WhatsApp_Phone && !!formik.errors.WhatsApp_Phone}
isValid ={!!formik.touched.WhatsApp_Phone && !formik.errors.WhatsApp_Phone}
></Form.Control>
{
    formik.errors.WhatsApp_Phone && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.WhatsApp_Phone}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Email</label>
<Form.Control type="text" name="Email" className="form-control" value={formik.values.Email}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Email && !!formik.errors.Email}
isValid ={!!formik.touched.Email && !formik.errors.Email}
></Form.Control>
{
    formik.errors.Email && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Email}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Website(Optional)</label>
<Form.Control type="text" name="Website" className="form-control" value={formik.values.Website}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Website && !!formik.errors.Website}
isValid ={!!formik.touched.Website && !formik.errors.Website}
></Form.Control>
{
    formik.errors.Website && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Website}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">NPA Approved</label>
<Form.Control type="text" name="NPA_Approved" placeholder="e.g NO or YES" className="form-control" value={formik.values.NPA_Approved}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.NPA_Approved && !!formik.errors.NPA_Approved}
isValid ={!!formik.touched.NPA_Approved && !formik.errors.NPA_Approved}
></Form.Control>
{
    formik.errors.NPA_Approved && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.NPA_Approved}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Company Logo Picture(Optional)</label>
<Form.Control type="text" name="Customer_Logo_Avatar" className="form-control" value={formik.values.Customer_Logo_Avatar}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Customer_Logo_Avatar && !!formik.errors.Customer_Logo_Avatar}
isValid ={!!formik.touched.Customer_Logo_Avatar && !formik.errors.Customer_Logo_Avatar}
></Form.Control>
{
    formik.errors.Customer_Logo_Avatar && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Customer_Logo_Avatar}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Company Certificate Picture(Optional)</label>
<Form.Control type="text" name="Company_Cert_Avatar" className="form-control" value={formik.values.Company_Cert_Avatar}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Company_Cert_Avatar && !!formik.errors.Company_Cert_Avatar}
isValid ={!!formik.touched.Company_Cert_Avatar && !formik.errors.Company_Cert_Avatar}
></Form.Control>
{
    formik.errors.Company_Cert_Avatar && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Company_Cert_Avatar}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="LoggedIP" className="form-control" value={formik.values.LoggedIP}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.LoggedIP && !!formik.errors.LoggedIP}
isValid ={!!formik.touched.LoggedIP && !formik.errors.LoggedIP}
></Form.Control>
{
    formik.errors.LoggedIP && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.LoggedIP}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="RecordCreatedAt" className="form-control" value={formik.values.RecordCreatedAt}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.RecordCreatedAt && !!formik.errors.RecordCreatedAt}
isValid ={!!formik.touched.RecordCreatedAt && !formik.errors.RecordCreatedAt}
></Form.Control>
{
    formik.errors.RecordCreatedAt && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.RecordCreatedAt}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="RecordUpdatedAt" className="form-control" value={formik.values.RecordUpdatedAt}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.RecordUpdatedAt && !!formik.errors.RecordUpdatedAt}
isValid ={!!formik.touched.RecordUpdatedAt && !formik.errors.RecordUpdatedAt}
></Form.Control>
{
    formik.errors.RecordUpdatedAt && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.RecordUpdatedAt}
    </Form.Control.Feedback>
)}
</Form.Group>

                    <Form.Group>
                        <Button type="submit" className="float-right" variant="primary">Save</Button>
                    </Form.Group>
                </Form>
            </Card.Body>
        </Card>
    );
}

