import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface IAiimma__Users_Access_Auditing {
ID:number,
LoginID:number,
UserRole:string,
Logged_In_At?:Date,
Logged_Out_At?:Date,
Logged_In_Ip?:string,
Logged_Out_Ip?:string,
LoginTrackingToken?:string,
LogoutTrackingToken?:string,
RecordCreatedAt:Date
}

interface IAiimma__Users_Access_AuditingData {
    list?: Array<IAiimma__Users_Access_Auditing>;
    pageNo: number;
    pageSize: number;
    searchKey?: string;
    totalCount?: number;
    message?: string;
}

const initialState: IAiimma__Users_Access_AuditingData = {
    pageNo: 1,
    pageSize: 20,
    searchKey: '',
    list: [],
    totalCount: 0,
    message: '',
};

const aiimma__users_access_auditingSlice = createSlice({
    name: "aiimma__users_access_auditing",
    initialState,
    reducers: {
        setAiimma__Users_Access_AuditingList: (state, _action: PayloadAction<IAiimma__Users_Access_AuditingData>) => {
            state.list = _action.payload.list;
            state.pageNo = _action.payload.pageNo;
            state.pageSize = _action.payload.pageSize;
            state.totalCount = _action.payload.totalCount;
        },
        resetAiimma__Users_Access_AuditingToInit: (state) => {
            state = initialState;
        },
        setAiimma__Users_Access_AuditingMessage: (state, _action: PayloadAction<string>) => {
            state.message = _action.payload;
        },
    },
});

export const { setAiimma__Users_Access_AuditingList, resetAiimma__Users_Access_AuditingToInit, setAiimma__Users_Access_AuditingMessage } = aiimma__users_access_auditingSlice.actions;

export default aiimma__users_access_auditingSlice.reducer;

