import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface IAiimma_Customers_Deposit_Balance {
ID:number,
customer_id:number,
customer_role:string,
AccountBalance:number,
FirstTimeBalance:string,
LoggedIP:string
}

interface IAiimma_Customers_Deposit_BalanceData {
    list?: Array<IAiimma_Customers_Deposit_Balance>;
    pageNo: number;
    pageSize: number;
    searchKey?: string;
    totalCount?: number;
    message?: string;
}

const initialState: IAiimma_Customers_Deposit_BalanceData = {
    pageNo: 1,
    pageSize: 20,
    searchKey: '',
    list: [],
    totalCount: 0,
    message: '',
};

const aiimma_customers_deposit_balanceSlice = createSlice({
    name: "aiimma_customers_deposit_balance",
    initialState,
    reducers: {
        setAiimma_Customers_Deposit_BalanceList: (state, _action: PayloadAction<IAiimma_Customers_Deposit_BalanceData>) => {
            state.list = _action.payload.list;
            state.pageNo = _action.payload.pageNo;
            state.pageSize = _action.payload.pageSize;
            state.totalCount = _action.payload.totalCount;
        },
        resetAiimma_Customers_Deposit_BalanceToInit: (state) => {
            state = initialState;
        },
        setAiimma_Customers_Deposit_BalanceMessage: (state, _action: PayloadAction<string>) => {
            state.message = _action.payload;
        },
    },
});

export const { setAiimma_Customers_Deposit_BalanceList, resetAiimma_Customers_Deposit_BalanceToInit, setAiimma_Customers_Deposit_BalanceMessage } = aiimma_customers_deposit_balanceSlice.actions;

export default aiimma_customers_deposit_balanceSlice.reducer;

