import { APIService } from "services";

export const getAiimma_Holdingbay_Containers_Upload = async (pageNo: any,pageSize: any,search: string | any[]) => {
    let res;
    if(search.length===0) {
        res = await getAllAiimma_Holdingbay_Containers_Upload(pageNo,pageSize);
    }
    else{
        try {
            res = await searchAiimma_Holdingbay_Containers_Upload(search,pageNo,pageSize);
        } catch(err) {
             return { records:[], totalCount:0 }
        }
    }
    if(res && res.data){
    return res.data;
    }else{
    return { records:[], totalCount:0 }
    }
    
}


export const addAiimma_Holdingbay_Containers_Upload = (data: any) => {
return APIService.api().post(`/aiimma_holdingbay_containers_upload`,data)
}
export const updateAiimma_Holdingbay_Containers_Upload = (ID: any,data: any) => {
return APIService.api().patch(`/aiimma_holdingbay_containers_upload/${ID}`,data)
}
export const getAllAiimma_Holdingbay_Containers_Upload = (pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_holdingbay_containers_upload/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const getOneAiimma_Holdingbay_Containers_Upload = (ID: any) => {
return APIService.api().get(`/aiimma_holdingbay_containers_upload/${ID}`)
}
export const searchAiimma_Holdingbay_Containers_Upload = (searchKey: any,pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_holdingbay_containers_upload/search/${searchKey}/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const deleteAiimma_Holdingbay_Containers_Upload = (ID: any) => {
return APIService.api().delete(`/aiimma_holdingbay_containers_upload/${ID}`)
}
