import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface IAiimma_Transporter_Driver_Junction {
ID:number,
transporter_id:number,
driver_id:number,
driver_still_active?:string,
connection_started?:Date,
connection_ended?:Date,
LoggedIP:string,
RecordCreationAt:Date
}

interface IAiimma_Transporter_Driver_JunctionData {
    list?: Array<IAiimma_Transporter_Driver_Junction>;
    pageNo: number;
    pageSize: number;
    searchKey?: string;
    totalCount?: number;
    message?: string;
}

const initialState: IAiimma_Transporter_Driver_JunctionData = {
    pageNo: 1,
    pageSize: 20,
    searchKey: '',
    list: [],
    totalCount: 0,
    message: '',
};

const aiimma_transporter_driver_junctionSlice = createSlice({
    name: "aiimma_transporter_driver_junction",
    initialState,
    reducers: {
        setAiimma_Transporter_Driver_JunctionList: (state, _action: PayloadAction<IAiimma_Transporter_Driver_JunctionData>) => {
            state.list = _action.payload.list;
            state.pageNo = _action.payload.pageNo;
            state.pageSize = _action.payload.pageSize;
            state.totalCount = _action.payload.totalCount;
        },
        resetAiimma_Transporter_Driver_JunctionToInit: (state) => {
            state = initialState;
        },
        setAiimma_Transporter_Driver_JunctionMessage: (state, _action: PayloadAction<string>) => {
            state.message = _action.payload;
        },
    },
});

export const { setAiimma_Transporter_Driver_JunctionList, resetAiimma_Transporter_Driver_JunctionToInit, setAiimma_Transporter_Driver_JunctionMessage } = aiimma_transporter_driver_junctionSlice.actions;

export default aiimma_transporter_driver_junctionSlice.reducer;

