import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface IAiimma_Notifyusers {
ID:number,
login_id:number,
Login_Role:string,
Pwd:string,
EmailID:string,
emailValidated:string,
emailActivationCode?:string,
PhoneNo:string,
phoneValidated:string,
phoneOneTimeValidationCode?:string,
phoneOneTimeValidationCodeSentAt?:Date,
phoneOneTimeValidationCodeValidPeriod?:number,
phoneLoginValidationCode?:string,
loginValidationCodeSentAt?:Date,
loginValidationCodeValidPeriod?:number,
RoleCode:string,
LoginStatus:string,
LoggedIP:string,
RecorCreatedAt:Date
}

interface IAiimma_NotifyusersData {
    list?: Array<IAiimma_Notifyusers>;
    pageNo: number;
    pageSize: number;
    searchKey?: string;
    totalCount?: number;
    message?: string;
}

const initialState: IAiimma_NotifyusersData = {
    pageNo: 1,
    pageSize: 20,
    searchKey: '',
    list: [],
    totalCount: 0,
    message: '',
};

const Aiimma_NotifyusersSlice = createSlice({
    name: "Aiimma_Notifyusers",
    initialState,
    reducers: {
        setAiimma_NotifyusersList: (state, _action: PayloadAction<IAiimma_NotifyusersData>) => {
            state.list = _action.payload.list;
            state.pageNo = _action.payload.pageNo;
            state.pageSize = _action.payload.pageSize;
            state.totalCount = _action.payload.totalCount;
        },
        resetAiimma_NotifyusersToInit: (state) => {
            state = initialState;
        },
        setAiimma_NotifyusersMessage: (state, _action: PayloadAction<string>) => {
            state.message = _action.payload;
        },
    },
});

export const { setAiimma_NotifyusersList, resetAiimma_NotifyusersToInit, setAiimma_NotifyusersMessage } = Aiimma_NotifyusersSlice.actions;

export default Aiimma_NotifyusersSlice.reducer;

