import React, { useEffect } from 'react';
import { Button, Card, Form } from "react-bootstrap";
import { useSelector } from "react-redux";
import { RootState } from "redux/reducers";

import { resetAiimma_Bt_Hb_CustomersToInit, setAiimma_Bt_Hb_CustomersList } from "redux/actions";
import { resetAiimma_TrucksToInit, setAiimma_TrucksList } from "redux/actions";
import { resetAiimma_DriversToInit, setAiimma_DriversList } from "redux/actions";
import { useAppDispatch } from "redux/store";

import { getAiimma_Transporters } from "services/aiimma_transportersService";
import { getAiimma_Drivers } from "services/aiimma_driversService"
import { getAiimma_Bt_Hb_Customers } from "services/aiimma_bt_hb_customersService";
import { getAiimma_Trucks } from "services/aiimma_trucksService";
import { Constant } from "template/Constant";

const DriverBayTruckDetails: React.FC = () => {
    const dispatch = useAppDispatch();

    const aiimma_bt_hb_customersData = useSelector((state: RootState) => state.aiimma_bt_hb_customers);

    const aiimma_trucksData = useSelector((state: RootState) => state.aiimma_trucks);

    const aiimma_driversData = useSelector((state: RootState) => state.aiimma_drivers);

    useEffect(() => {
        if (aiimma_driversData && aiimma_driversData.list && aiimma_driversData.list.length === 0) {
                    dispatch(resetAiimma_DriversToInit());
                    getAiimma_Drivers(Constant.defaultPageNumber, Constant.defaultDropdownPageSize, '').then((response) => {
                        if (response && response.records) {
                          dispatch(setAiimma_DriversList({ pageNo: Constant.defaultPageNumber, pageSize: Constant.defaultDropdownPageSize, list: response.records, totalCount: response.total_count, searchKey: '' }));
                        } else {
                          //dispatch(setAiima_Transporter_Driver_Bthb_BookingMessage("No Record Found For Aiimma_Drivers"));
                        }
                      })
                }
        if (aiimma_bt_hb_customersData && aiimma_bt_hb_customersData.list && aiimma_bt_hb_customersData.list.length === 0) {
                    dispatch(resetAiimma_Bt_Hb_CustomersToInit());
                    getAiimma_Bt_Hb_Customers(Constant.defaultPageNumber, Constant.defaultDropdownPageSize, '').then((response) => {
                        if (response && response.records) {
                          dispatch(setAiimma_Bt_Hb_CustomersList({ pageNo: Constant.defaultPageNumber, pageSize: Constant.defaultDropdownPageSize, list: response.records, totalCount: response.total_count, searchKey: '' }));
                        } else {
                          //dispatch(setAiima_Transporter_Driver_Bthb_BookingMessage("No Record Found For Aiimma_Bt_Hb_Customers"));
                        }
                      })
                }
        if (aiimma_trucksData && aiimma_trucksData.list && aiimma_trucksData.list.length === 0) {
                    dispatch(resetAiimma_TrucksToInit());
                    getAiimma_Trucks(Constant.defaultPageNumber, Constant.defaultDropdownPageSize, '').then((response) => {
                        if (response && response.records) {
                          dispatch(setAiimma_TrucksList({ pageNo: Constant.defaultPageNumber, pageSize: Constant.defaultDropdownPageSize, list: response.records, totalCount: response.total_count, searchKey: '' }));
                        } else {
                          //dispatch(setAiima_Transporter_Driver_Bthb_BookingMessage("No Record Found For Aiimma_Trucks"));
                        }
                      })
                }
        })

  return <>
  <Card className="shadow mb-4">
          <Card.Header className="py-3">
              <h3 className="m-0 font-weight-bold text-primary text-capitalize">Define Driver, Truck and Holding Bay / Terminal 
              </h3>

          </Card.Header>
          <Card.Body>
          <Form.Group>
<label className="form -control-label">Select Driver</label>
<Form.Control 
as="select" 
name="Driver_id" 
className="form-control" 
>    
<option value={0}>Select Driver </option> 
{
aiimma_driversData.list.map((item, i) => {
return <option value={item.ID} key={`aiimma_drivers-${i}`}>{item.FullName}</option>
})}
</Form.Control>
</Form.Group>

<Form.Group>
<label className="form -control-label">Select Truck</label>
<Form.Control 
as="select" 
name="Truck_id" 
className="form-control" 
>    
<option value={0}>Select Trucks </option> 
{
aiimma_trucksData.list.map((item, i) => {
return <option value={item.ID} key={`aiimma_trucks-${i}`}>{item.TruckName}</option>
})}
</Form.Control>
</Form.Group>

<Form.Group>
<label className="form -control-label">Select Holding Bay / Terminal</label>
<Form.Control as="select"  
name="bt_hb_customer_id" 
className="form-control" 
value=""
>
<option value={0}>Select Holding Bay / Bonded Terminal </option> 
{
aiimma_bt_hb_customersData.list.map((item, i) => {
return <option value={item.ID} key={`aiimma_bt_hb_customers-${i}`}>{item.CompanyName}</option>
})

}
</Form.Control>

</Form.Group>
          </Card.Body>
      </Card>
</>
}

export default DriverBayTruckDetails;
