import { APIService } from "services";

export const getAiimma_Drivers = async (pageNo: any,pageSize: any,search: string | any[]) => {
    let res;
    if(search.length===0) {
        res = await getAllAiimma_Drivers(pageNo,pageSize);
    }
    else{
        try {
            res = await searchAiimma_Drivers(search,pageNo,pageSize);
        } catch(err) {
             return { records:[], totalCount:0 }
        }
    }
    if(res && res.data){
    return res.data;
    }else{
    return { records:[], totalCount:0 }
    }
    
}


export const addAiimma_Drivers = (data: any) => {
return APIService.api().post(`/aiimma_drivers`,data)
}
export const updateAiimma_Drivers = (ID: any,data: any) => {
return APIService.api().patch(`/aiimma_drivers/${ID}`,data)
}
export const getAllAiimma_Drivers = (pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_drivers/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const getOneAiimma_Drivers = (ID: any) => {
return APIService.api().get(`/aiimma_drivers/${ID}`)
}
export const searchAiimma_Drivers = (searchKey: any,pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiimma_drivers/search/${searchKey}/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const deleteAiimma_Drivers = (ID: any) => {
return APIService.api().delete(`/aiimma_drivers/${ID}`)
}
