import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface IAiimma_ContainerDeliveryCallCard {
ID:number,
login_id:number,
Login_Role:string,
Pwd:string,
EmailID:string,
emailValidated:string,
emailActivationCode?:string,
PhoneNo:string,
phoneValidated:string,
phoneOneTimeValidationCode?:string,
phoneOneTimeValidationCodeSentAt?:Date,
phoneOneTimeValidationCodeValidPeriod?:number,
phoneLoginValidationCode?:string,
loginValidationCodeSentAt?:Date,
loginValidationCodeValidPeriod?:number,
RoleCode:string,
LoginStatus:string,
LoggedIP:string,
RecorCreatedAt:Date
}

interface IAiimma_ContainerDeliveryCallCardData {
    list?: Array<IAiimma_ContainerDeliveryCallCard>;
    pageNo: number;
    pageSize: number;
    searchKey?: string;
    totalCount?: number;
    message?: string;
}

const initialState: IAiimma_ContainerDeliveryCallCardData = {
    pageNo: 1,
    pageSize: 20,
    searchKey: '',
    list: [],
    totalCount: 0,
    message: '',
};

const Aiimma_ContainerDeliveryCallCardSlice = createSlice({
    name: "Aiimma_Notifyusers",
    initialState,
    reducers: {
        setAiimma_ContainerDeliveryCallCardList: (state, _action: PayloadAction<IAiimma_ContainerDeliveryCallCardData>) => {
            state.list = _action.payload.list;
            state.pageNo = _action.payload.pageNo;
            state.pageSize = _action.payload.pageSize;
            state.totalCount = _action.payload.totalCount;
        },
        resetAiimma_ContainerDeliveryCallCardToInit: (state) => {
            state = initialState;
        },
        setAiimma_ContainerDeliveryCallCardMessage: (state, _action: PayloadAction<string>) => {
            state.message = _action.payload;
        },
    },
});

export const { setAiimma_ContainerDeliveryCallCardList, resetAiimma_ContainerDeliveryCallCardToInit, setAiimma_ContainerDeliveryCallCardMessage } = Aiimma_ContainerDeliveryCallCardSlice.actions;

export default Aiimma_ContainerDeliveryCallCardSlice.reducer;

