import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface IAiimma_Tdos {
ID:number,
TDOGeneralCode?:string,
TDOIndividualCode?:string,
ShippingLine:string,
ContainerNumber:string,
Tonage:string,
TonageDetails?:string,
Destination:string,
DestinationDetails?:string,
ServiceStatus?:string,
LoggedIP:string,
UploadedBy?:string,
User_Uploaded_ID?:number,
RecordCreatedAt:Date
}

interface IAiimma_TdosData {
    list?: Array<IAiimma_Tdos>;
    pageNo: number;
    pageSize: number;
    searchKey?: string;
    totalCount?: number;
    message?: string;
}

const initialState: IAiimma_TdosData = {
    pageNo: 1,
    pageSize: 20,
    searchKey: '',
    list: [],
    totalCount: 0,
    message: '',
};

const aiimma_tdosSlice = createSlice({
    name: "aiimma_tdos",
    initialState,
    reducers: {
        setAiimma_TdosList: (state, _action: PayloadAction<IAiimma_TdosData>) => {
            state.list = _action.payload.list;
            state.pageNo = _action.payload.pageNo;
            state.pageSize = _action.payload.pageSize;
            state.totalCount = _action.payload.totalCount;
        },
        resetAiimma_TdosToInit: (state) => {
            state = initialState;
        },
        setAiimma_TdosMessage: (state, _action: PayloadAction<string>) => {
            state.message = _action.payload;
        },
    },
});

export const { setAiimma_TdosList, resetAiimma_TdosToInit, setAiimma_TdosMessage } = aiimma_tdosSlice.actions;

export default aiimma_tdosSlice.reducer;

