import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface IAiimma_Holdingbay_Containers_Upload {
ID:number,
bt_hb_customer_id?:number,
UploadCode?:string,
ShippingLine:string,
ContainerMTU_Number:string,
ContainerSize:string,
ContainerSpec?:string,
LoggedIP:string,
holdingbay_id?:number,
RecordCreatedAt:Date
}

interface IAiimma_Holdingbay_Containers_UploadData {
    list?: Array<IAiimma_Holdingbay_Containers_Upload>;
    pageNo: number;
    pageSize: number;
    searchKey?: string;
    totalCount?: number;
    message?: string;
}

const initialState: IAiimma_Holdingbay_Containers_UploadData = {
    pageNo: 1,
    pageSize: 20,
    searchKey: '',
    list: [],
    totalCount: 0,
    message: '',
};

const aiimma_holdingbay_containers_uploadSlice = createSlice({
    name: "aiimma_holdingbay_containers_upload",
    initialState,
    reducers: {
        setAiimma_Holdingbay_Containers_UploadList: (state, _action: PayloadAction<IAiimma_Holdingbay_Containers_UploadData>) => {
            state.list = _action.payload.list;
            state.pageNo = _action.payload.pageNo;
            state.pageSize = _action.payload.pageSize;
            state.totalCount = _action.payload.totalCount;
        },
        resetAiimma_Holdingbay_Containers_UploadToInit: (state) => {
            state = initialState;
        },
        setAiimma_Holdingbay_Containers_UploadMessage: (state, _action: PayloadAction<string>) => {
            state.message = _action.payload;
        },
    },
});

export const { setAiimma_Holdingbay_Containers_UploadList, resetAiimma_Holdingbay_Containers_UploadToInit, setAiimma_Holdingbay_Containers_UploadMessage } = aiimma_holdingbay_containers_uploadSlice.actions;

export default aiimma_holdingbay_containers_uploadSlice.reducer;

