import { useFormik } from "formik";
import React from "react";

import { Button, Card, Form } from "react-bootstrap";
import { setAiimma_DriversMessage } from "redux/actions";


import { useAppDispatch } from "redux/store";
import { addAiimma_Drivers, updateAiimma_Drivers } from "services/aiimma_driversService";
import { Constant } from "template/Constant";
import * as yup from 'yup';
type Props = {
    row?: any,
    hideShowForm: (actionName: any) => void;
    getData: (page: any, pageSize: any, searchKey: any) => void;
    action?: string
};
export const Aiimma_DriversForm: React.FC<Props> = ({ row, hideShowForm, getData, action }) => {
    const dispatch = useAppDispatch();
    const iValue={ID:'',UserID:'',FullName:'',Gender:'',DateOfBirth:'',Phone:'',WhatsApp:'',Avatar:'',DLNumber:'',DLAvatar:'',HouseAddress:'',StateOfOrigin:'',Nationality:'',Email:'',LoggedIP:'',RecordCreatedAt:'',RecordUpdatedAt:''};
    const initialValue = action === 'edit' ? row : iValue;
    
    
    const formik = useFormik({
        initialValues: initialValue,
        onSubmit: async (values) => {
            if (action === 'edit') {
                const response = await updateAiimma_Drivers(values.ID,values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_DriversMessage("Updated Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_DriversMessage("Some error occured!"));
                }
            } else if (action === 'add') {
                const response = await addAiimma_Drivers(values);
               if (response && (response.status === 200 || response.status === 201)) {
                    dispatch(setAiimma_DriversMessage("Added Successfully"));
                    getData(Constant.defaultPageNumber, Constant.defaultPageSize, '');
                    hideShowForm('');
                } else {
                    dispatch(setAiimma_DriversMessage("Some error occured!"));
                }
            }
        },
        validationSchema: yup.object({
           UserID: yup.string(),
FullName: yup.string().required('FullName is required'),
Gender: yup.string().required('Gender is required'),
DateOfBirth: yup.string(),
Phone: yup.string().required('Phone Number is required'),
WhatsApp: yup.string(),
Avatar: yup.string(),
DLNumber: yup.string(),
DLAvatar: yup.string(),
HouseAddress: yup.string().required('House Address is required'),
StateOfOrigin: yup.string(),
Nationality: yup.string(),
Email: yup.string().required('Email is required'),
LoggedIP: yup.string(),
RecordCreatedAt: yup.date(),
RecordUpdatedAt: yup.date(),

        }),
    });
    return (
        <Card className="shadow mb-4">
            <Card.Header className="py-3">
                <h6 className="m-0 font-weight-bold text-primary text-capitalize">{action} Drivers
                    <Button className="btn-icon-split float-right" onClick={() => hideShowForm(false)}>
                        <span className="icon text-white-50">
                            <i className="fas fa-list"></i>
                        </span>
                        <span className="text">View Drivers</span>
                    </Button>
                </h6>

            </Card.Header>
            <Card.Body>
                <Form onSubmit={formik.handleSubmit}>
                  <Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="UserID" className="form-control" value={formik.values.UserID}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.UserID && !!formik.errors.UserID}
isValid ={!!formik.touched.UserID && !formik.errors.UserID}
></Form.Control>
{
    formik.errors.UserID && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.UserID}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Full Name</label>
<Form.Control type="text" name="FullName" className="form-control" value={formik.values.FullName}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.FullName && !!formik.errors.FullName}
isValid ={!!formik.touched.FullName && !formik.errors.FullName}
></Form.Control>
{
    formik.errors.FullName && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.FullName}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Gender</label>
<Form.Control type="select" name="Gender" className="form-control" value={formik.values.Gender}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Gender && !!formik.errors.Gender}
isValid ={!!formik.touched.Gender && !formik.errors.Gender}
>
<option value="Female">Female</option>
<option value="Male">Male</option>
</Form.Control>
{
    formik.errors.Gender && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Gender}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Date Of Birth</label>
<Form.Control type="date" name="DateOfBirth" className="form-control" value={formik.values.DateOfBirth}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.DateOfBirth && !!formik.errors.DateOfBirth}
isValid ={!!formik.touched.DateOfBirth && !formik.errors.DateOfBirth}
></Form.Control>
{
    formik.errors.DateOfBirth && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.DateOfBirth}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Phone</label>
<Form.Control type="text" name="Phone" className="form-control" value={formik.values.Phone}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Phone && !!formik.errors.Phone}
isValid ={!!formik.touched.Phone && !formik.errors.Phone}
></Form.Control>
{
    formik.errors.Phone && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Phone}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">WhatsApp Phone Number</label>
<Form.Control type="text" name="WhatsApp" className="form-control" value={formik.values.WhatsApp}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.WhatsApp && !!formik.errors.WhatsApp}
isValid ={!!formik.touched.WhatsApp && !formik.errors.WhatsApp}
></Form.Control>
{
    formik.errors.WhatsApp && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.WhatsApp}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Driver Avatar(Picture - Optional)</label>
<Form.Control type="text" name="Avatar" className="form-control" value={formik.values.Avatar}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Avatar && !!formik.errors.Avatar}
isValid ={!!formik.touched.Avatar && !formik.errors.Avatar}
></Form.Control>
{
    formik.errors.Avatar && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Avatar}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Driver License Number</label>
<Form.Control type="text" name="DLNumber" className="form-control" value={formik.values.DLNumber}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.DLNumber && !!formik.errors.DLNumber}
isValid ={!!formik.touched.DLNumber && !formik.errors.DLNumber}
></Form.Control>
{
    formik.errors.DLNumber && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.DLNumber}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Driver License Avatar(Picture - Optional)</label>
<Form.Control type="text" name="DLAvatar" className="form-control" value={formik.values.DLAvatar}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.DLAvatar && !!formik.errors.DLAvatar}
isValid ={!!formik.touched.DLAvatar && !formik.errors.DLAvatar}
></Form.Control>
{
    formik.errors.DLAvatar && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.DLAvatar}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">House Address</label>
<Form.Control type="text" name="HouseAddress" className="form-control" value={formik.values.HouseAddress}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.HouseAddress && !!formik.errors.HouseAddress}
isValid ={!!formik.touched.HouseAddress && !formik.errors.HouseAddress}
></Form.Control>
{
    formik.errors.HouseAddress && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.HouseAddress}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">State Of Origin</label>
<Form.Control type="text" name="StateOfOrigin" className="form-control" value={formik.values.StateOfOrigin}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.StateOfOrigin && !!formik.errors.StateOfOrigin}
isValid ={!!formik.touched.StateOfOrigin && !formik.errors.StateOfOrigin}
></Form.Control>
{
    formik.errors.StateOfOrigin && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.StateOfOrigin}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Nationality</label>
<Form.Control type="text" name="Nationality" className="form-control" value={formik.values.Nationality}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Nationality && !!formik.errors.Nationality}
isValid ={!!formik.touched.Nationality && !formik.errors.Nationality}
></Form.Control>
{
    formik.errors.Nationality && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Nationality}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label">Email</label>
<Form.Control type="text" name="Email" className="form-control" value={formik.values.Email}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.Email && !!formik.errors.Email}
isValid ={!!formik.touched.Email && !formik.errors.Email}
></Form.Control>
{
    formik.errors.Email && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.Email}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="LoggedIP" className="form-control" value={formik.values.LoggedIP}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.LoggedIP && !!formik.errors.LoggedIP}
isValid ={!!formik.touched.LoggedIP && !formik.errors.LoggedIP}
></Form.Control>
{
    formik.errors.LoggedIP && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.LoggedIP}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="RecordCreatedAt" className="form-control" value={formik.values.RecordCreatedAt}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.RecordCreatedAt && !!formik.errors.RecordCreatedAt}
isValid ={!!formik.touched.RecordCreatedAt && !formik.errors.RecordCreatedAt}
></Form.Control>
{
    formik.errors.RecordCreatedAt && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.RecordCreatedAt}
    </Form.Control.Feedback>
)}
</Form.Group>
<Form.Group>
<label className="form -control-label"></label>
<Form.Control type="hidden" name="RecordUpdatedAt" className="form-control" value={formik.values.RecordUpdatedAt}
onChange={formik.handleChange}
onBlur ={formik.handleBlur}
isInvalid ={!!formik.touched.RecordUpdatedAt && !!formik.errors.RecordUpdatedAt}
isValid ={!!formik.touched.RecordUpdatedAt && !formik.errors.RecordUpdatedAt}
></Form.Control>
{
    formik.errors.RecordUpdatedAt && (
    <Form.Control.Feedback type="invalid">
        {formik.errors.RecordUpdatedAt}
    </Form.Control.Feedback>
)}
</Form.Group>

                    <Form.Group>
                        <Button type="submit" className="float-right" variant="primary">Save</Button>
                    </Form.Group>
                </Form>
            </Card.Body>
        </Card>
    );
}

