import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface IAiimma_Drivers {
ID:number,
UserID:string,
FullName:string,
Gender?:string,
DateOfBirth?:string,
Phone?:string,
WhatsApp?:string,
Avatar?:string,
DLNumber?:string,
DLAvatar?:string,
HouseAddress?:string,
StateOfOrigin?:string,
Nationality?:string,
Email?:string,
LoggedIP:string,
RecordCreatedAt:Date,
RecordUpdatedAt?:Date
}

interface IAiimma_DriversData {
    list?: Array<IAiimma_Drivers>;
    pageNo: number;
    pageSize: number;
    searchKey?: string;
    totalCount?: number;
    message?: string;
}

const initialState: IAiimma_DriversData = {
    pageNo: 1,
    pageSize: 20,
    searchKey: '',
    list: [],
    totalCount: 0,
    message: '',
};

const aiimma_driversSlice = createSlice({
    name: "aiimma_drivers",
    initialState,
    reducers: {
        setAiimma_DriversList: (state, _action: PayloadAction<IAiimma_DriversData>) => {
            state.list = _action.payload.list;
            state.pageNo = _action.payload.pageNo;
            state.pageSize = _action.payload.pageSize;
            state.totalCount = _action.payload.totalCount;
        },
        resetAiimma_DriversToInit: (state) => {
            state = initialState;
        },
        setAiimma_DriversMessage: (state, _action: PayloadAction<string>) => {
            state.message = _action.payload;
        },
    },
});

export const { setAiimma_DriversList, resetAiimma_DriversToInit, setAiimma_DriversMessage } = aiimma_driversSlice.actions;

export default aiimma_driversSlice.reducer;

