import { APIService } from "services";

export const getAiima_Transporter_Driver_Bthb_Booking = async (pageNo: number,pageSize: number,search: string | any[]) => {
    let res;
    if(search.length===0) {
        res = await getAllAiima_Transporter_Driver_Bthb_Booking(pageNo,pageSize);
    }
    else{
        try {
            res = await searchAiima_Transporter_Driver_Bthb_Booking(search,pageNo,pageSize);
        } catch(err) {
             return { records:[], totalCount:0 }
        }
    }
    if(res && res.data){
    return res.data;
    }else{
    return { records:[], totalCount:0 }
    }
    
}


export const addAiima_Transporter_Driver_Bthb_Booking = (data: any) => {
return APIService.api().post(`/aiima_transporter_driver_bthb_booking`,data)
}
export const updateAiima_Transporter_Driver_Bthb_Booking = (ID: any,data: any) => {
return APIService.api().patch(`/aiima_transporter_driver_bthb_booking/${ID}`,data)
}
export const getAllAiima_Transporter_Driver_Bthb_Booking = (pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiima_transporter_driver_bthb_booking/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const getOneAiima_Transporter_Driver_Bthb_Booking = (ID: any) => {
return APIService.api().get(`/aiima_transporter_driver_bthb_booking/${ID}`)
}
export const searchAiima_Transporter_Driver_Bthb_Booking = (searchKey: any,pageNo: any,pageSize: any) => {
return APIService.api().get(`/aiima_transporter_driver_bthb_booking/search/${searchKey}/?pageNo=${pageNo}&pageSize=${pageSize}`)
}
export const deleteAiima_Transporter_Driver_Bthb_Booking = (ID: any) => {
return APIService.api().delete(`/aiima_transporter_driver_bthb_booking/${ID}`)
}
